




















































import Vue from 'vue';
import {
  computed, defineComponent, ref, Ref,
} from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import useI18N from '@/app/shared/client/logic/i18n';
import { LocationData } from '@/app/shared/client/type/location';
import {
  Accordion,
  AccordionItem,
} from '@/app/editor/client/view/component';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';

function useLocationContentUpdate(
  store: Store<State>,
  locationIdRef: Ref<Readonly<string>>,
): (locationContent: LocationData) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);

  function updateLocationContent(locationContent: LocationData): void {
    const locationId = locationIdRef.value;
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: locationId, value: locationContent },
    );
    debouncedDispatch();
  }

  return updateLocationContent;
}

function useLocationSubheadingChangeHandler(
  updateLocationContent: (locationContent: LocationData) => void,
  locationContent: Readonly<Ref<LocationData | undefined>>,
): (value: string | undefined) => void {
  function changeLocationSubheading(value: string | undefined): void {
    const newLocationContent = {
      ...locationContent.value,
      subheading: value,
    };

    updateLocationContent(newLocationContent);
  }

  return changeLocationSubheading;
}

function useLocationAddressChangeHandler(
  updateLocationContent: (locationContent: LocationData) => void,
  locationContent: Readonly<Ref<LocationData | undefined>>,
): (value: string | undefined) => void {
  function changeLocationAddress(value: string | undefined): void {
    const newLocationContent = {
      ...locationContent.value,
      address: value,
    };

    updateLocationContent(newLocationContent);
  }

  return changeLocationAddress;
}

function useLocationPostalAddressChangeHandler(
  updateLocationContent: (locationContent: LocationData) => void,
  locationContent: Readonly<Ref<LocationData | undefined>>,
): (value: string | undefined) => void {
  function changeLocationPostalAddress(value: string | undefined): void {
    const newLocationContent = {
      ...locationContent.value,
      postalAddress: value,
    };

    updateLocationContent(newLocationContent);
  }

  return changeLocationPostalAddress;
}

function useMapSettingsActivityToggleHandler(
  mapSettingsActivity: Ref<boolean>,
): () => void {
  function toggleMapSettingsActivity(): void {
    mapSettingsActivity.value = !mapSettingsActivity.value;
  }

  return toggleMapSettingsActivity;
}

const TileLocationEditor = defineComponent({
  components: {
    Accordion,
    AccordionItem,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => vm.$store.dispatch<GetTileDataAction>({
      type: ActionType.GET_TILE_DATA,
      payload: { id },
    }));
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const locationId = computed(() => root.$route.params.locationId);
    const locationContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, LocationData | undefined>;
      return tileContentRecord[locationId.value];
    });

    const updateLocationContent = useLocationContentUpdate(root.$store, locationId);

    const updateLocationSubheading = useLocationSubheadingChangeHandler(
      updateLocationContent,
      locationContent,
    );

    const updateLocationAddress = useLocationAddressChangeHandler(
      updateLocationContent,
      locationContent,
    );

    const updateLocationPostalAddress = useLocationPostalAddressChangeHandler(
      updateLocationContent,
      locationContent,
    );

    const initialMapSettingsActivity = isNotNullOrEmpty(locationContent.value?.postalAddress);
    const mapSettingsActivity = ref(initialMapSettingsActivity);
    const changeMapSettingsActivity = useMapSettingsActivityToggleHandler(mapSettingsActivity);

    return {
      i18n,
      tileDataLoaded,
      locationContent,
      updateLocationSubheading,
      updateLocationAddress,
      updateLocationPostalAddress,
      mapSettingsActivity,
      changeMapSettingsActivity,
    };
  },
});

export default TileLocationEditor;
