import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { api } from '@/app/editor/client/api';
import { commitSetSyncTilesStatus, commitTileList } from '@/app/editor/client/store/action/helpers';
import { GetTileListResponse } from '@/app/editor/client/api/get-tile-list';
import { TileData } from '@/app/shared/client/type/tile';

export interface GetTileListAction extends Payload {
  type: ActionType.GET_TILE_LIST;
}

function getDreamTileList(): Promise<TileData[]> {
  return new Promise((resolve, reject) => {
    api.getTileList({ published: false })
      .then((response: GetTileListResponse) => {
        resolve(response.tiles);
      }).catch((error: Error) => reject(error.message));
  });
}

function getRealityTileList(): Promise<TileData[]> {
  return new Promise((resolve, reject) => {
    api.getTileList({ published: true })
      .then((response: GetTileListResponse) => {
        resolve(response.tiles);
      }).catch((error: Error) => reject(error.message));
  });
}

export default function getTileListAction(
  { commit }: ActionContext<State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    commitSetSyncTilesStatus(commit, true);
    Promise.all([getDreamTileList(), getRealityTileList()])
      .then(([tileList, publishedTileList]) => {
        commitSetSyncTilesStatus(commit, false);
        commitTileList(
          tileList,
          publishedTileList,
          commit,
        );
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
