import { State } from '@/app/editor/client/store/state';
import Vue from 'vue';

interface ChangeTileVisibilityMutationPayload {
  readonly tileId: string;
  readonly visibility: boolean;
}

export default function changeTileVisibilityMutation(
  oldState: State,
  payload: ChangeTileVisibilityMutationPayload,
): State {
  const { tileId, visibility } = payload;
  const newState = oldState;
  const { tiles } = newState;

  const tileIndexForUpdate = tiles.findIndex((tile) => tile.id === tileId);
  if (tileIndexForUpdate >= 0) {
    const editedTileData = tiles[tileIndexForUpdate];
    editedTileData.visibility = visibility;
    editedTileData.hasChanges = true;
    Vue.set(tiles, tileIndexForUpdate, editedTileData);
  }
  return newState;
}
