import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { Profile } from '@/app/shared/client/type/profile';

interface SetProfileMutationPayload {
  profile: Profile;
}

export interface SetProfileMutation extends MutationPayload {
  type: MutationType.SET_PROFILE;
  payload: SetProfileMutationPayload;
}

export default function setProfileMutation(oldState: State, data: SetProfileMutation): State {
  const newState = oldState;
  newState.profile = data.payload.profile;
  return newState;
}
