import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface AddTileFromShowcaseRequest {
  readonly tileShowcaseItemId: string;
}

export type AddTileFromShowcaseResponse = TileData;

export function createAddTileFromShowcaseFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: AddTileFromShowcaseRequest) => Promise<AddTileFromShowcaseResponse> {
  return (request): Promise<AddTileFromShowcaseResponse> => fetch(`${apiUrl}/${siteId.value}/tile`, {
    method: 'POST',
    body: JSON.stringify(request),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: AddTileFromShowcaseResponse) => resp);
}
