







import { computed, defineComponent, Ref } from '@vue/composition-api';
import { State } from '@/app/editor/client/store/state';
import { TileData } from '@/app/shared/client/type/tile';
import Editors from './editor';

function isRemovableTile(tile: Ref<TileData | undefined>): boolean {
  return tile.value?.type !== 'HEADER' && tile.value?.type !== 'FOOTER';
}

function getEditorName(tile: Ref<TileData | undefined>): string {
  if (isRemovableTile(tile)) {
    return 'TileRemovalEditor';
  }

  return 'TileVisibilityEditor';
}

const TileManagementContainer = defineComponent({
  components: {
    Editors,
  },
  setup(_, { root }) {
    const editedTile = computed(() => {
      const state = root.$store.state as State;
      return state.tiles.find((tile) => tile.id === state.editedTileData?.id);
    });
    const editorName = getEditorName(editedTile);

    return {
      editedTile,
      editorName,
    };
  },
});

export default TileManagementContainer;
