import { PreviewApiEventType, UpdatePreviewSiteSettings } from '@/app/shared/client/type/preview-api';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';
import { GlobalData } from '@/app/shared/client/type/tile/global';

export default function sendPreviewSiteSettings(
  siteSettings: GlobalData | undefined,
): void {
  if (siteSettings === undefined) {
    return;
  }
  sendToPreviewWindow<UpdatePreviewSiteSettings>({
    type: PreviewApiEventType.UPDATE_PREVIEW_SITE_SETTINGS,
    payload: { siteSettings },
  });
}
