import { MutationPayload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { State } from '@/app/editor/client/store/state';

interface ToggleMobilePreviewMutationPayload {
  isShowMobilePreview: boolean;
}

export interface ToggleMobilePreviewMutation extends MutationPayload {
  type: MutationType.TOGGLE_MOBILE_PREVIEW;
  payload: ToggleMobilePreviewMutationPayload;
}

export default function toggleMobilePreviewMutation(
  oldState: State,
  data: ToggleMobilePreviewMutation,
): State {
  const newState = oldState;
  newState.isShowMobilePreview = data.payload.isShowMobilePreview;
  return newState;
}
