








































































import Vue from 'vue';
import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { ActionLinkData, ActionLinkType } from '@/app/shared/client/type/action-link';
import useI18N from '@/app/shared/client/logic/i18n';
import { FieldName } from '@/app/shared/client/helper/base';
import { ComboBoxEditorControl } from '@/app/editor/client/view/component/control';
import { ComboBoxChoice }
  from '@/app/editor/client/view/component/control/ComboBoxEditorControl.vue';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { TileData } from '@/app/shared/client/type/tile';
import { canScrollToTile } from '@/app/editor/client/logic/tile';
import { useLinkCheckAndUpdate } from '@/app/editor/client/logic/link';

function useUpdateValue(
  store: Store<State>,
  field: FieldName<ActionLinkData>,
  actionLinkId: Readonly<Ref<string>>,
  actionLinkContent: Readonly<Ref<ActionLinkData | undefined>>,
): (v?: string) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);
  function updateValue(value?: string): void {
    if (actionLinkContent.value === undefined) {
      return;
    }
    (actionLinkContent.value as unknown as Record<string, string | undefined>)[field] = value;
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: actionLinkId.value, value: actionLinkContent.value },
    );
    debouncedDispatch();
  }
  return updateValue;
}

const TileActionLinkEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => vm.$store.dispatch<GetTileDataAction>({
      type: ActionType.GET_TILE_DATA,
      payload: { id },
    }));
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const actionLinkId = computed(() => root.$route.params.actionLinkId);
    const actionLinkContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, ActionLinkData | undefined>;
      return tileContentRecord[actionLinkId.value];
    });

    const updateTitle = useUpdateValue(root.$store, 'title', actionLinkId, actionLinkContent);
    const updateActionType = useUpdateValue(root.$store, 'type', actionLinkId, actionLinkContent);
    const updateLink = useUpdateValue(root.$store, 'link', actionLinkId, actionLinkContent);
    const updateEmail = useUpdateValue(root.$store, 'email', actionLinkId, actionLinkContent);
    const updatePhone = useUpdateValue(root.$store, 'phone', actionLinkId, actionLinkContent);
    const updateTileId = useUpdateValue(root.$store, 'tileId', actionLinkId, actionLinkContent);

    const actionTypeChoices: ComboBoxChoice[] = Object.keys(ActionLinkType)
      .map((actionLinkType) => ({
        key: actionLinkType,
        label: i18n(`CommonLabels.ActionLink.ActionType.${actionLinkType}`),
      }));
    const actionType = computed({
      get: () => actionLinkContent.value?.type,
      set: updateActionType,
    });

    const tilesChoices = computed(() => root.$store.getters.blockTileList
      .filter((tile: TileData) => canScrollToTile(tile))
      .map((tile: TileData) => ({
        key: tile.id,
        label: i18n(`TileItem.${tile.sourceId}.name`),
      })));
    const tileId = computed({
      get: () => actionLinkContent.value?.tileId,
      set: updateTileId,
    });

    const link = computed(() => actionLinkContent.value?.link);
    const checkAndUpdateLink = useLinkCheckAndUpdate(link, updateLink);

    return {
      i18n,
      tileDataLoaded,
      actionLinkContent,
      updateTitle,
      updateLink,
      checkAndUpdateLink,
      updateEmail,
      updatePhone,
      tilesChoices,
      tileId,
      actionTypeChoices,
      actionType,
      ActionLinkType,
    };
  },
});

export default TileActionLinkEditor;
