



















import { computed, defineComponent } from '@vue/composition-api';

const CheckboxButtonEditorControl = defineComponent({
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Boolean,
    },
    bold: {
      required: false,
      default: false,
      type: Boolean,
    },
    italic: {
      required: false,
      default: false,
      type: Boolean,
    },
  },
  setup(props, { emit }) {
    const localValue = computed({
      get: () => props.value,
      set: (value) => emit('input', value),
    });

    return {
      localValue,
    };
  },
});

export default CheckboxButtonEditorControl;
