


































import { computed, defineComponent, PropType } from '@vue/composition-api';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import useFocusHandler from '@/app/editor/client/logic/focus';
import { calculateSizeClass, ControlSize } from '@/app/editor/client/logic/control';

const TextContentEditor = defineComponent({
  props: {
    placeholderLabel: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: String,
    },
    maxLength: {
      required: false,
      default: undefined,
      type: String,
    },
    size: {
      required: false,
      default: ControlSize.MEDIUM,
      type: String as PropType<ControlSize>,
    },
  },
  setup(props, { emit }) {
    const localValue = computed({
      get: () => props.value,
      set: (v) => emit('input', v),
    });
    const { isFocused, onFocus, onBlur } = useFocusHandler(() => emit('blur'));

    const sizeClass = computed(() => calculateSizeClass(props.size));

    return {
      localValue,
      isEmpty: computed(() => isNullOrEmpty(props.value)),
      isFocused,
      onFocus,
      onBlur,
      sizeClass,
    };
  },
});

export default TextContentEditor;
