





































import { defineComponent } from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import Icon from '@/app/editor/client/view/icon';

const StoreDesignEditor = defineComponent({
  components: {
    ExternalIcon: Icon.ExternalIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const navigateToCpDesign = (): void => {
      window.open(`${root.$store.state.context.cpUrl}#design`, '_blank');
    };

    return {
      i18n,
      navigateToCpDesign,
    };
  },
});

export default StoreDesignEditor;
