















































































import Vue from 'vue';
import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import useI18N from '@/app/shared/client/logic/i18n';
import Icon from '@/app/editor/client/view/icon';
import {
  ComboBoxEditorControl,
  TextEditorControl,
} from '@/app/editor/client/view/component/control';
import { SocialIconListData, SocialIconType } from '@/app/shared/client/type/social-icon-list';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import {
  socialIconComponentName, socialIconLabel,
  socialIconPlaceholderLabel,
} from '@/app/shared/client/helper/social-icon-list';
import { LayoutConfig } from '@/app/editor/client/type/tile-config';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { GetTileConfigAction } from '@/app/editor/client/store/action/get-tile-config';

function useSocialIconListContentUpdate(
  store: Store<State>,
  socialIconListIdRef: Ref<Readonly<string>>,
): (socialIconListContent: SocialIconListData | undefined) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);

  function updateSocialIconListContent(
    socialIconListContent: SocialIconListData | undefined,
  ): void {
    const socialIconListId = socialIconListIdRef.value;
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: socialIconListId, value: socialIconListContent },
    );
    debouncedDispatch();
  }

  return updateSocialIconListContent;
}

function useSocialIconTypeChangeHandler(
  updateSocialIconListContent: (socialIconListContent: SocialIconListData | undefined) => void,
  socialIconListContent: Readonly<Ref<SocialIconListData | undefined>>,
  index: number,
): (value: SocialIconType) => void {
  function changeSocialIconType(value: SocialIconType): void {
    const currentSocialIconContent = socialIconListContent.value?.list?.[index];
    const newSocialIconContent = {
      ...currentSocialIconContent,
      type: value,
    };
    socialIconListContent.value?.list?.splice(index, 1, newSocialIconContent);
    updateSocialIconListContent(socialIconListContent.value);
  }

  return changeSocialIconType;
}

function useSocialIconLinkChangeHandler(
  updateSocialIconListContent: (socialIconListContent: SocialIconListData | undefined) => void,
  socialIconListContent: Readonly<Ref<SocialIconListData | undefined>>,
  index: number,
): (value: string | undefined) => void {
  function changeSocialIconLink(value: string | undefined): void {
    const currentSocialIconContent = socialIconListContent.value?.list?.[index];
    const newSocialIconContent = {
      ...currentSocialIconContent,
      link: value,
    };
    socialIconListContent.value?.list?.splice(index, 1, newSocialIconContent);
    updateSocialIconListContent(socialIconListContent.value);
  }

  return changeSocialIconLink;
}

function useSocialIconAddHandler(
  updateSocialIconListContent: (socialIconListContent: SocialIconListData | undefined) => void,
  socialIconListContent: Readonly<Ref<SocialIconListData | undefined>>,
): () => void {
  function addSocialIcon(): void {
    socialIconListContent.value?.list?.push({ type: SocialIconType.FACEBOOK, link: undefined });
    updateSocialIconListContent(socialIconListContent.value);
  }

  return addSocialIcon;
}

function useSocialIconRemoveHandler(
  updateSocialIconListContent: (socialIconListContent: SocialIconListData | undefined) => void,
  socialIconListContent: Readonly<Ref<SocialIconListData | undefined>>,
  index: number,
): () => void {
  function removeSocialIcon(): void {
    socialIconListContent.value?.list?.splice(index, 1);
    updateSocialIconListContent(socialIconListContent.value);
  }

  return removeSocialIcon;
}

const TileSocialIconListEditor = defineComponent({
  components: {
    TextEditorControl,
    ComboBoxEditorControl,
    CrossIcon: Icon.CrossIcon,
    PlusIcon: Icon.PlusIcon,
    FacebookIcon: Icon.FacebookIcon,
    InstagramIcon: Icon.InstagramIcon,
    TwitterIcon: Icon.TwitterIcon,
    WhatsappIcon: Icon.WhatsappIcon,
    YoutubeIcon: Icon.YoutubeIcon,
    PinterestIcon: Icon.PinterestIcon,
    VkontakteIcon: Icon.VkontakteIcon,
    MessengerIcon: Icon.MessengerIcon,
    TiktokIcon: Icon.TiktokIcon,
    YelpIcon: Icon.YelpIcon,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    next((vm) => {
      const { id } = to.params;
      return vm.$store.dispatch<GetTileDataAction>({
        type: ActionType.GET_TILE_DATA,
        payload: { id },
      })
        .then(() => vm.$store.dispatch<GetTileConfigAction>({
          type: ActionType.GET_TILE_CONFIG,
        }));
    });
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        }).then(() => (this as Vue).$store.dispatch<GetTileConfigAction>({
          type: ActionType.GET_TILE_CONFIG,
        }));
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData);
    const editedTileConfig = computed(() => state.value.editedTileConfig);
    const tileDataLoaded = computed(() => {
      const hasTileData = editedTile.value !== undefined;
      const hasTileConfig = editedTileConfig.value !== undefined;
      return hasTileData && hasTileConfig;
    });

    const socialIconListId = computed(() => root.$route.params.socialIconListId);
    const socialIconListContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, SocialIconListData | undefined>;
      return tileContentRecord[socialIconListId.value];
    });

    const socialIconListMaxItems = computed(() => {
      const layoutConfigList = editedTileConfig.value?.layoutConfigList as LayoutConfig[];
      const currentLayoutConfig = layoutConfigList
        .find((config: LayoutConfig) => {
          const currentLayout = editedTile.value?.design?.layout;
          return config.name === currentLayout;
        });

      const socialIconListEditorConfig = currentLayoutConfig?.contentEditorConfig?.mainEditors
        ?.find((contentEditor) => contentEditor.field === socialIconListId.value);

      return socialIconListEditorConfig?.maxListItems;
    });

    const updateSocialIconListContent = useSocialIconListContentUpdate(
      root.$store,
      socialIconListId,
    );

    const socialIconList = computed(() => {
      const iconList = socialIconListContent.value?.list;
      return iconList
        ?.map((socialIcon, index) => ({
          placeholderLabel: socialIconPlaceholderLabel(socialIcon.type),
          icon: socialIconComponentName(socialIcon.type),
          type: socialIcon.type,
          link: socialIcon.link,
          changeType: useSocialIconTypeChangeHandler(
            updateSocialIconListContent,
            socialIconListContent,
            index,
          ),
          changeLink: useSocialIconLinkChangeHandler(
            updateSocialIconListContent,
            socialIconListContent,
            index,
          ),
          remove: useSocialIconRemoveHandler(
            updateSocialIconListContent,
            socialIconListContent,
            index,
          ),
        }));
    });

    const socialIconListMaxItemsReached = computed(
      () => socialIconList.value?.length === socialIconListMaxItems.value,
    );

    const socialIconTypeList = Object.keys(SocialIconType);
    const socialIconChoices = socialIconTypeList.map((socialIconType) => ({
      key: socialIconType,
      label: i18n(socialIconLabel(socialIconType as SocialIconType)),
    }));

    const addSocialIcon = useSocialIconAddHandler(
      updateSocialIconListContent,
      socialIconListContent,
    );

    return {
      i18n,
      tileDataLoaded,
      socialIconList,
      socialIconListMaxItemsReached,
      socialIconChoices,
      addSocialIcon,
    };
  },
});

export default TileSocialIconListEditor;
