import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { commitSetSyncTilesStatus } from '@/app/editor/client/store/action/helpers';
import { TileData } from '@/app/shared/client/type/tile';

interface SaveTileListActionPayload {
  tileList: TileData[];
}

export interface SaveTileListAction extends ActionPayload {
  type: ActionType.SAVE_TILE_LIST;
  payload: SaveTileListActionPayload;
}

export default function saveTileListAction(
  { commit }: ActionContext <State, State>,
  data: SaveTileListAction,
): void {
  const { tileList } = data.payload;
  commitSetSyncTilesStatus(commit, true);
  api.saveTileList({ tiles: tileList })
    .then(() => {
      commitSetSyncTilesStatus(commit, false);
    });
}
