




















import { computed, defineComponent, PropType } from '@vue/composition-api';

enum CheckboxToggleSize {
  MEDIUM = 'MEDIUM',
  LARGE = 'LARGE'
}

const CheckboxToggleEditorControl = defineComponent({
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Boolean,
    },
    size: {
      required: false,
      default: CheckboxToggleSize.MEDIUM,
      type: String as PropType<CheckboxToggleSize>,
    },
  },
  setup(props, { emit }) {
    const localValue = computed({
      get: () => props.value,
      set: (value) => emit('input', value),
    });

    const sizeClass = computed(() => `ine-control--${props.size.toLowerCase()}`);

    return {
      localValue,
      sizeClass,
    };
  },
});

export default CheckboxToggleEditorControl;
