import { State } from '@/app/editor/client/store/state';
import { ActionContext, ActionPayload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import sendPreviewUploadingImageList
  from '@/app/editor/client/logic/preview/send-preview-uploading-image-list';
import { ActionType } from '@/app/editor/client/store/action/type';

interface AddImageInUploadingStateActionPayload {
  readonly tileId: string;
  readonly imageId: string;
  readonly rawImage: string;
}

export interface AddImageInUploadingStateAction extends ActionPayload {
  type: ActionType.ADD_IMAGE_IN_UPLOADING_STATE;
  payload: AddImageInUploadingStateActionPayload;
}

export default function addImageInUploadingStateAction(
  actionContext: ActionContext <State, State>,
  data: AddImageInUploadingStateAction,
): void {
  const { tileId, imageId, rawImage } = data.payload;

  actionContext.commit(MutationType.ADD_IMAGE_IN_UPLOADING_STATE, {
    tileId,
    imageId,
    rawImage,
  });

  sendPreviewUploadingImageList(actionContext.state.imageListInUploadingState);
}
