import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';

interface RemoveTileMutationPayload {
  tileId?: string;
}

export interface RemoveTileMutation extends MutationPayload {
  type: MutationType.REMOVE_TILE;
  payload: RemoveTileMutationPayload;
}

export default function removeTileMutation(oldState: State, data: RemoveTileMutation): State {
  const newState = oldState;
  const { tileId } = data.payload;
  if (tileId != null) {
    const tileIdForRemove = oldState.tiles.findIndex((tile) => tile.id === tileId);
    if (tileIdForRemove !== -1) {
      newState.tiles.splice(tileIdForRemove, 1);
    }
  }
  return newState;
}
