import { PreviewApiEventType, UpdatePreviewProfile } from '@/app/shared/client/type/preview-api';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';
import { Profile } from '@/app/shared/client/type/profile';

export default function sendPreviewProfile(
  profile: Profile | undefined,
): void {
  if (profile === undefined) {
    return;
  }

  sendToPreviewWindow<UpdatePreviewProfile>({
    type: PreviewApiEventType.UPDATE_PREVIEW_PROFILE,
    payload: { profile },
  });
}
