import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { GlobalData } from '@/app/shared/client/type/tile/global';

interface SetSiteSettingsMutationPayload {
  siteSettings?: GlobalData;
  publishedSiteSettings?: GlobalData;
}

export interface SetSiteSettingsMutation extends MutationPayload {
  type: MutationType.SET_SITE_SETTINGS;
  payload: SetSiteSettingsMutationPayload;
}

export default function setSiteSettingsMutation(
  oldState: State,
  data: SetSiteSettingsMutation,
): State {
  const newState = oldState;
  newState.siteSettings = data.payload.siteSettings;
  newState.publishedSiteSettings = data.payload.publishedSiteSettings;
  return newState;
}
