import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';

interface SetTranslationsMutationPayload {
  editorTranslations: Record<string, string>;
  websiteTranslations: Record<string, string>;
}

export interface SetTranslationsMutation extends MutationPayload {
  type: MutationType.SET_TRANSLATIONS;
  payload: SetTranslationsMutationPayload;
}

export default function setTranslationsMutation(
  oldState: State,
  data: SetTranslationsMutation,
): State {
  const newState = oldState;
  newState.translations = data.payload.editorTranslations;
  newState.websiteTranslations = data.payload.websiteTranslations;
  return newState;
}
