


































































import Vue from 'vue';
import {
  computed, defineComponent, Ref, watch,
} from '@vue/composition-api';
import { Route, VueRouter } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { GetTileConfigAction } from '@/app/editor/client/store/action/get-tile-config';
import RouteName from '@/app/editor/client/router/RouteName';
import useI18N from '@/app/shared/client/logic/i18n';
import { TileData } from '@/app/shared/client/type/tile';
import ContentEditors from './body/TileContentEditor/editor';
import DesignEditors from './body/TileDesignEditor/editor';
import Buttons from './body/Button';

enum Section {
  CONTENT,
  DESIGN
}

interface SectionManager {
  isContentSection: Readonly<Ref<Readonly<boolean>>>;
  isDesignSection: Readonly<Ref<Readonly<boolean>>>;
}

function useSectionManager(route: Ref<Route>): SectionManager {
  function getSection(): Section {
    switch (route.value.name) {
      case RouteName.TILE_CONTENT:
        return Section.CONTENT;
      case RouteName.TILE_DESIGN:
        return Section.DESIGN;
      default:
        return Section.CONTENT;
    }
  }

  function isContentSection(): boolean {
    return getSection() === Section.CONTENT;
  }

  function isDesignSection(): boolean {
    return getSection() === Section.DESIGN;
  }

  return {
    isContentSection: computed(isContentSection),
    isDesignSection: computed(isDesignSection),
  };
}

function useNavigate(
  router: VueRouter,
  tile: Readonly<Ref<Readonly<TileData>>>,
  section: Section,
): () => void {
  function navigate(): void {
    const tileId = tile.value?.id;
    if (section === Section.DESIGN) {
      router.replace({ path: `/edit/${tileId}/design` });
    } else {
      router.replace({ path: `/edit/${tileId}` });
    }
  }

  return navigate;
}

const TileEditor = defineComponent({
  components: {
    // eslint-disable-next-line vue/no-unused-components
    ContentEditors,
    // eslint-disable-next-line vue/no-unused-components
    DesignEditors,
    // eslint-disable-next-line vue/no-unused-components
    Buttons,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    next((vm) => {
      const { id } = to.params;
      return vm.$store.dispatch<GetTileDataAction>({
        type: ActionType.GET_TILE_DATA,
        payload: { id },
      })
        .then(() => vm.$store.dispatch<GetTileConfigAction>({
          type: ActionType.GET_TILE_CONFIG,
        }));
    });
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        }).then(() => (this as Vue).$store.dispatch<GetTileConfigAction>({
          type: ActionType.GET_TILE_CONFIG,
        }));
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const route = computed(() => root.$route);

    const tile = computed(() => root.$store.state.editedTileData);
    watch(tile, (newTileData, oldTileData) => {
      if (oldTileData !== undefined && newTileData === undefined) {
        root.$router.push({ path: '/' });
      }
    });
    const tileDataLoaded = computed(() => {
      const hasTileData = root.$store.state.editedTileData !== undefined;
      const hasTileConfig = root.$store.state.editedTileConfig !== undefined;
      return hasTileData && hasTileConfig;
    });

    const navigateToContent = useNavigate(root.$router, tile, Section.CONTENT);
    const navigateToDesign = useNavigate(root.$router, tile, Section.DESIGN);
    const { isContentSection, isDesignSection } = useSectionManager(route);

    return {
      i18n,
      tile,
      tileDataLoaded,
      navigateToContent,
      navigateToDesign,
      isContentSection,
      isDesignSection,
    };
  },
});

export default TileEditor;
