import { MutationPayload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { State } from '@/app/editor/client/store/state';

interface SetHasCustomCssChangesMutationPayload {
  hasCustomCssChanges: boolean;
}

export interface SetHasCustomCssChangesMutation extends MutationPayload {
  type: MutationType.SET_HAS_CUSTOM_CSS_CHANGES;
  payload: SetHasCustomCssChangesMutationPayload;
}

export default function setHasCistomCssChangesMutation(
  oldState: State,
  data: SetHasCustomCssChangesMutation,
): State {
  const newState = oldState;
  newState.hasCustomCssChanges = data.payload.hasCustomCssChanges;
  return newState;
}
