import { Profile } from '@/app/shared/client/type/profile';
import { Ref } from '@vue/composition-api';

export type GetProfileResponse = Profile

export function createGetProfileFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): () => Promise<GetProfileResponse> {
  return (): Promise<GetProfileResponse> => fetch(`${apiUrl}/${siteId.value}/profile`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetProfileResponse) => resp);
}
