import { Profile } from '@/app/shared/client/type/profile';
import { Ref } from '@vue/composition-api';

export interface UpdateProfileRequest {
  readonly profile: Profile;
}

export type UpdateProfileResponse = Profile

export function createUpdateProfileFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: UpdateProfileRequest) => Promise<UpdateProfileResponse> {
  return (request: UpdateProfileRequest): Promise<UpdateProfileResponse> => fetch(`${apiUrl}/${siteId.value}/profile`, {
    method: 'PUT',
    body: JSON.stringify(request.profile),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: UpdateProfileResponse) => resp);
}
