import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface SaveTileListRequest {
  readonly tiles: TileData[];
}

export interface SaveTileListResponse {
  readonly tiles: TileData[];
}

export function createSaveTileListFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: SaveTileListRequest) => Promise<SaveTileListResponse> {
  return (request: SaveTileListRequest): Promise<SaveTileListResponse> => fetch(`${apiUrl}/${siteId.value}/tile`, {
    method: 'PUT',
    body: JSON.stringify(request),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: SaveTileListResponse) => resp);
}
