















































import Vue from 'vue';
import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import useI18N from '@/app/shared/client/logic/i18n';
import { ComboBoxEditorControl } from '@/app/editor/client/view/component/control';
import {
  ComboBoxChoice,
} from '@/app/editor/client/view/component/control/ComboBoxEditorControl.vue';
import { LogoData, LogoType } from '@/app/shared/client/type/logo';
import useImageContentEditorActionHandler
  from '@/app/editor/client/logic/editor-action/image-content-editor';
import { ImageData } from '@/app/shared/client/type/image';
import { isImageInUploadingState } from '@/app/website/client/logic/image-uploading';
import { SaveTileDataAction } from '@/app/editor/client/store/action/save-tile-data';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { TileContent } from '@/app/shared/client/type/tile';
import { TileContentWithLogo } from '@/app/shared/client/type/tile/partial/content';

function useLogoContentUpdate(
  store: Store<State>,
): (logoContent: LogoData) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);

  function updateLogoContent(logoContent: LogoData): void {
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: 'logo', value: logoContent },
    );
    debouncedDispatch();
  }

  return updateLogoContent;
}

function useLogoTypeChangeHandler(
  updateLogoContent: (logoContent: LogoData) => void,
  logoContent: Readonly<Ref<LogoData | undefined>>,
): (value: LogoType | undefined) => void {
  function changeLogoType(value: LogoType | undefined): void {
    const newLogoContent = {
      ...logoContent.value,
      type: value,
    };

    updateLogoContent(newLogoContent);
  }

  return changeLogoType;
}

function useLogoTextChangeHandler(
  updateLogoContent: (logoContent: LogoData) => void,
  logoContent: Readonly<Ref<LogoData | undefined>>,
): (value: string) => void {
  function changeLogoText(value: string | undefined): void {
    const newLogoContent = {
      ...logoContent.value,
      text: value,
    };

    updateLogoContent(newLogoContent);
  }

  return changeLogoText;
}

function useLogoImageChangeHandler(
  store: Store<State>,
  tileId: string,
): (value: ImageData | undefined) => void {
  function changeLogoImage(value: ImageData | undefined): void {
    const actualTile = store.state.tiles.find((tile) => tile.id === tileId);
    if (actualTile === undefined) {
      return;
    }

    const actualTileContent = actualTile.content as TileContentWithLogo;

    const newLogoContent = {
      ...actualTileContent?.logo,
      image: value,
    };

    if (actualTile.id === store.state.editedTileData?.id) {
      store.commit(
        MutationType.UPDATE_EDITED_TILE_FIELD,
        { section: 'content', field: 'logo', value: newLogoContent },
      );
    }

    actualTileContent.logo = newLogoContent;
    actualTile.content = actualTileContent as TileContent;
    actualTile.hasChanges = true;
    store.dispatch<SaveTileDataAction>({
      type: ActionType.SAVE_TILE_DATA,
      payload: { tileData: actualTile },
    });
  }

  return changeLogoImage;
}

const TileLogoEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => vm.$store.dispatch<GetTileDataAction>({
      type: ActionType.GET_TILE_DATA,
      payload: { id },
    }));
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }

    next();
  },
  setup(_, { root }) {
    const { id: tileId } = root.$route.params;

    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const logoContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, LogoData | undefined>;
      return tileContentRecord?.logo;
    });

    const isTextLogo = computed(() => logoContent.value?.type === LogoType.TEXT);
    const isImageLogo = computed(() => logoContent.value?.type === LogoType.IMAGE);

    const updateLogoContent = useLogoContentUpdate(root.$store);

    const logoType = computed({
      get: () => logoContent.value?.type,
      set: useLogoTypeChangeHandler(updateLogoContent, logoContent),
    });
    const logoTypeChoices: ComboBoxChoice[] = [
      {
        key: LogoType.IMAGE,
        label: i18n('CommonLabels.Logo.Type.Image'),
      },
      {
        key: LogoType.TEXT,
        label: i18n('CommonLabels.Logo.Type.Text'),
      },
    ];

    const updateLogoText = useLogoTextChangeHandler(updateLogoContent, logoContent);
    const updateLogoImage = useLogoImageChangeHandler(root.$store, tileId);
    const logoImageEditorAction = useImageContentEditorActionHandler(
      root.$store,
      editedTile,
      'logo-image',
      updateLogoImage,
    );

    const isUploadingLogoImage = computed(() => {
      const { imageListInUploadingState } = state.value;
      return isImageInUploadingState(imageListInUploadingState, tileId, 'logo-image');
    });

    return {
      i18n,
      editedTile,
      tileDataLoaded,
      logoContent,
      isTextLogo,
      isImageLogo,
      logoType,
      logoTypeChoices,
      updateLogoText,
      logoImageEditorAction,
      isUploadingLogoImage,
    };
  },
});

export default TileLogoEditor;
