import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface SaveTileDataRequest {
  readonly tile: TileData;
}

export type SaveTileDataResponse = TileData;

export function createSaveTileDataFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: SaveTileDataRequest) => Promise<SaveTileDataResponse> {
  return (request: SaveTileDataRequest): Promise<SaveTileDataResponse> => fetch(`${apiUrl}/${siteId.value}/tile/${request.tile.id}`, {
    method: 'PUT',
    body: JSON.stringify(request.tile),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: SaveTileDataResponse) => resp);
}
