import { ActionContext, Payload } from 'vuex';
import { ActionType } from '@/app/editor/client/store/action/type';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { AuthorizeResponse } from '@/app/editor/client/api/authorize';

export interface AuthorizeAction extends Payload {
  type: ActionType.AUTHORIZE;
}

export default function authorizeAction(actionContext: ActionContext<State, State>): Promise<void> {
  return new Promise((resolve, reject) => {
    api.authorize({
      code: actionContext.state.context.apiToken,
    }).then((response: AuthorizeResponse) => {
      api.accessToken.value = response.accessToken;
      setTimeout(
        () => window.location.reload(),
        response.expiresIn * 1000,
      );
      resolve();
    }).catch((error: Error) => reject(error.message));
  });
}
