import { useSessionStorage } from '@vueuse/core';
import {
  AuthorizeRequest,
  AuthorizeResponse,
  createAuthorizeFunction,
} from '@/app/editor/client/api/authorize';
import {
  createGetProfileFunction,
  GetProfileResponse,
} from '@/app/editor/client/api/get-profile';
import {
  createUpdateProfileFunction,
  UpdateProfileRequest,
  UpdateProfileResponse,
} from '@/app/editor/client/api/update-profile';
import {
  createGetTranslationsFunction,
  GetTranslationsRequest,
  GetTranslationsResponse,
} from '@/app/editor/client/api/get-translations';
import {
  createGetImageBucketsFunction,
  GetImageBucketsResponse,
} from '@/app/editor/client/api/get-image-buckets';
import {
  createPublishChangesFunction,
  PublishChangesRequest, PublishChangesResponse,
} from '@/app/editor/client/api/publish-changes';
import {
  createDiscardChangesFunction,
  DiscardChangesResponse,
} from '@/app/editor/client/api/discard-changes';
import {
  createSaveTileListFunction,
  SaveTileListRequest,
  SaveTileListResponse,
} from '@/app/editor/client/api/save-tile-list';
import {
  createSaveTileDataFunction,
  SaveTileDataRequest,
  SaveTileDataResponse,
} from '@/app/editor/client/api/save-tile-data';
import {
  createGetTileListFunction, GetTileListRequest,
  GetTileListResponse,
} from '@/app/editor/client/api/get-tile-list';
import {
  AddTileFromShowcaseRequest,
  AddTileFromShowcaseResponse,
  createAddTileFromShowcaseFunction,
} from '@/app/editor/client/api/add-tile-from-showcase';
import {
  createGetTileDataFunction,
  GetTileDataRequest,
  GetTileDataResponse,
} from '@/app/editor/client/api/get-tile-data';
import {
  createGetTileConfigFunction,
  GetTileConfigRequest,
  GetTileConfigResponse,
} from '@/app/editor/client/api/get-tile-config';
import {
  createGetTileShowcaseFunction,
  GetTileShowcaseResponse,
} from '@/app/editor/client/api/get-tile-showcase';
import {
  createRemoveTileFunction,
  RemoveTileRequest,
  RemoveTileResponse,
} from '@/app/editor/client/api/remove-tile';
import {
  createGetImageUploadInfoFunction,
  GetImageUploadInfoRequest,
  GetImageUploadInfoResponse,
} from '@/app/editor/client/api/get-image-upload-info';
import {
  createGetImageUploadResultFunction,
  GetImageUploadResultRequest, GetImageUploadResultResponse,
} from '@/app/editor/client/api/get-image-upload-result';
import { ref, Ref } from '@vue/composition-api';

const siteId = ref(0);

const ACCESS_TOKEN = 'accessToken';

const accessToken = useSessionStorage<string | undefined>(
  ACCESS_TOKEN,
  undefined,
);

interface Api {
  siteId: Ref<number>;
  accessToken: Ref<string | undefined>;
  authorize: (request: AuthorizeRequest) => Promise<AuthorizeResponse>;
  getTranslations: (request: GetTranslationsRequest) => Promise<GetTranslationsResponse>;
  getImageBuckets: () => Promise<GetImageBucketsResponse>;
  getProfile: () => Promise<GetProfileResponse>;
  updateProfile: (request: UpdateProfileRequest) => Promise<UpdateProfileResponse>;
  publishChanges: (request: PublishChangesRequest) => Promise<PublishChangesResponse>;
  discardChanges: () => Promise<DiscardChangesResponse>
  addTileFromShowcase:
    (request: AddTileFromShowcaseRequest) => Promise<AddTileFromShowcaseResponse>;
  getTileData: (request: GetTileDataRequest) => Promise<GetTileDataResponse>;
  getTileList: (request: GetTileListRequest) => Promise<GetTileListResponse>;
  saveTileData: (request: SaveTileDataRequest) => Promise<SaveTileDataResponse>;
  saveTileList: (request: SaveTileListRequest) => Promise<SaveTileListResponse>;
  removeTile: (request: RemoveTileRequest) => Promise<RemoveTileResponse>;
  getTileConfig: (request: GetTileConfigRequest) => Promise<GetTileConfigResponse>;
  getTileShowcase: () => Promise<GetTileShowcaseResponse>;
  getImageUploadInfo: (request: GetImageUploadInfoRequest) => Promise<GetImageUploadInfoResponse>;
  getImageUploadResult:
    (request: GetImageUploadResultRequest) => Promise<GetImageUploadResultResponse>;
}

export function createApi(): Api {
  const apiUrl = window.editorApi.apiV1Url;

  return {
    siteId,
    accessToken,
    authorize: createAuthorizeFunction(apiUrl, siteId),
    getTranslations: createGetTranslationsFunction(apiUrl, siteId, accessToken),
    getImageBuckets: createGetImageBucketsFunction(apiUrl, siteId, accessToken),
    getProfile: createGetProfileFunction(apiUrl, siteId, accessToken),
    updateProfile: createUpdateProfileFunction(apiUrl, siteId, accessToken),
    publishChanges: createPublishChangesFunction(apiUrl, siteId, accessToken),
    discardChanges: createDiscardChangesFunction(apiUrl, siteId, accessToken),
    addTileFromShowcase: createAddTileFromShowcaseFunction(apiUrl, siteId, accessToken),
    getTileData: createGetTileDataFunction(apiUrl, siteId, accessToken),
    getTileList: createGetTileListFunction(apiUrl, siteId, accessToken),
    saveTileData: createSaveTileDataFunction(apiUrl, siteId, accessToken),
    saveTileList: createSaveTileListFunction(apiUrl, siteId, accessToken),
    removeTile: createRemoveTileFunction(apiUrl, siteId, accessToken),
    getTileConfig: createGetTileConfigFunction(apiUrl, siteId, accessToken),
    getTileShowcase: createGetTileShowcaseFunction(apiUrl, siteId, accessToken),
    getImageUploadInfo: createGetImageUploadInfoFunction(apiUrl, siteId, accessToken),
    getImageUploadResult: createGetImageUploadResultFunction(apiUrl, siteId, accessToken),
  };
}
export const api = createApi();

interface ApiContext {
  readonly siteId: number;
}
export function initApiContext(context: ApiContext): void {
  siteId.value = context.siteId;
}
