import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { commitSetSyncTilesStatus } from '@/app/editor/client/store/action/helpers';
import { TileData } from '@/app/shared/client/type/tile';

interface SaveTileDataPayload {
  tileData: TileData;
}

export interface SaveTileDataAction extends ActionPayload {
  type: ActionType.SAVE_TILE_DATA;
  payload: SaveTileDataPayload;
}

export default function saveTileDataAction(
  context: ActionContext <State, State>,
  data: SaveTileDataAction,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const { tileData } = data.payload;
    commitSetSyncTilesStatus(context.commit, true);
    api.saveTileData({ tile: tileData })
      .then(() => {
        commitSetSyncTilesStatus(context.commit, false);
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
