import { State } from '@/app/editor/client/store/state';

interface RemoveImageInUploadingStateMutationPayload {
  tileId: string;
  imageId: string;
}

export default function removeImageInUploadingStateMutation(
  oldState: State,
  payload: RemoveImageInUploadingStateMutationPayload,
): State {
  const { tileId, imageId } = payload;
  const newState = oldState;
  const uploadingImageForRemove = oldState.imageListInUploadingState
    .findIndex((imageInUploadingState) => {
      const tileIdMatch = imageInUploadingState.tileId === tileId;
      const imageIdMatch = imageInUploadingState.imageId === imageId;
      return tileIdMatch && imageIdMatch;
    });
  if (uploadingImageForRemove !== -1) {
    newState.imageListInUploadingState.splice(uploadingImageForRemove, 1);
  }
  return newState;
}
