





















































import { computed, defineComponent, PropType } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';

enum ComboBoxSize {
  MEDIUM = 'MEDIUM',
  LARGE = 'LARGE'
}

export interface ComboBoxChoice {
  key: undefined | string | number;
  label: string;
}

const ComboBoxEditorControl = defineComponent({
  components: {
    ArrowDownIcon: Icon.ArrowDownIcon,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: [String, Number],
    },
    choices: {
      required: true,
      type: Array as PropType<ComboBoxChoice[]>,
    },
    size: {
      required: false,
      default: ComboBoxSize.MEDIUM,
      type: String as PropType<ComboBoxSize>,
    },
    disabled: {
      required: false,
      default: false,
      type: Boolean,
    },
    placeholderLabel: {
      required: false,
      default: 'CommonLabels.ComboBox.Default.placeholder',
      type: String,
    },
  },
  setup(props, { emit, slots, root }) {
    const i18n = useI18N(root.$store);
    const sizeClass = computed(() => `ine-control--${props.size.toLowerCase()}`);

    const selectValue = computed({
      get: () => props.value,
      set: (value) => emit('input', value),
    });
    const selectedChoice = computed(() => {
      const { value, choices } = props;
      return choices.find((choice) => value === choice.key);
    });
    const selectedChoiceLabel = computed(() => selectedChoice?.value?.label);

    const hasIcon = computed(() => slots.icon !== undefined);
    const hasSelectedValue = computed(() => selectedChoice.value !== undefined);

    const selectOptions = computed(() => {
      if (hasSelectedValue.value) {
        return props.choices;
      }
      return [
        { label: i18n(props.placeholderLabel) },
        ...props.choices,
      ];
    });

    return {
      i18n,
      sizeClass,
      selectValue,
      hasSelectedValue,
      selectedChoiceLabel,
      hasIcon,
      selectOptions,
    };
  },
});

export default ComboBoxEditorControl;
