import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { TileShowcaseCategory } from '@/app/editor/client/type/showcase';

interface SetTileShowcaseCategoriesMutationPayload {
  categories: TileShowcaseCategory[];
}

export interface SetTileShowcaseCategoriesMutation extends MutationPayload {
  type: MutationType.SET_TILE_SHOWCASE_CATEGORIES;
  payload: SetTileShowcaseCategoriesMutationPayload;
}

export default function setTileShowcaseCategoriesMutation(
  oldState: State,
  data: SetTileShowcaseCategoriesMutation,
): State {
  const newState = oldState;
  newState.tileShowcaseCategories = data.payload.categories;
  return newState;
}
