

























import {
  computed, defineComponent, PropType, Ref, toRefs,
} from '@vue/composition-api';
import { VueRouter } from 'vue-router/types/router';
import Icon from '@/app/editor/client/view/icon/';
import useI18N from '@/app/shared/client/logic/i18n';
import { State } from '@/app/editor/client/store/state';
import { nameOf } from '@/app/shared/client/helper/base';
import { TileData } from '@/app/shared/client/type/tile';

function useNavigate(router: VueRouter, id: string): () => void {
  function navigate(): void {
    router.push({ path: `/edit/${id}` });
  }

  return navigate;
}

function useShowModifiedMarker(tileRef: Ref<TileData>, state: State): () => boolean {
  return (): boolean => {
    const tile = tileRef.value;
    if (!tile.hasChanges) {
      return false;
    }
    const publishedTile = state.publishedTiles
      .find((currentTile) => currentTile.id === tile.id);
    if (publishedTile === undefined) {
      return true;
    }

    const publishedTilePropertyMap = new Map(Object.entries(publishedTile));
    return Object.entries(tile).some(([key, value]) => {
      // не отображаем синюю точку, если поменялся лишь порядок тайлов
      const isIgnoredProperty = key === nameOf<TileData>('order')
          || key === nameOf<TileData>('hasChanges');
      const isPropertyEquals = value === publishedTilePropertyMap.get(key);
      return !isIgnoredProperty && !isPropertyEquals;
    });
  };
}

const TileItem = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
  },
  props: {
    tile: {
      required: true,
      type: Object as PropType<TileData>,
    },
    activity: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root }) {
    const { tile } = toRefs(props);
    const i18n = useI18N(root.$store);
    const navigateToEdit = useNavigate(root.$router, tile.value.id);
    const showModifiedMarker = computed(useShowModifiedMarker(tile, root.$store.state));

    return {
      i18n,
      navigateToEdit,
      showModifiedMarker,
    };
  },
});

export default TileItem;
