
































import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { ActionType } from '@/app/editor/client/store/action/type';
import {
  ContentEditor, ContentEditorType, EditorButton, EditorButtonAdviceType, LayoutConfig,
} from '@/app/editor/client/type/tile-config';
import { VueRouter } from 'vue-router/types/router';
import { contentEditorName, useEditorButtonCreator } from '@/app/editor/client/logic/tile-editor';
import {
  ImageContentEditorActionPayload,
} from '@/app/editor/client/type/editor-action/image-content-editor';
import { ImageData } from '@/app/shared/client/type/image';
import useImageContentEditorActionHandler
  from '@/app/editor/client/logic/editor-action/image-content-editor';
import { SaveTileDataAction } from '@/app/editor/client/store/action/save-tile-data';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { TileData } from '@/app/shared/client/type/tile';
import useMenuContentEditorActionHandler
  from '@/app/editor/client/logic/editor-action/menu-content-editor';
import useI18N from '@/app/shared/client/logic/i18n';

import { StoreTileExternalContent } from '@/app/shared/client/type/tile/store-tile';
import { Profile } from '@/app/shared/client/type/profile';
import { HeaderTileExternalContent } from '@/app/shared/client/type/tile/header-tile';
import { FooterTileExternalContent } from '@/app/shared/client/type/tile/footer-tile';

type ContentEditorAction = (value: ImageContentEditorActionPayload & string & undefined) => void;

interface ContentEditorData {
  name?: string;
  field?: string;
  label?: string;
  placeholderLabel?: string;
  action?: ContentEditorAction;
}

function useUpdateValue(
  store: Store<State>, field: string | undefined,
): (v: string | undefined) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);
  function updateValue(value: string | undefined): void {
    store.commit(MutationType.UPDATE_EDITED_TILE_FIELD, { section: 'content', field, value });
    debouncedDispatch();
  }
  return updateValue;
}

function useTileImageUpdate(
  store: Store<State>,
  tileId: string,
  field: string | undefined,
): (newImageData: ImageData | undefined) => void {
  function updateImage(newImageData: ImageData | undefined): void {
    if (field === undefined) {
      return;
    }

    const actualTile = store.state.tiles.find((tile) => tile.id === tileId);
    if (actualTile === undefined) {
      return;
    }

    const actualTileContent = actualTile.content as Record<string, unknown>;

    if (actualTile.id === store.state.editedTileData?.id) {
      store.commit(MutationType.UPDATE_EDITED_TILE_FIELD, {
        section: 'content',
        field,
        value: newImageData,
      });
    }

    actualTileContent[field] = newImageData;
    actualTile.content = actualTileContent;
    actualTile.hasChanges = true;
    store.dispatch<SaveTileDataAction>({
      type: ActionType.SAVE_TILE_DATA,
      payload: { tileData: actualTile },
    });
  }
  return updateImage;
}

function useNavigateToActionLinkEditor(
  $router: VueRouter,
  tile: Readonly<Ref<TileData>>,
  actionLinkId: string | undefined,
): () => void {
  function navigate(): void {
    const tileId = tile.value?.id;
    $router.push({ path: `/edit/${tileId}/action-link/${actionLinkId}` });
  }

  return navigate;
}

