function useAction(
  store: Store<State>,
  router: VueRouter,
  i18n: (label: string) => string,
  tile: Readonly<Ref<TileData>>,
  editor: ContentEditor,
): ContentEditorAction {
  switch (editor.type) {
    case ContentEditorType.TEXT_EDITOR:
    case ContentEditorType.MULTILINE_TEXT_EDITOR:
      return useUpdateValue(store, editor.field);
    case ContentEditorType.ACTION_LINK_EDITOR:
      return useNavigateToActionLinkEditor(router, tile, editor.field);
    case ContentEditorType.IMAGE_EDITOR: {
      const updateImage = useTileImageUpdate(store, tile.value.id, editor.field);
      return useImageContentEditorActionHandler(
        store,
        tile,
        editor.field,
        updateImage,
      );
    }
    case ContentEditorType.CONTACT_INFO_EDITOR:
      return useContactInfoEditorActionHandler(router, tile);
    case ContentEditorType.CONTACT_INFO_WITH_SUBHEADING_EDITOR:
      return useContactInfoWithSubheadingEditorActionHandler(router, tile);
    case ContentEditorType.LOGO_EDITOR:
      return useLogoEditorActionHandler(router, tile);
    case ContentEditorType.HOURS_EDITOR:
      return useHoursEditorActionHandler(router, tile);
    case ContentEditorType.SOCIAL_ICON_LIST_EDITOR:
      return useSocialIconListEditorActionHandler(router, tile, editor.field);
    case ContentEditorType.LOCATION_EDITOR:
      return useLocationEditorActionHandler(router, tile, editor.field);
    case ContentEditorType.MENU_EDITOR:
      return useMenuContentEditorActionHandler(store, router, i18n, tile);
    default:
      throw new Error('Unknown action for editor');
  }
}

function useContentEditorCreator(
  store: Store<State>,
  router: VueRouter,
  i18n: (label: string) => string,
  tile: Readonly<Ref<TileData>>,
): (editor: ContentEditor) => ContentEditorData {
  function createDesignEditor(editor: ContentEditor): ContentEditorData {
    const {
      type, field, label, placeholderLabel,
    } = editor;
    const action = useAction(store, router, i18n, tile, editor);
    return {
      name: contentEditorName(type),
      field,
      label,
      placeholderLabel,
      action,
    };
  }

  return createDesignEditor;
}

function isAdviceApplied(
  profile: Profile,
  externalContent:
    StoreTileExternalContent | HeaderTileExternalContent | FooterTileExternalContent | undefined,
  adviceType: EditorButtonAdviceType | undefined,
): boolean {
  switch (adviceType) {
    case EditorButtonAdviceType.STORE_CLOSED:
      return profile.storeClosed ?? false;
    case EditorButtonAdviceType.NO_CATEGORIES:
      return externalContent !== undefined
        ? (externalContent as StoreTileExternalContent).storeData.categories.length === 0
        : false;
    case EditorButtonAdviceType.NO_PRODUCTS:
      return externalContent !== undefined
        ? (externalContent as StoreTileExternalContent).storeData.products.length === 0
        : false;
    default:
      return false;
  }
}

function useEditorButtonAdviceLabelGetter(
  profile: Profile,
  externalContent:
    StoreTileExternalContent | HeaderTileExternalContent | FooterTileExternalContent | undefined,
): (button: EditorButton) => string | undefined {
  function getEditorButtonAdviceLabel(button: EditorButton): string | undefined {
    const appliedAdvices = button.advices?.filter((advice) => isAdviceApplied(
      profile,
      externalContent,
      advice.type,
    ));
    return (typeof appliedAdvices !== 'undefined' && appliedAdvices.length > 0)
      ? appliedAdvices[0].label
      : undefined;
  }
  return getEditorButtonAdviceLabel;
}

const TileContentEditor = defineComponent({
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData as TileData);

    const content = computed(() => editedTile.value?.content);
    const externalContent = computed(() => editedTile.value?.externalContent);
    const design = computed(() => editedTile.value?.design);

    const layoutConfigList = computed(() => {
      const { editedTileConfig } = root.$store.state;
      return editedTileConfig.layoutConfigList as LayoutConfig[];
    });
    const currentLayoutConfig = computed(() => {
      const configList = layoutConfigList.value;
      return configList
        .find((config: LayoutConfig) => {
          const currentLayout = design?.value?.layout;
          return config.name === currentLayout;
        });
    });

    const mainEditorList = computed(() => {
      const createContentEditor = useContentEditorCreator(
        root.$store,
        root.$router,
        i18n,
        editedTile,
      );
      return currentLayoutConfig.value
        ?.contentEditorConfig
        ?.mainEditors
        ?.map(createContentEditor);
    });

    const editorButtons = computed(() => {
      const createEditorButton = useEditorButtonCreator();
      return currentLayoutConfig.value
        ?.contentEditorConfig
        ?.buttons
        ?.map(createEditorButton);
    });

    const getEditorButtonAdviceLabel = useEditorButtonAdviceLabelGetter(
      root.$store.state.profile,
      externalContent.value,
    );
    return {
      mainEditorList,
      editorButtons,
      content,
      editedTile,
      getEditorButtonAdviceLabel,
    };
  },
});

export default TileContentEditor;
