import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { TileData } from '@/app/shared/client/type/tile';

interface AddTileMutationPayload {
  tile?: TileData;
}

export interface AddTileMutation extends MutationPayload {
  type: MutationType.ADD_TILE;
  payload: AddTileMutationPayload;
}

export default function addTileMutation(oldState: State, data: AddTileMutation): State {
  const newState = oldState;
  const { tile } = data.payload;
  if (tile != null) {
    newState.tiles.push(tile);
    newState.tiles.sort((a: TileData, b: TileData) => a.order - b.order);
  }
  return newState;
}
