import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import {
  ImageContentEditorActionPayload,
  ImageContentEditorActionType,
} from '@/app/editor/client/type/editor-action/image-content-editor';
import { uploadImage } from '@/app/editor/client/logic/image-uploader';
import { ImageData } from '@/app/shared/client/type/image';
import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export default function useImageContentEditorActionHandler(
  store: Store<State>,
  tile: Ref<Readonly<TileData | undefined>>,
  imageId: string | undefined,
  updateImage: (newImageData: ImageData | undefined) => void,
): (payload: ImageContentEditorActionPayload) => void {
  function imageEditorAction(payload: ImageContentEditorActionPayload): void {
    if (imageId === undefined) {
      return;
    }

    const { type, imageUploadingData, convertOperations } = payload;

    switch (type) {
      case ImageContentEditorActionType.UPLOAD_IMAGE: {
        if (imageUploadingData === undefined) {
          return;
        }

        const tileId = tile.value?.id;
        if (tileId === undefined) {
          return;
        }

        uploadImage(store, tileId, imageId, imageUploadingData,
          updateImage, convertOperations);
        return;
      }

      case ImageContentEditorActionType.REMOVE_IMAGE: {
        updateImage(undefined);
        return;
      }

      default: throw new Error('Unknown action for image content editor');
    }
  }

  return imageEditorAction;
}
