import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SaveTileDataAction } from '@/app/editor/client/store/action/save-tile-data';

interface DiscardTileDesignActionPayload {
  readonly tileId: string;
}

export interface DiscardTileDesignAction extends ActionPayload {
  type: ActionType.DISCARD_TILE_DESIGN;
  payload: DiscardTileDesignActionPayload;
}

export default function discardTileDesignAction(
  actionContext: ActionContext<State, State>,
): void {
  actionContext.commit(MutationType.DISCARD_TILE_DESIGN);

  const tileData = actionContext.state.editedTileData;
  if (tileData === undefined) {
    return;
  }

  actionContext.dispatch<SaveTileDataAction>(
    { type: ActionType.SAVE_TILE_DATA, payload: { tileData } },
  );
}
