


































import {
  computed,
  defineComponent, onMounted,
  PropType,
  ref,
  Ref,
} from '@vue/composition-api';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import useFocusHandler from '@/app/editor/client/logic/focus';
import { calculateSizeClass, ControlSize } from '@/app/editor/client/logic/control';

function resizeTextArea(textAreaRef: Ref<HTMLElement | undefined>): void {
  if (textAreaRef.value) {
    textAreaRef.value.style.height = '0px';
    textAreaRef.value.style.height = `${textAreaRef.value?.scrollHeight}px`;
  }
}

const MultilineTextEditorControl = defineComponent({
  props: {
    placeholderLabel: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: String,
    },
    maxLength: {
      required: false,
      default: undefined,
      type: String,
    },
    size: {
      required: false,
      default: ControlSize.MEDIUM,
      type: String as PropType<ControlSize>,
    },
  },
  setup(props, { emit }) {
    const textAreaRef = ref<HTMLElement>();
    const localValue = computed({
      get: () => props.value,
      set: (v) => {
        resizeTextArea(textAreaRef);
        emit('input', v);
      },
    });

    const { isFocused, onFocus, onBlur } = useFocusHandler();

    const sizeClass = computed(() => calculateSizeClass(props.size));

    onMounted(() => {
      resizeTextArea(textAreaRef);
    });

    return {
      textAreaRef,
      localValue,
      isEmpty: computed(() => isNullOrEmpty(props.value)),
      isFocused,
      onFocus,
      onBlur,
      sizeClass,
    };
  },
});

export default MultilineTextEditorControl;
