








import { computed, defineComponent } from '@vue/composition-api';
import { PopupType } from '@/app/editor/client/type/popup';
import Popups from './popup';

function getPopupComponentName(popup: PopupType): string {
  switch (popup) {
    case PopupType.TILE_SHOWCASE:
      return 'TileShowcasePopup';
    default:
      throw new Error('Unknown popup type');
  }
}

const Popup = defineComponent({
  components: {
    Popups,
  },
  setup(_, { root }) {
    const currentPopupComponentName = computed(() => {
      const { currentPopup } = root.$store.state;
      return getPopupComponentName(currentPopup);
    });

    return {
      currentPopupComponentName,
    };
  },
});

export default Popup;
