import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface DiscardChangesResponse {
  readonly tiles: TileData[];
}

export function createDiscardChangesFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): () => Promise<DiscardChangesResponse> {
  return (): Promise<DiscardChangesResponse> => fetch(`${apiUrl}/${siteId.value}/discard`, {
    method: 'POST',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: DiscardChangesResponse) => resp);
}
