
































import Vue from 'vue';
import { computed, defineComponent, Ref } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { contentEditorName } from '@/app/editor/client/logic/tile-editor';
import useI18N from '@/app/shared/client/logic/i18n';
import { ContactInfoData } from '@/app/shared/client/type/contact-info';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { ContentEditor, ContentEditorType } from '@/app/editor/client/type/tile-config';

function getEditorList(withSubheading: boolean): ContentEditor[] {
  const SubheadingEditor = {
    type: ContentEditorType.TEXT_EDITOR,
    field: 'subheading',
    label: 'CommonLabels.ContactInfo.Subheading',
    descriptionLabel: '',
    placeholderLabel: 'CommonLabels.ContactInfo.Subheading.placeholder',
    actionLabel: '',
    url: '',
    maxListItems: 0,
  };
  const PhoneEditor = {
    type: ContentEditorType.TEXT_EDITOR,
    field: 'phone',
    label: 'CommonLabels.ContactInfo.Phone',
    descriptionLabel: '',
    placeholderLabel: 'CommonLabels.ContactInfo.Phone.placeholder',
    actionLabel: '',
    url: '',
    maxListItems: 0,
  };
  const EmailEditor = {
    type: ContentEditorType.TEXT_EDITOR,
    field: 'email',
    label: 'CommonLabels.ContactInfo.Email',
    descriptionLabel: '',
    placeholderLabel: 'CommonLabels.ContactInfo.Email.placeholder',
    actionLabel: '',
    url: '',
    maxListItems: 0,
  };

  const optionalEditorList = [];
  if (withSubheading) {
    optionalEditorList.push(SubheadingEditor);
  }

  return [
    ...optionalEditorList,
    PhoneEditor,
    EmailEditor,
  ];
}

function useUpdateValue(
  store: Store<State>,
  field: string | undefined,
  contactInfoContent: Readonly<Ref<ContactInfoData | undefined>>,
): (v: string) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);
  function updateValue(value: string): void {
    if (field === undefined) {
      return;
    }

    if (contactInfoContent.value === undefined) {
      return;
    }
    (contactInfoContent.value as unknown as Record<string, string | undefined>)[field] = value;
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: 'contactInfo', value: contactInfoContent.value },
    );
    debouncedDispatch();
  }
  return updateValue;
}

const TileContactInfoEditor = defineComponent({
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => vm.$store.dispatch<GetTileDataAction>({
      type: ActionType.GET_TILE_DATA,
      payload: { id },
    }));
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }
    next();
  },
  props: {
    withSubheading: {
      required: false,
      default: false,
      type: Boolean,
    },
  },
  setup(props, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const contactInfoContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, ContactInfoData | undefined>;
      return tileContentRecord?.contactInfo;
    });

    const contactInfoEditorList = computed(() => {
      const editorList = getEditorList(props.withSubheading).map((editor) => {
        const {
          type, field, label, placeholderLabel,
        } = editor;
        const updateValue = useUpdateValue(root.$store, field, contactInfoContent);
        return {
          name: contentEditorName(type),
          field,
          label,
          placeholderLabel,
          updateValue,
        };
      });
      return editorList;
    });

    return {
      i18n,
      tileDataLoaded,
      contactInfoEditorList,
      contactInfoContent,
    };
  },
});

export default TileContactInfoEditor;
