import { State } from '@/app/editor/client/store/state';
import { Payload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';

export interface DiscardChangesMutation extends Payload {
  type: MutationType.DISCARD_CHANGES;
}

export default function discardChangesMutation(oldState: State): State {
  const newState = oldState;
  newState.siteSettings = newState.publishedSiteSettings;
  newState.tiles = newState.publishedTiles;
  if (newState.editedTileData !== undefined) {
    newState.editedTileData = newState.publishedTiles
      .find((tile) => tile.id === newState.editedTileData?.id);
  }
  return newState;
}
