import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface PublishChangesRequest {
  readonly storeUrl: string;
}

export interface PublishChangesResponse {
  readonly tiles: TileData[];
}

export function createPublishChangesFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: PublishChangesRequest) => Promise<PublishChangesResponse> {
  return (request: PublishChangesRequest): Promise<PublishChangesResponse> => fetch(`${apiUrl}/${siteId.value}/publish`, {
    method: 'POST',
    body: JSON.stringify(request),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: PublishChangesResponse) => resp);
}
