import { ActionContext, Payload } from 'vuex';
import { ActionType } from '@/app/editor/client/store/action/type';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { GetTileConfigResponse } from '@/app/editor/client/api/get-tile-config';
import { SetTileConfigMutation } from '@/app/editor/client/store/mutation/set-tile-config';

export interface GetTileConfigAction extends Payload {
  type: ActionType.GET_TILE_CONFIG;
}

export default function getTileConfigAction(
  actionContext: ActionContext<State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const type = actionContext.state.editedTileData?.type;
    if (type === undefined) {
      reject();
      return;
    }

    api.getTileConfig({ type })
      .then((response: GetTileConfigResponse) => {
        const payload = {
          tileConfig: response.config,
        };
        actionContext.commit<SetTileConfigMutation>({
          type: MutationType.SET_TILE_CONFIG,
          payload,
        });

        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
