import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { TileData } from '@/app/shared/client/type/tile';

interface SetTileListMutationPayload {
  tileList: TileData[];
  publishedTileList?: TileData[];
}

export interface SetTileListMutation extends MutationPayload {
  type: MutationType.SET_TILE_LIST;
  payload: SetTileListMutationPayload;
}

export default function setTileListMutation(oldState: State, data: SetTileListMutation): State {
  const newState = oldState;
  newState.tiles = data.payload.tileList;
  if (data.payload.publishedTileList !== undefined) {
    newState.publishedTiles = data.payload.publishedTileList;
  }
  return newState;
}
