  interface Accordion {
    isActiveAccordion: (accordionId: string) => boolean;
    onChangeActiveAccordion: (accordionId: string) => void;
  }
function useAccordion(): Accordion {
  const activeAccordionRef = ref<string | undefined>(undefined);

  function isActiveAccordion(accordionId: string): boolean {
    return activeAccordionRef.value === accordionId;
  }

  function onChangeActiveAccordion(accordionId: string): void {
    if (!isActiveAccordion(accordionId)) {
      activeAccordionRef.value = accordionId;
    } else {
      activeAccordionRef.value = undefined;
    }
  }

  return {
    isActiveAccordion,
    onChangeActiveAccordion,
  };
}

function updateSiteDesignSettings(store: Store<State>, field: string, value: unknown): void {
  const debouncedDispatch = debounce(() => store.dispatch<SaveSiteSettingsAction>({
    type: ActionType.SAVE_SITE_SETTINGS,
  }),
  1000);
  store.commit<UpdateSiteSettingsFieldMutation>({
    type: MutationType.UPDATE_SITE_SETTINGS_FIELD,
    payload: { section: 'design', field, value },
  });
  sendPreviewSiteSettings(store.state.siteSettings);
  debouncedDispatch();
}

function useTitleTextSizeChangeHandler(
  $store: Store<State>,
  siteDesignSettings: Readonly<Ref<Readonly<GlobalDesign | undefined>>>,
): (value?: number) => void {
  function onTitleTextSizeChange(value?: number): void {
    const newTextSize = {
      ...siteDesignSettings.value?.textSize,
      title: value,
    };
    updateSiteDesignSettings($store, 'textSize', newTextSize);
  }
  return onTitleTextSizeChange;
}

