function useLocaleChangeHandler(
  $store: Store<State>,
  profile: Readonly<Ref<Profile | undefined>>,
): (value?: Locale) => void {
  function onLocaleChange(value?: Locale): void {
    if (profile.value === undefined) {
      return;
    }

    const newProfile = {
      ...profile.value,
      locale: value as string,
    };

    $store.dispatch<UpdateProfileAction>({
      type: ActionType.UPDATE_PROFILE,
      payload: {
        profile: newProfile,
      },
    });

    $store.dispatch<GetTranslationsAction>({
      type: ActionType.GET_TRANSLATIONS,
      payload: { locale: value },
    }).then(
      () => sendPreviewContext($store.state.context.bucketUrls, $store.state.websiteTranslations),
    );
  }
  return onLocaleChange;
}

