import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { SaveTileDataAction } from '@/app/editor/client/store/action/save-tile-data';
import { SaveSiteSettingsAction } from '@/app/editor/client/store/action/save-site-settings';

let timeout = 0;
let currentSaveTileSettingsOperation: (() => Promise<void>) | undefined;

function performDebouncedOperationCall(): void {
  window.clearTimeout(timeout);
  timeout = window.setTimeout(() => {
    if (currentSaveTileSettingsOperation !== undefined) {
      currentSaveTileSettingsOperation();
      currentSaveTileSettingsOperation = undefined;
    }
  }, 1000);
}

export function useSaveTileSettingsDebounced(store: Store<State>): () => void {
  return (): void => {
    const tileData = store.state.editedTileData;
    if (tileData === undefined) {
      return;
    }
    currentSaveTileSettingsOperation = (): Promise<void> => store.dispatch<SaveTileDataAction>({
      type: ActionType.SAVE_TILE_DATA,
      payload: { tileData },
    });

    performDebouncedOperationCall();
  };
}

export function useSaveSiteSettingsDebounced(store: Store<State>): () => void {
  return (): void => {
    currentSaveTileSettingsOperation = (): Promise<void> => store.dispatch<SaveSiteSettingsAction>({
      type: ActionType.SAVE_SITE_SETTINGS,
    });

    performDebouncedOperationCall();
  };
}

export function forceSaveTileSettingsDebounced(): Promise<void> {
  if (currentSaveTileSettingsOperation === undefined) {
    return Promise.resolve();
  }

  window.clearTimeout(timeout);
  return currentSaveTileSettingsOperation();
}

export default {};
