import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { commitSetSyncTilesStatus } from '@/app/editor/client/store/action/helpers';
import { TileData } from '@/app/shared/client/type/tile';

export interface SaveSiteSettingsAction extends Payload {
  type: ActionType.SAVE_SITE_SETTINGS;
}

export default function saveSiteSettingsAction(
  actionContext: ActionContext <State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const { siteSettings } = actionContext.state;
    if (siteSettings === undefined) {
      reject();
      return;
    }

    commitSetSyncTilesStatus(actionContext.commit, true);
    api.saveTileData({
      tile: siteSettings as unknown as TileData,
    }).then(() => {
      commitSetSyncTilesStatus(actionContext.commit, false);
      resolve();
    }).catch((error: Error) => reject(error.message));
  });
}
