import { GetterTree } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { TileData } from '@/app/shared/client/type/tile';

function allChangesPublished(state: State): boolean {
  const allTilesPublished = state.tiles.every((tile) => !tile.hasChanges);
  const globalSettingsPublished = !state.siteSettings?.hasChanges ?? true;
  const sameTilesCount = state.tiles.length === state.publishedTiles.length;
  return allTilesPublished && globalSettingsPublished && sameTilesCount;
}

function blockTileList(state: State): TileData[] {
  return state.tiles.filter((tile) => tile.role === 'BLOCK');
}

export default function createGetters(): GetterTree<State, State> {
  return {
    allChangesPublished,
    blockTileList,
  };
}
