import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { AddTileMutation } from '@/app/editor/client/store/mutation/add-tile';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { AddTileFromShowcaseResponse } from '@/app/editor/client/api/add-tile-from-showcase';
import { TileData } from '@/app/shared/client/type/tile';

export interface CreateGDPRBannerTileAction extends ActionPayload {
  type: ActionType.CREATE_GDPR_BANNER_TILE;
}

const GDPR_BANNER_TILE_SHOWCASE_ID = 'gdpr_banner';

export default function createGDPRBannerTileAction(
  context: ActionContext <State, State>,
): Promise<TileData> {
  return new Promise((resolve, reject) => {
    api.addTileFromShowcase({
      tileShowcaseItemId: GDPR_BANNER_TILE_SHOWCASE_ID,
    })
      .then((response: AddTileFromShowcaseResponse) => {
        const tile = response;
        if (tile === undefined) {
          reject();
          return;
        }

        const addTilePayload = { tile };
        context.commit<AddTileMutation>({
          type: MutationType.ADD_TILE,
          payload: addTilePayload,
        });

        resolve(tile);
      }).catch((error: Error) => reject(error.message));
  });
}
