













































import { defineComponent } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { PopupType } from '@/app/editor/client/type/popup';
import { SetPopupMutation } from '@/app/editor/client/store/mutation/set-popup';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import useI18N from '@/app/shared/client/logic/i18n';

function useClosePopupHandler(store: Store<State>): () => void {
  function closePopup(): void {
    switch (store.state.currentPopup) {
      case PopupType.TILE_SHOWCASE:
        store.commit<SetPopupMutation>({
          type: MutationType.SET_POPUP,
          payload: {
            popup: undefined,
          },
        });
        break;
      default:
        break;
    }
  }
  return closePopup;
}

const Container = defineComponent({
  components: {
    BackIcon: Icon.BackIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const closePopup = useClosePopupHandler(root.$store);

    return {
      i18n,
      closePopup,
    };
  },
});

export default Container;
