




















































































import {
  computed, defineComponent, PropType, ref,
} from '@vue/composition-api';
import useFocusHandler from '@/app/editor/client/logic/focus';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import { Sketch } from 'vue-color';
import tinycolor from 'tinycolor2';
import Icon from '@/app/editor/client/view/icon';
import { Color, HSLColor, RGBAColor } from '@/app/shared/client/type/color';
import useI18N from '@/app/shared/client/logic/i18n';

interface VueColor {
  hex?: string;
  hex8?: string;
  hsl?: HSLColor;
  rgba?: RGBAColor;
}

function constructColorForPreview(color?: Color, defaultColor?: Color): Record<string, string> {
  const colorForPreview = color ?? defaultColor;

  const hue = colorForPreview?.hsl?.h ?? 0;
  const saturation = colorForPreview?.hsl?.s ?? 0;
  const lightness = colorForPreview?.hsl?.l ?? 0;
  const alpha = colorForPreview?.hsl?.a ?? 1;

  return {
    '--local-color-h': `${hue}`,
    '--local-color-s': `${saturation * 100}%`,
    '--local-color-l': `${lightness * 100}%`,
    '--local-color-a': `${alpha}`,
  };
}

function toColorFromRaw(
  alphaEnabled: boolean,
  defaultColor?: Color,
  raw?: string,
): Color | undefined {
  if (isNullOrEmpty(raw)) {
    return undefined;
  }

  const auto = raw?.toLowerCase() === 'auto';
  if (auto) {
    return {
      raw,
      auto: true,
    };
  }

  const color = tinycolor(raw);
  if (!color.isValid()) {
    return {
      raw,
      hex: defaultColor?.hex,
      hsl: defaultColor?.hsl,
      rgba: defaultColor?.rgba,
      auto: defaultColor?.auto,
    };
  }

  if (!alphaEnabled) {
    color.setAlpha(1);
  }
  return {
    raw,
    hex: color.toHex8String(),
    hsl: color.toHsl(),
    rgba: color.toRgb(),
    auto: false,
  };
}

function toColorFromVueColor(alphaEnabled: boolean, color?: VueColor): Color {
  const opaqueColor = color?.rgba?.a === 1;
  let hexValue;
  if (!alphaEnabled || opaqueColor) {
    hexValue = color?.hex;
  } else {
    hexValue = color?.hex8;
  }

  return {
    raw: hexValue,
    hex: hexValue,
    hsl: color?.hsl,
    rgba: color?.rgba,
  };
}

function toVueColorFromColor(color?: Color, defaultColor?: Color): VueColor {
  const currentColor = color ?? defaultColor;

  return {
    hex: currentColor?.hex,
    hex8: currentColor?.hex,
    hsl: currentColor?.hsl,
    rgba: currentColor?.rgba,
  };
}

function useAutoColorClickHandler(
  alphaEnabled: boolean,
  emit: (event: string, args?: Color) => void,
): () => void {
  function onAutoColorClick(): void {
    emit('input', toColorFromRaw(alphaEnabled, undefined, 'Auto'));
  }
  return onAutoColorClick;
}

function useResetToDefaultClickHandler(
  emit: (event: string, args?: Color) => void,
): () => void {
  function onResetToDefaultClick(): void {
    emit('input', undefined);
  }
  return onResetToDefaultClick;
}

const ColorBoxEditorControl = defineComponent({
  components: {
    ColorPicker: Sketch,
    AutoColorIcon: Icon.AutoColorIcon,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<Color>,
    },
    defaultValue: {
      required: true,
      type: Object as PropType<Color>,
    },
    alphaEnabled: {
      required: false,
      default: false,
      type: Boolean,
    },
    autoEnabled: {
      required: false,
      default: false,
      type: Boolean,
    },
    resetColorEnabled: {
      required: false,
      default: true,
      type: Boolean,
    },
    preset: {
      required: false,
      default: (): string[] => [
        '#FFFFFF',
        '#333333',
        '#7AB2FF',
        '#FED876',
        '#FE9E76',
        '#FF9CA1',
        '#BE9CC2',
      ],
      type: Array as PropType<string[]>,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const colorPreview = computed(() => constructColorForPreview(props.value, props.defaultValue));
    const colorInput = computed({
      get: () => props.value?.raw,
      set: (value) => emit('input', toColorFromRaw(props.alphaEnabled, props.defaultValue, value)),
    });
    const colorPicker = computed({
      get: () => toVueColorFromColor(props.value, props.defaultValue),
      set: (value) => emit('input', toColorFromVueColor(props.alphaEnabled, value)),
    });
    const colorPreset = computed(() => {
      const uniqPreset: (string)[] = Array.from(new Set(props.preset));
      if (props.autoEnabled) {
        return ['auto', ...uniqPreset];
      }
      return uniqPreset;
    });

    const defaultColorText = computed(() => {
      const defaultColorCode = props.defaultValue.raw ?? '';
      return i18n('CommonLabels.ColorPicker.DefaultColor')
        .replace('{colorCode}', defaultColorCode);
    });

    const isAutoColor = computed(() => props.value?.auto ?? false);
    const isEmpty = computed(() => isNullOrEmpty(props.value?.raw));
    const { isFocused, onFocus, onBlur } = useFocusHandler();
    const onAutoColorClick = useAutoColorClickHandler(props.alphaEnabled, emit);

    const onResetToDefaultClick = useResetToDefaultClickHandler(emit);

    const pickerRef = ref<HTMLElement>();
    const pickerOffsetTop = ref(0);
    const onShowPicker = (): void => {
      onFocus();
      pickerOffsetTop.value = pickerRef.value?.offsetTop ?? 0;
    };
    const slidePickerDown = computed(() => pickerOffsetTop.value < 400);

    return {
      pickerRef,
      i18n,
      colorPreview,
      colorInput,
      colorPicker,
      colorPreset,
      defaultColorText,
      isAutoColor,
      isEmpty,
      isFocused,
      onShowPicker,
      onFocus,
      onBlur,
      onAutoColorClick,
      onResetToDefaultClick,
      slidePickerDown,
    };
  },
});

export default ColorBoxEditorControl;
