import { PreviewApiEventType, SaveMobilePreviewPosition } from '@/app/shared/client/type/preview-api';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';

export default function saveMobilePreviewPosition(): void {
  sendToPreviewWindow<SaveMobilePreviewPosition>({
    type: PreviewApiEventType.SAVE_MOBILE_PREVIEW_POSITION,
  });
}
