import { Ref } from '@vue/composition-api';
import { ImageData } from '@/app/shared/client/type/image';

export interface GetImageUploadResultRequest {
  readonly imageId: string;
}

export type GetImageUploadResultResponse = ImageData

export function createGetImageUploadResultFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetImageUploadResultRequest) => Promise<GetImageUploadResultResponse> {
  return (request): Promise<GetImageUploadResultResponse> => fetch(`${apiUrl}/${siteId.value}/image/${request.imageId}`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetImageUploadResultResponse) => resp);
}
