import {
  GlobalDesign,
} from '@/app/shared/client/type/tile/global';
import { Color } from '@/app/shared/client/type/color';
import { Font } from '@/app/shared/client/type/font';
import {
  GlobalColorsString,
  GlobalFontsString,
  GlobalTextSizeString,
} from '@/app/shared/client/type/global-string';

export function extractGlobalColorHex(
  color: string | GlobalColorsString,
  siteDesignSettings?: GlobalDesign,
): string {
  switch (color) {
    case 'global.color.title':
      return siteDesignSettings?.color?.title?.hex ?? '';
    case 'global.color.body':
      return siteDesignSettings?.color?.body?.hex ?? '';
    case 'global.color.button':
      return siteDesignSettings?.color?.button?.hex ?? '';
    case 'global.color.link':
      return siteDesignSettings?.color?.link?.hex ?? '';
    case 'global.color.background':
      return siteDesignSettings?.color?.background?.hex ?? '';
    default:
      return color;
  }
}

export function extractGlobalFont(
  font?: GlobalFontsString,
  siteDesignSettings?: GlobalDesign,
): Font | undefined {
  switch (font) {
    case 'global.fontFamily.title':
      return siteDesignSettings?.fontFamily?.title;
    case 'global.fontFamily.body':
      return siteDesignSettings?.fontFamily?.body;
    default:
      return undefined;
  }
}

export function extractGlobalTextSize(
  textSize?: GlobalTextSizeString,
  siteDesignSettings?: GlobalDesign,
): number | undefined {
  switch (textSize) {
    case 'global.textSize.title':
      return siteDesignSettings?.textSize?.title;
    case 'global.textSize.subtitle':
      return siteDesignSettings?.textSize?.subtitle;
    case 'global.textSize.body':
      return siteDesignSettings?.textSize?.body;
    default:
      return undefined;
  }
}

export function getDefaultGlobalTextColor(): Color {
  return {
    raw: '#333',
    hex: '#333333FF',
    hsl: {
      h: 0,
      s: 0,
      l: 0.2,
      a: 1,
    },
    rgba: {
      r: 51,
      g: 51,
      b: 51,
      a: 1,
    },
    auto: false,
  };
}

export function getDefaultGlobalLinkColor(): Color {
  return {
    raw: '#1E7EC8',
    hex: '#1E7EC8FF',
    hsl: {
      h: 206.11764705882356,
      s: 0.7391304347826086,
      l: 0.45098039215686275,
      a: 1,
    },
    rgba: {
      r: 30,
      g: 126,
      b: 200,
      a: 1,
    },
    auto: false,
  };
}

export function getDefaultGlobalBackgroundColor(): Color {
  return {
    raw: '#FFF',
    hex: '#ffffffff',
    hsl: {
      h: 0,
      s: 0,
      l: 1,
      a: 1,
    },
    rgba: {
      r: 255,
      g: 255,
      b: 255,
      a: 1,
    },
    auto: false,
  };
}

export function extractGlobalColor(
  color?: GlobalColorsString,
  siteDesignSettings?: GlobalDesign,
): Color | undefined {
  const defaultTextColor = getDefaultGlobalTextColor();
  const defaultLinkColor = getDefaultGlobalLinkColor();
  const defaultBackgroundColor = getDefaultGlobalBackgroundColor();

  switch (color) {
    case 'global.color.title':
      return siteDesignSettings?.color?.title ?? defaultTextColor;
    case 'global.color.body':
      return siteDesignSettings?.color?.body ?? defaultTextColor;
    case 'global.color.button':
      return siteDesignSettings?.color?.button ?? defaultTextColor;
    case 'global.color.link':
      return siteDesignSettings?.color?.link ?? defaultLinkColor;
    case 'global.color.background':
      return siteDesignSettings?.color?.background ?? defaultBackgroundColor;
    default:
      return undefined;
  }
}

export default {};
