import { Ref } from '@vue/composition-api';

export interface AuthorizeRequest {
  readonly code: string;
}

export interface AuthorizeResponse {
  readonly accessToken: string;
  readonly tokenType: string;
  readonly expiresIn: number;
}

export function createAuthorizeFunction(
  apiUrl: string,
  siteId: Ref<number>,
): (request: AuthorizeRequest) => Promise<AuthorizeResponse> {
  return (request: AuthorizeRequest): Promise<AuthorizeResponse> => fetch(`${apiUrl}/oauth/token`, {
    method: 'POST',
    body: `grant_type=authorization_code&site_id=${siteId.value}&code=${request.code}`,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: AuthorizeResponse) => resp);
}
