
















import {
  computed,
  defineComponent, Ref, ref, watch,
} from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import { State } from '@/app/editor/client/store/state';
import { initPreviewWindow } from '@/app/editor/client/logic/preview/preview';
import sendPreviewTileList from '@/app/editor/client/logic/preview/send-preview-tile-list';
import { sendPreviewFocusedTile }
  from '@/app/editor/client/logic/preview/send-preview-focused-tile-id';
import sendPreviewContext from '@/app/editor/client/logic/preview/send-preview-context';
import sendPreviewSiteSettings from '@/app/editor/client/logic/preview/send-preview-site-settings';
import sendPreviewProfile from '@/app/editor/client/logic/preview/send-preview-profile';

function createOnLoadPreviewFrameFunction(
  state: Readonly<Ref<State>>,
  previewFrame: Ref<HTMLIFrameElement | undefined>,
) {
  return (): void => {
    const previewElement = previewFrame.value;
    if (previewElement === undefined) {
      return;
    }

    initPreviewWindow(previewElement);
    sendPreviewContext(state.value.context.bucketUrls, state.value.websiteTranslations);
    sendPreviewSiteSettings(state.value.siteSettings);
    sendPreviewProfile(state.value.profile);

    watch(
      () => state.value.tiles,
      sendPreviewTileList,
      { immediate: true },
    );
    watch(
      () => state.value.editedTileData,
      (tile) => sendPreviewFocusedTile(tile),
      { immediate: true },
    );
  };
}

const Preview = defineComponent({
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const siteId = computed(() => state.value.profile?.siteId);
    const previewFrame = ref<HTMLIFrameElement>();

    const onLoadPreviewFrame = createOnLoadPreviewFrameFunction(state, previewFrame);
    return {
      i18n,
      siteId,
      previewFrame,
      onLoadPreviewFrame,
    };
  },
});

export default Preview;
