import { OverlaySource } from '@/app/editor/client/type/overlay';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { SetOverlayMutation } from '@/app/editor/client/store/mutation/set-overlay';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { Ref } from '@vue/composition-api';

const clickHandlers: Map<OverlaySource, (event: Event) => void> = new Map();

function commitSetOverlayIfNeeded(
  setOverlay: boolean,
  overlaySource: OverlaySource,
  store: Store<State>,
): void {
  const newOverlaySource = (setOverlay) ? overlaySource : undefined;
  if (setOverlay || store.state.overlaySource === overlaySource) {
    store.commit<SetOverlayMutation>({
      type: MutationType.SET_OVERLAY,
      payload: {
        overlaySource: newOverlaySource,
      },
    });
  }
}

function addOverlayClickHandler(overlaySource: OverlaySource, clickHandler: EventListener): void {
  window.setTimeout(() => {
    document.body.addEventListener('click', clickHandler);
    clickHandlers.set(overlaySource, clickHandler);
  });
}

function removeOverlayClickHandler(overlaySource: OverlaySource): void {
  const clickHandler = clickHandlers.get(overlaySource);
  if (clickHandler !== undefined) {
    document.body.removeEventListener('click', clickHandler);
    clickHandlers.delete(overlaySource);
  }
}

function createOverlayClickHandler(
  overlayShowing: Ref<boolean>,
  overlaySource: OverlaySource,
  store: Store<State>,
): EventListener {
  return (): void => {
    overlayShowing.value = false;
    commitSetOverlayIfNeeded(overlayShowing.value, overlaySource, store);
    removeOverlayClickHandler(overlaySource);
  };
}

export function toggleOverlay(
  overlayShowing: Ref<boolean>,
  overlaySource: OverlaySource,
  store: Store<State>,
): void {
  commitSetOverlayIfNeeded(overlayShowing.value, overlaySource, store);

  if (overlayShowing.value) {
    const clickHandler = createOverlayClickHandler(overlayShowing, overlaySource, store);
    addOverlayClickHandler(overlaySource, clickHandler);
  } else {
    removeOverlayClickHandler(overlaySource);
  }
}

export default {};
