import { ActionContext } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetProfileMutation } from '@/app/editor/client/store/mutation/set-profile';
import { GetProfileResponse } from '@/app/editor/client/api/get-profile';

export interface GetProfileAction {
  type: ActionType.GET_PROFILE;
}

export default function getProfileAction(
  actionContext: ActionContext<State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    api.getProfile()
      .then((response: GetProfileResponse) => {
        const payload = { profile: response };
        actionContext.commit<SetProfileMutation>({ type: MutationType.SET_PROFILE, payload });
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
