



















































































import {
  computed,
  defineComponent,
  reactive,
  ref,
  Ref,
  UnwrapRef,
} from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import { ComboBoxEditorControl } from '@/app/editor/client/view/component/control';
import {
  fontKeyLabelList,
  fontWithFallback,
} from '@/app/shared/client/logic/font';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { GoogleFont, SystemFont } from '@/app/shared/client/type/font';
import {
  UpdateSiteSettingsFieldMutation,
} from '@/app/editor/client/store/mutation/update-site-settings-field';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import sendPreviewSiteSettings from '@/app/editor/client/logic/preview/send-preview-site-settings';
import MultilineTextEditorControl
  from '@/app/editor/client/view/component/control/MultilineTextEditorControl.vue';
import { Accordion, AccordionItem, AccordionItemHint } from '@/app/editor/client/view/component';
import NavigationLink from '@/app/editor/client/view/component/NavigationLink.vue';
import {
  useSaveSiteSettingsDebounced,
} from '@/app/shared/client/helper/save-tile-settings-debounce';
import { Profile } from '@/app/shared/client/type/profile';

interface StringAccordionSetting {
  readonly value: Ref<string | undefined>;
  readonly activity: Ref<boolean>;
  readonly changeActivity: (activity: boolean) => void;
}

function useAccordionSettingActivityToggleHandler(
  activity: Ref<boolean>,
): () => void {
  function toggleActivity(): void {
    activity.value = !activity.value;
  }

  return toggleActivity;
}

function useStringAccordionField(
  initialValue: Ref<string | undefined>,
): UnwrapRef<StringAccordionSetting> {
  const value = initialValue;
  const advancedHash = window.location.hash.endsWith('?advanced');
  const activity = ref(advancedHash);
  const changeActivity = useAccordionSettingActivityToggleHandler(activity);
  return reactive({
    value,
    activity,
    changeActivity,
  });
}

function updateSiteDesignSettings(store: Store<State>, field: string, value: unknown): void {
  const debouncedDispatch = useSaveSiteSettingsDebounced(store);
  store.commit<UpdateSiteSettingsFieldMutation>({
    type: MutationType.UPDATE_SITE_SETTINGS_FIELD,
    payload: { section: 'design', field, value },
  });
  sendPreviewSiteSettings(store.state.siteSettings);
  debouncedDispatch();
}

function useHeadingFontChangeHandler(
  $store: Store<State>,
  siteDesignSettings: Readonly<Ref<GlobalDesign | undefined>>,
): (value?: SystemFont | GoogleFont) => void {
  function onHeadingFontChange(value?: SystemFont | GoogleFont): void {
    const newFontFamily = {
      ...siteDesignSettings.value?.fontFamily,
      title: value,
    };
    updateSiteDesignSettings($store, 'fontFamily', newFontFamily);
  }
  return onHeadingFontChange;
}

function useBodyFontChangeHandler(
  $store: Store<State>,
  siteDesignSettings: Readonly<Ref<GlobalDesign | undefined>>,
): (value?: SystemFont | GoogleFont) => void {
  function onBodyFontChange(value?: SystemFont | GoogleFont): void {
    const newFontFamily = {
      ...siteDesignSettings.value?.fontFamily,
      body: value,
    };
    updateSiteDesignSettings($store, 'fontFamily', newFontFamily);
  }
  return onBodyFontChange;
}

function useCustomCssChangeHandler(
  $store: Store<State>,
): (value: string | undefined) => void {
  function onCustomCssChange(value?: string): void {
    updateSiteDesignSettings($store, 'customCss', value);
  }
  return onCustomCssChange;
}

const StyleSettingsEditor = defineComponent({
  components: {
    NavigationLink,
    Accordion,
    AccordionItem,
    AccordionItemHint,
    MultilineTextEditorControl,
    ComboBoxEditorControl,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const siteSettings = computed(() => state.value.siteSettings);
    const siteDesignSettings = computed(() => siteSettings.value?.design);

    const fontChoices = computed(() => fontKeyLabelList());
    const headingFont = computed({
      get: () => fontWithFallback(siteDesignSettings.value?.fontFamily?.title),
      set: useHeadingFontChangeHandler(root.$store, siteDesignSettings),
    });
    const bodyFont = computed({
      get: () => fontWithFallback(siteDesignSettings.value?.fontFamily?.body),
      set: useBodyFontChangeHandler(root.$store, siteDesignSettings),
    });

    const customCss = useStringAccordionField(
      computed({
        get: () => siteDesignSettings.value?.customCss,
        set: useCustomCssChangeHandler(root.$store),
      }),
    );

    const showEcwidLinks = computed(() => {
      const profile = root.$store.state.profile as Profile;
      return profile.hideEcwidLinks === false;
    });

    const navigateToHelpCenter = (): void => {
      const helpCenterUrl = 'https://support.ecwid.com/hc/en-us/articles/'
        + '360000034499-Ecwid-CSS-Cookbook';
      window.open(helpCenterUrl, '_blank');
    };

    return {
      i18n,
      fontChoices,
      headingFont,
      bodyFont,
      customCss,
      showEcwidLinks,
      navigateToHelpCenter,
    };
  },
});

export default StyleSettingsEditor;
