import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import {
  commitSetSyncTilesStatus,
  commitTileList,
} from '@/app/editor/client/store/action/helpers';
import { forceSaveTileSettingsDebounced } from '@/app/shared/client/helper/save-tile-settings-debounce';
import { PublishChangesResponse } from '@/app/editor/client/api/publish-changes';
import { reloadPreview } from '@/app/editor/client/logic/preview/preview';
import {
  SetHasCustomCssChangesMutation,
} from '@/app/editor/client/store/mutation/set-has-custom-css-changes';
import { MutationType } from '@/app/editor/client/store/mutation/type';

export interface PublishAllChangesAction extends Payload {
  type: ActionType.PUBLISH_ALL_CHANGES;
}

export default async function publishAllChangesAction(
  { commit, state }: ActionContext<State, State>,
): Promise<void> {
  await forceSaveTileSettingsDebounced();
  commitSetSyncTilesStatus(commit, true);
  return new Promise((resolve, reject) => {
    api.publishChanges({ storeUrl: window.location.origin })
      .then((response: PublishChangesResponse) => {
        commitSetSyncTilesStatus(commit, false);
        const newTileList = response.tiles;
        // после успешного publishAllChanges draft == published
        commitTileList(newTileList, newTileList, commit);

        if (state.hasCustomCssChanges) {
          reloadPreview();
          commit<SetHasCustomCssChangesMutation>({
            type: MutationType.SET_HAS_CUSTOM_CSS_CHANGES,
            payload: { hasCustomCssChanges: false },
          });
        }

        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
