import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { InitContextMutation } from '@/app/editor/client/store/mutation/init-context';
import { ActionType } from '@/app/editor/client/store/action/type';
import { GetImageBucketsResponse } from '@/app/editor/client/api/get-image-buckets';

export interface GetBucketUrlsAction extends Payload {
  type: ActionType.GET_BUCKET_URLS;
}

export default function getBucketUrlsAction(
  actionContext: ActionContext <State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    api.getImageBuckets()
      .then((response: GetImageBucketsResponse) => {
        const payload = {
          context: {
            ...actionContext.state.context,
            bucketUrls: response.urls,
          },
        };
        actionContext.commit<InitContextMutation>({ type: MutationType.INIT_CONTEXT, payload });
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
