import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import Vue from 'vue';
import { sendPreviewFocusedTile }
  from '@/app/editor/client/logic/preview/send-preview-focused-tile-id';
import { MutationType } from './type';

export interface UpdateEditedTileFieldPayload {
  readonly section: 'content' | 'design';
  readonly field: string;
  readonly value: string;
}

export interface UpdateEditedTileFieldMutation extends MutationPayload {
  type: MutationType.UPDATE_EDITED_TILE_FIELD;
  payload: UpdateEditedTileFieldPayload;
}

export default function updateEditedTileFieldMutation(
  oldState: State,
  payload: UpdateEditedTileFieldPayload,
): State {
  const newState = oldState;
  const editedTileData = newState.editedTileData;
  if (editedTileData === undefined) {
    return oldState;
  }

  editedTileData.hasChanges = true;
  if (payload.section === 'content') {
    const newEditedTileContent = (editedTileData.content ?? {}) as Record<string, unknown>;
    newEditedTileContent[payload.field] = payload.value;
    editedTileData.content = newEditedTileContent;
  }
  if (payload.section === 'design') {
    const newEditedTileDesign = (editedTileData?.design ?? {}) as Record<string, unknown>;
    newEditedTileDesign[payload.field] = payload.value;
    editedTileData.design = newEditedTileDesign;
  }

  sendPreviewFocusedTile(editedTileData);
  const tileIndexForUpdate = oldState.tiles
    .findIndex((tile) => tile.id === editedTileData.id);
  if (tileIndexForUpdate !== -1) {
    Vue.set(newState.tiles, tileIndexForUpdate, editedTileData);
  }

  newState.editedTileData = {
    ...editedTileData,
  };

  return newState;
}
