import { PreviewApiEventPayload } from '@/app/shared/client/type/preview-api';

let previewElement: HTMLIFrameElement | undefined;

export function initPreviewWindow(iframe: HTMLIFrameElement): void {
  previewElement = iframe;
}

export function sendToPreviewWindow<P extends PreviewApiEventPayload>(payload: P): void {
  const previewWindow = previewElement?.contentWindow;
  if (previewWindow == null) {
    return;
  }
  previewWindow.postMessage(payload, window.parent.origin);
}

export function isPreviewFrameVisible(): boolean {
  const previewWindow = previewElement?.contentWindow;
  const previewParent = previewElement?.parentElement;
  if (previewElement !== undefined && previewWindow != null && previewParent != null) {
    return window.getComputedStyle(previewParent).display !== 'none';
  }
  return false;
}

export function reloadPreview(): void {
  const previewWindow = previewElement?.contentWindow;
  if (previewElement !== undefined && previewWindow != null) {
    previewWindow.location.reload();
  }
}

export default {};
