import { Commit } from 'vuex';
import { SetTileListMutation } from '@/app/editor/client/store/mutation/set-tile-list';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetSiteSettingsMutation } from '@/app/editor/client/store/mutation/set-site-settings';
import { SetSyncTilesStatusMutation } from '@/app/editor/client/store/mutation/set-sync-tiles-status';
import { TileData } from '@/app/shared/client/type/tile';
import { GlobalData } from '@/app/shared/client/type/tile/global';

function getTileList(tileList: TileData[]): TileData[] {
  return tileList.filter((tile) => tile.type !== 'GLOBAL');
}

function getSiteSettings(tileList: TileData[]): GlobalData | undefined {
  return tileList.find((tile) => tile.type === 'GLOBAL') as GlobalData | undefined;
}

export function commitTileList(
  tileList: TileData[],
  publishedTileList: TileData[],
  commit: Commit,
): void {
  commit<SetTileListMutation>({
    type: MutationType.SET_TILE_LIST,
    payload: {
      tileList: getTileList(tileList),
      publishedTileList: getTileList(publishedTileList),
    },
  });

  const siteSettings = getSiteSettings(tileList);
  const publishedSiteSettings = getSiteSettings(publishedTileList);
  commit<SetSiteSettingsMutation>({
    type: MutationType.SET_SITE_SETTINGS,
    payload: {
      siteSettings,
      publishedSiteSettings,
    },
  });
}

export function commitSetSyncTilesStatus(commit: Commit, syncTilesInProgress: boolean): void {
  commit<SetSyncTilesStatusMutation>({
    type: MutationType.SET_SYNC_TILES_STATUS,
    payload: { syncTilesInProgress },
  });
}
