



























import { defineComponent } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';

function useActivityChangeHandler(
  emit: (event: string) => void,
): () => void {
  function changeActivity(): void {
    emit('change-activity');
  }

  return changeActivity;
}

const AccordionItem = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    activity: {
      required: true,
      type: Boolean,
    },
    muted: {
      required: false,
      default: true,
      type: Boolean,
    },
  },
  setup(_, { emit }) {
    const changeActivity = useActivityChangeHandler(emit);

    return {
      changeActivity,
    };
  },
});

export default AccordionItem;
