



















import { computed, defineComponent } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import useI18N from '@/app/shared/client/logic/i18n';
import { ignoredPropertyNameForDiscard }
  from '@/app/editor/client/store/mutation/discard-tile-design';
import { DiscardTileDesignAction } from '@/app/editor/client/store/action/discard-tile-design';

function useDiscardClickHandler(
  tileId: string,
  store: Store<State>,
): () => void {
  function onDiscardClick(): void {
    store.dispatch<DiscardTileDesignAction>({
      type: ActionType.DISCARD_TILE_DESIGN,
      payload: { tileId },
    });
  }

  return onDiscardClick;
}

function useDefaultDesignTemplate(state: State): () => boolean {
  return (): boolean => {
    const tile = state.editedTileData;
    if (tile === undefined) {
      return false;
    }

    const tileDesign = tile.design;
    if (tileDesign === undefined) {
      return false;
    }

    return Object.keys(tileDesign)
      .every((key) => key === ignoredPropertyNameForDiscard);
  };
}

const TileStyleDiscardEditor = defineComponent({
  components: {
    RefreshIcon: Icon.RefreshIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const { id } = root.$route.params;
    const onDiscardClick = useDiscardClickHandler(id, root.$store);
    const isDefaultDesignTemplate = computed(useDefaultDesignTemplate(root.$store.state));

    return {
      i18n,
      onDiscardClick,
      isDefaultDesignTemplate,
    };
  },
});

export default TileStyleDiscardEditor;
