import { Ref } from '@vue/composition-api';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';

const supportedLinkProtocols = [
  'http://',
  'https://',
  'mailto:',
  'tel:',
  'whatsapp:',
  'tg://',
  'sms:',
  'skype:',
];

export function useLinkCheckAndUpdate(
  link: Ref<Readonly<string | undefined>>,
  updateLink: (value: string) => void,
): () => void {
  function checkAndUpdateLink(): void {
    const linkValue = link.value;
    if (isNullOrEmpty(linkValue)) {
      return;
    }
    const linkWithSupportedProtocol = supportedLinkProtocols.some(
      (protocol: string) => linkValue.startsWith(protocol),
    );
    if (!linkWithSupportedProtocol) {
      updateLink(`https://${linkValue}`);
    }
  }

  return checkAndUpdateLink;
}

export default {};
