














import { defineComponent } from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import Icon from '@/app/editor/client/view/icon';

const NavigationLink = defineComponent({
  components: {
    ExternalSmallIcon: Icon.ExternalSmallIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
  },
  setup(_, { root, emit }) {
    const i18n = useI18N(root.$store);

    const onClick = (): void => emit('action');

    return {
      i18n,
      onClick,
    };
  },
});

export default NavigationLink;
