import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetTranslationsMutation } from '@/app/editor/client/store/mutation/set-translations';
import { GetTranslationsResponse } from '@/app/editor/client/api/get-translations';

interface GetTranslationsActionPayload {
  locale?: string;
}

export interface GetTranslationsAction extends Payload {
  type: ActionType.GET_TRANSLATIONS;
  payload: GetTranslationsActionPayload;
}

export default function getTranslationsAction(
  actionContext: ActionContext<State, State>,
  data: GetTranslationsAction,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const { locale } = data.payload;
    if (locale === undefined) {
      reject();
      return;
    }

    api.getTranslations({ locale })
      .then((response: GetTranslationsResponse) => {
        actionContext.commit<SetTranslationsMutation>({
          type: MutationType.SET_TRANSLATIONS,
          payload: {
            editorTranslations: response.editorTranslations,
            websiteTranslations: response.websiteTranslations,
          },
        });
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
