import {
  PreviewApiEventType,
  UpdatePreviewContextPayload,
} from '@/app/shared/client/type/preview-api';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';
import { Bucket } from '@/app/shared/client/type/bucket';

export default function sendPreviewContext(
  bucketUrls: Record<Bucket, string | undefined>,
  translations: Record<string, string>,
): void {
  sendToPreviewWindow<UpdatePreviewContextPayload>({
    type: PreviewApiEventType.UPDATE_PREVIEW_CONTEXT,
    payload: {
      bucketUrls,
      translations,
    },
  });
}
