import {
  ContentEditorType, DesignEditorType,
  EditorButton, EditorButtonAdvice,
  EditorButtonType,
} from '@/app/editor/client/type/tile-config';

export function contentEditorName(type: ContentEditorType | undefined): string | undefined {
  switch (type) {
    case ContentEditorType.TEXT_EDITOR:
      return 'TextContentEditor';
    case ContentEditorType.MULTILINE_TEXT_EDITOR:
      return 'MultilineTextContentEditor';
    case ContentEditorType.ACTION_LINK_EDITOR:
      return 'ActionLinkContentEditor';
    case ContentEditorType.IMAGE_EDITOR:
      return 'ImageContentEditor';
    case ContentEditorType.CONTACT_INFO_EDITOR:
    case ContentEditorType.CONTACT_INFO_WITH_SUBHEADING_EDITOR:
      return 'ContactInfoContentEditor';
    case ContentEditorType.LOGO_EDITOR:
      return 'LogoContentEditor';
    case ContentEditorType.HOURS_EDITOR:
      return 'HoursContentEditor';
    case ContentEditorType.SOCIAL_ICON_LIST_EDITOR:
      return 'SocialIconListContentEditor';
    case ContentEditorType.LOCATION_EDITOR:
      return 'LocationContentEditor';
    case ContentEditorType.MENU_EDITOR:
      return 'MenuContentEditor';
    default:
      throw new Error('Unknown content editor type');
  }
}

export function designEditorName(type: DesignEditorType | undefined): string | undefined {
  switch (type) {
    case DesignEditorType.GENERAL_EDITOR:
      return 'GeneralDesignEditor';
    case DesignEditorType.TEXT_EDITOR:
      return 'TextDesignEditor';
    case DesignEditorType.ACTION_LINK_EDITOR:
      return 'ActionLinkDesignEditor';
    case DesignEditorType.IMAGE_EDITOR:
      return 'ImageDesignEditor';
    case DesignEditorType.CUSTOM_SIDEBAR_EDITOR:
      return 'CustomSidebarDesignEditor';
    case DesignEditorType.SCROLLABLE_IMAGE_EDITOR:
      return 'ScrollableImageDesignEditor';
    case DesignEditorType.ARROW_EDITOR:
      return 'ArrowDesignEditor';
    case DesignEditorType.ICON_EDITOR:
      return 'IconDesignEditor';
    case DesignEditorType.LOGO_EDITOR:
      return 'LogoDesignEditor';
    case DesignEditorType.VISIBILITY_EDITOR:
      return 'VisibilityDesignEditor';
    case DesignEditorType.SOCIAL_ICON_LIST_EDITOR:
      return 'SocialIconListDesignEditor';
    default:
      throw new Error('Unknown design editor type');
  }
}

function editorButtonName(type: EditorButtonType | undefined): string | undefined {
  switch (type) {
    case EditorButtonType.CONTROL_PANEL_LINK_BUTTON:
      return 'ControlPanelLinkButton';
    default:
      throw new Error('Unknown editor button type');
  }
}

interface EditorButtonViewData {
  type?: EditorButtonType;
  name?: string;
  label?: string;
  descriptionLabel?: string;
  actionLabel?: string;
  url?: string;
  advices?: EditorButtonAdvice[];
}
export function useEditorButtonCreator(): (editor: EditorButton) => EditorButtonViewData {
  function createEditorButton(button: EditorButton): EditorButtonViewData {
    const {
      type,
      label,
      descriptionLabel,
      actionLabel,
      url,
      advices,
    } = button;
    return {
      type,
      name: editorButtonName(type),
      label,
      descriptionLabel,
      actionLabel,
      url,
      advices,
    };
  }

  return createEditorButton;
}
