import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { RemoveTileMutation } from '@/app/editor/client/store/mutation/remove-tile';
import { RemoveTileResponse } from '@/app/editor/client/api/remove-tile';

interface RemoveTileActionPayload {
  tileId: string;
}

export interface RemoveTileAction extends ActionPayload {
  type: ActionType.REMOVE_TILE;
  payload: RemoveTileActionPayload;
}

export default function removeTileAction(
  actionContext: ActionContext<State, State>,
  data: RemoveTileAction,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const { tileId } = data.payload;
    api.removeTile({ id: tileId })
      .then((response: RemoveTileResponse) => {
        const tile = response;
        const payload = { tileId: tile.id };
        actionContext.commit<RemoveTileMutation>({ type: MutationType.REMOVE_TILE, payload });

        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
