import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { computed, Ref } from '@vue/composition-api';
import {
  MenuContentEditorActionPayload,
  MenuContentEditorActionType,
} from '@/app/editor/client/type/editor-action/menu-content-editor';
import { VueRouter } from 'vue-router/types/router';
import {
  useMenuContentUpdate,
  useMenuItemAdd,
  useMenuNavigate,
} from '@/app/editor/client/logic/editor/menu';
import { TileData } from '@/app/shared/client/type/tile';
import { TileContentWithMenu } from '@/app/shared/client/type/tile/partial/content';

export default function useMenuContentEditorActionHandler(
  store: Store<State>,
  router: VueRouter,
  i18n: (label: string) => string,
  tile: Readonly<Ref<TileData>>,
): (payload: MenuContentEditorActionPayload) => void {
  function menuContentEditorAction(payload: MenuContentEditorActionPayload): void {
    const { type } = payload;

    switch (type) {
      case MenuContentEditorActionType.ADD_MENU_ITEM: {
        const tileContent = tile.value.content as TileContentWithMenu;
        const menuContent = computed(() => tileContent?.menu);
        const updateMenuContent = useMenuContentUpdate(store);
        const addMenuItem = useMenuItemAdd(
          router,
          i18n,
          tile,
          menuContent,
          updateMenuContent,
        );
        addMenuItem();
        return;
      }

      case MenuContentEditorActionType.NAVIGATE_TO_EDITOR: {
        const navigateToMenu = useMenuNavigate(router, tile);
        navigateToMenu();
        return;
      }

      default: throw new Error('Unknown action for image content editor');
    }
  }

  return menuContentEditorAction;
}
