import { TileType } from '@/app/shared/client/type/tile-type';

function textLayoutSVGName(name: string | undefined): string {
  switch (name) {
    case 'ONE_COLUMN':
      return 'TextTileOneColumnLayoutIcon';
    case 'TITLE_LEFT':
      return 'TextTileTitleLeftLayoutIcon';
    case 'SUBTITLE_RIGHT':
      return 'TextTileSubtitleRightLayoutIcon';
    case 'TWO_COLUMNS':
      return 'TextTileTwoColumnsLayoutIcon';
    case 'CENTER':
      return 'TextTileCenterLayoutIcon';
    case 'DESCRIPTION_RIGHT':
      return 'TextTileDescriptionRightLayoutIcon';
    default:
      return '';
  }
}

