export function getTileLayoutSvgName(type: TileType, name: string | undefined): string {
  switch (type) {
    case 'TEXT':
      return textLayoutSVGName(name);
    case 'CALL_TO_ACTION':
      return ctaLayoutSVGName(name);
    case 'COVER':
      return coverLayoutSVGName(name);
    case 'IMAGE_TEXT':
      return imageTextLayoutSVGName(name);
    case 'LOCATION':
      return locationLayoutSVGName(name);
    default:
      return '';
  }
}

export default {};
