import { PreviewApiEventType, SetFocusedTileIdPayload } from '@/app/shared/client/type/preview-api';
import {
  isPreviewFrameVisible,
  sendToPreviewWindow,
} from '@/app/editor/client/logic/preview/preview';
import { TileData } from '@/app/shared/client/type/tile';
import { TileRole } from '@/app/shared/client/type/tile-role';

export function sendPreviewFocusedTileId(focusedTileId: string | undefined): void {
  if (isPreviewFrameVisible()) {
    sendToPreviewWindow<SetFocusedTileIdPayload>({
      type: PreviewApiEventType.SET_FOCUSED_TILE_ID,
      payload: { focusedTileId },
    });
  }
}

function tileNeedFocus(role: TileRole): boolean {
  return role === 'BLOCK';
}

export function sendPreviewFocusedTile(tile: TileData | undefined): void {
  if (tile !== undefined && tileNeedFocus(tile.role)) {
    sendPreviewFocusedTileId(tile.id);
  }
}
