
































import {
  computed, defineComponent, PropType,
} from '@vue/composition-api';
import { LayoutConfig } from '@/app/editor/client/type/tile-config';
import TextTileLayoutIcons from '@/app/editor/client/view/icon/layout/text';
import CTATileLayoutIcons from '@/app/editor/client/view/icon/layout/cta';
import CoverTileLayoutIcons from '@/app/editor/client/view/icon/layout/cover';
import ImageTextTileLayoutIcons from '@/app/editor/client/view/icon/layout/image-text';
import LocationTileLayoutIcons from '@/app/editor/client/view/icon/layout/location';
import useI18N from '@/app/shared/client/logic/i18n';
import { State } from '@/app/editor/client/store/state';
import { TileData } from '@/app/shared/client/type/tile';
import { getTileLayoutSvgName } from '@/app/editor/client/logic/tile-config';

interface LayoutEditor {
  name?: string;
  svgName?: string;
}

function layoutActivity(selectedLayout: string | undefined): (value: string) => boolean {
  function isActive(currentLayout: string): boolean {
    return selectedLayout === currentLayout;
  }

  return isActive;
}

function useChangeLayoutHandler(
  emit: (event: string, ...args: unknown[]) => void,
): (value: string) => void {
  function onChangeLayout(value: string): void {
    emit('changeLayout', value);
  }

  return onChangeLayout;
}

const LayoutDesignEditor = defineComponent({
  components: {
    TextTileLayoutIcons,
    CTATileLayoutIcons,
    CoverTileLayoutIcons,
    ImageTextTileLayoutIcons,
    LocationTileLayoutIcons,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: String,
    },
    layoutConfigList: {
      required: true,
      type: Array as PropType<LayoutConfig[]>,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData as TileData);

    const layoutEditorList = computed(() => {
      const { layoutConfigList } = props;
      return layoutConfigList
        .filter((layoutConfig) => !layoutConfig.deprecated)
        .map((layoutConfig: LayoutConfig): LayoutEditor => ({
          name: layoutConfig.name,
          svgName: getTileLayoutSvgName(editedTile.value.type, layoutConfig.name),
        }));
    });

    const isActiveLayout = computed(() => layoutActivity(props.value));
    const onChangeLayout = useChangeLayoutHandler(emit);

    return {
      i18n,
      layoutEditorList,
      isActiveLayout,
      onChangeLayout,
    };
  },
});

export default LayoutDesignEditor;
