import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { GetImageUploadResultResponse } from '@/app/editor/client/api/get-image-upload-result';
import { ImageData } from '@/app/shared/client/type/image';

interface GetImageUploadResultActionPayload {
  readonly imageId: string;
}

export interface GetImageUploadResultAction extends ActionPayload {
  type: ActionType.GET_IMAGE_UPLOAD_RESULT;
  payload: GetImageUploadResultActionPayload;
}

export default function getImageUploadResultAction(
  _: ActionContext <State, State>,
  data: GetImageUploadResultAction,
): Promise<ImageData | undefined> {
  return new Promise((resolve, reject) => {
    const { imageId } = data.payload;
    api.getImageUploadResult({ imageId })
      .then((response: GetImageUploadResultResponse) => {
        resolve(response);
      }).catch((error: Error) => reject(error.message));
  });
}
