import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { GetTileDataResponse } from '@/app/editor/client/api/get-tile-data';

interface GetTileDataActionPayload {
  readonly id: string;
}

export interface GetTileDataAction extends ActionPayload {
  type: ActionType.GET_TILE_DATA;
  payload: GetTileDataActionPayload;
}

export default function GetTileDataAction(
  actionContext: ActionContext <State, State>,
  data: GetTileDataAction,
): Promise<void> {
  return new Promise((resolve, reject) => {
    const { id } = data.payload;
    api.getTileData({ id })
      .then((response: GetTileDataResponse) => {
        const tile = response;
        actionContext.commit(MutationType.SET_TILE_DATA, {
          type: MutationType.SET_TILE_DATA,
          tileData: tile,
        });
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
