import { ActionContext } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetTileShowcaseCategoriesMutation } from '@/app/editor/client/store/mutation/set-tile-showcase-categories';
import { GetTileShowcaseResponse } from '@/app/editor/client/api/get-tile-showcase';

export interface GetTileShowcaseDataAction {
  type: ActionType.GET_TILE_SHOWCASE_DATA;
}

export default function getTileShowcaseDataAction(
  actionContext: ActionContext<State, State>,
): Promise<void> {
  return new Promise((resolve, reject) => {
    api.getTileShowcase()
      .then((response: GetTileShowcaseResponse) => {
        const payload = {
          categories: response.categories,
        };
        actionContext.commit<SetTileShowcaseCategoriesMutation>({
          type: MutationType.SET_TILE_SHOWCASE_CATEGORIES,
          payload,
        });
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
