import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface GetTileDataRequest {
  readonly id: string;
}

export type GetTileDataResponse = TileData;

export function createGetTileDataFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetTileDataRequest) => Promise<GetTileDataResponse> {
  return (request): Promise<GetTileDataResponse> => fetch(`${apiUrl}/${siteId.value}/tile/${request.id}`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetTileDataResponse) => resp);
}
