import { MutationTree } from 'vuex';
import initContextMutation from '@/app/editor/client/store/mutation/init-context';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import setTileListMutation from '@/app/editor/client/store/mutation/set-tile-list';
import addTileMutation from '@/app/editor/client/store/mutation/add-tile';
import removeTileMutation from '@/app/editor/client/store/mutation/remove-tile';
import setTileDataMutation from '@/app/editor/client/store/mutation/set-tile-data';
import updateEditedTileFieldMutation from '@/app/editor/client/store/mutation/update-edited-tile-field';
import setTileConfigMutation from '@/app/editor/client/store/mutation/set-tile-config';
import discardTileDesignMutation from '@/app/editor/client/store/mutation/discard-tile-design';
import setSiteSettingsMutation from '@/app/editor/client/store/mutation/set-site-settings';
import updateSiteSettingsFieldMutation
  from '@/app/editor/client/store/mutation/update-site-settings-field';
import setProfileMutation from '@/app/editor/client/store/mutation/set-profile';
import setTranslationsMutation from '@/app/editor/client/store/mutation/set-translations';
import setPopupMutation from '@/app/editor/client/store/mutation/set-popup';
import setTileShowcaseCategoriesMutation
  from '@/app/editor/client/store/mutation/set-tile-showcase-categories';
import addImageInUploadingStateMutation
  from '@/app/editor/client/store/mutation/add-image-in-uploading-state';
import removeImageInUploadingStateMutation
  from '@/app/editor/client/store/mutation/remove-image-in-uploading-state';
import changeTileVisibilityMutation
  from '@/app/editor/client/store/mutation/change-tile-visibility';
import setOverlayMutation from '@/app/editor/client/store/mutation/set-overlay';
import toggleMobilePreviewMutation from '@/app/editor/client/store/mutation/toggle-mobile-preview';
import discardChangesMutation from '@/app/editor/client/store/mutation/discard-changes';
import setSyncTilesStatusMutation from '@/app/editor/client/store/mutation/set-sync-tiles-status';
import setHasCistomCssChangesMutation from '@/app/editor/client/store/mutation/set-has-custom-css-changes';
import { State } from './state';

function mutationsFactory(): MutationTree<State> {
  return {
    [MutationType.INIT_CONTEXT]: initContextMutation,
    [MutationType.SET_PROFILE]: setProfileMutation,
    [MutationType.SET_TRANSLATIONS]: setTranslationsMutation,
    [MutationType.SET_TILE_CONFIG]: setTileConfigMutation,
    [MutationType.SET_TILE_LIST]: setTileListMutation,
    [MutationType.ADD_TILE]: addTileMutation,
    [MutationType.REMOVE_TILE]: removeTileMutation,
    [MutationType.SET_TILE_DATA]: setTileDataMutation,
    [MutationType.UPDATE_EDITED_TILE_FIELD]: updateEditedTileFieldMutation,
    [MutationType.DISCARD_TILE_DESIGN]: discardTileDesignMutation,
    [MutationType.SET_SITE_SETTINGS]: setSiteSettingsMutation,
    [MutationType.UPDATE_SITE_SETTINGS_FIELD]: updateSiteSettingsFieldMutation,
    [MutationType.SET_POPUP]: setPopupMutation,
    [MutationType.SET_TILE_SHOWCASE_CATEGORIES]: setTileShowcaseCategoriesMutation,
    [MutationType.ADD_IMAGE_IN_UPLOADING_STATE]: addImageInUploadingStateMutation,
    [MutationType.REMOVE_IMAGE_IN_UPLOADING_STATE]: removeImageInUploadingStateMutation,
    [MutationType.CHANGE_TILE_VISIBILITY]: changeTileVisibilityMutation,
    [MutationType.SET_OVERLAY]: setOverlayMutation,
    [MutationType.TOGGLE_MOBILE_PREVIEW]: toggleMobilePreviewMutation,
    [MutationType.DISCARD_CHANGES]: discardChangesMutation,
    [MutationType.SET_SYNC_TILES_STATUS]: setSyncTilesStatusMutation,
    [MutationType.SET_HAS_CUSTOM_CSS_CHANGES]: setHasCistomCssChangesMutation,
  };
}

export default function createMutation(): MutationTree<State> {
  const mutations = mutationsFactory();
  return { ...mutations };
}
