import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { PopupType } from '@/app/editor/client/type/popup';

interface SetPopupMutationPayload {
  popup: PopupType | undefined;
}

export interface SetPopupMutation extends MutationPayload {
  type: MutationType.SET_POPUP;
  payload: SetPopupMutationPayload;
}

export default function setPopupMutation(oldState: State, data: SetPopupMutation): State {
  const newState = oldState;
  newState.currentPopup = data.payload.popup;
  return newState;
}
