import {
  PreviewApiEventType,
  UpdatePreviewUploadingImageList,
} from '@/app/shared/client/type/preview-api';
import { ImageInUploadingState } from '@/app/shared/client/type/image-in-uploading-state';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';

export default function sendPreviewUploadingImageList(
  imageListInUploadingState: ImageInUploadingState[],
): void {
  sendToPreviewWindow<UpdatePreviewUploadingImageList>({
    type: PreviewApiEventType.UPDATE_PREVIEW_UPLOADING_IMAGE_LIST,
    payload: { imageListInUploadingState },
  });
}
