























































































import {
  computed,
  defineComponent,
  Ref,
  ref,
  reactive,
  UnwrapRef,
} from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  Accordion,
  AccordionItem,
} from '@/app/editor/client/view/component';
import useImageContentEditorActionHandler
  from '@/app/editor/client/logic/editor-action/image-content-editor';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { UpdateSiteSettingsFieldMutation }
  from '@/app/editor/client/store/mutation/update-site-settings-field';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ImageData } from '@/app/shared/client/type/image';
import {
  ImageContentEditorActionPayload, ImageContentEditorActionType,
} from '@/app/editor/client/type/editor-action/image-content-editor';
import {
  TextEditorControl,
  MultilineTextEditorControl,
  ImageEditorControl,
} from '@/app/editor/client/view/component/control';
import {
  COMMON_UPLOAD_ERROR_LABELS,
  UploadError,
} from '@/app/editor/client/type/image-uploader';
import { useSaveSiteSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import sendPreviewSiteSettings from '@/app/editor/client/logic/preview/send-preview-site-settings';
import { TileData } from '@/app/shared/client/type/tile';
import { GlobalData, SeoSettings } from '@/app/shared/client/type/tile/global';
import NavigationLink from '@/app/editor/client/view/component/NavigationLink.vue';
import AccordionItemHint from '@/app/editor/client/view/component/AccordionItemHint.vue';
import { ConvertOperationType } from '@/app/editor/client/type/image-convert-operation';

const SOCIAL_SHARE_IMAGE_SCALE_DIMENSIONS = [
  { // превью в контроле
    height: 200,
    width: 200,
  }, { // максимальный размер
    height: 1200,
    width: 1200,
  },
];

interface StringSeoOption {
  readonly value: Ref;
  readonly activity: Ref<boolean>;
  readonly changeActivity: (activity: boolean) => void;
}

function updateSiteSeoSettings($store: Store<State>, value: unknown): void {
  const debouncedDispatch = useSaveSiteSettingsDebounced($store);
  const field = 'seo';
  $store.commit<UpdateSiteSettingsFieldMutation>({
    type: MutationType.UPDATE_SITE_SETTINGS_FIELD,
    payload: { section: 'content', field, value },
  });
  sendPreviewSiteSettings($store.state.siteSettings);
  debouncedDispatch();
}

function useSeoTitleChangeHandler(
  $store: Store<State>,
  seoSettings: Readonly<Ref<Readonly<SeoSettings | undefined>>>,
): (value: string | undefined) => void {
  function onSeoTitleChange(value?: string): void {
    const newSeoSettings = {
      ...seoSettings.value,
      title: value,
    };
    updateSiteSeoSettings($store, newSeoSettings);
  }
  return onSeoTitleChange;
}

function useSeoDescriptionChangeHandler(
  $store: Store<State>,
  seoSettings: Readonly<Ref<Readonly<SeoSettings | undefined>>>,
): (value: string | undefined) => void {
  function onSeoTitleChange(value?: string): void {
    const newSeoSettings = {
      ...seoSettings.value,
      description: value,
    };
    updateSiteSeoSettings($store, newSeoSettings);
  }
  return onSeoTitleChange;
}

function useSocialShareImageChangeHandler(
  store: Store<State>,
  seoSettings: Readonly<Ref<Readonly<SeoSettings | undefined>>>,
): (value: ImageData | undefined) => void {
  function changeSocialShareImage(value: ImageData | undefined): void {
    const newSeoSettings = {
      ...seoSettings.value,
      socialShareImage: value,
    };
    updateSiteSeoSettings(store, newSeoSettings);
  }

  return changeSocialShareImage;
}

function useSocialShareImageContentEditorActionHandler(
  store: Store<State>,
  siteSettings: Ref<Readonly<GlobalData | undefined>>,
  imageId: string,
  updateImage: (newImageData: ImageData | undefined) => void,
): (payload: ImageContentEditorActionPayload) => void {
  function socialShareImageEditorAction(payload: ImageContentEditorActionPayload): void {
    const imageEditorAction = useImageContentEditorActionHandler(
      store,
      siteSettings as Ref<Readonly<TileData | undefined>>,
      imageId,
      updateImage,
    );
    if (payload.type === ImageContentEditorActionType.UPLOAD_IMAGE) {
      const convertOperation = {
        type: ConvertOperationType.CONVERT_TO_DEFAULT,
        dimensions: SOCIAL_SHARE_IMAGE_SCALE_DIMENSIONS,
      };
      imageEditorAction({
        ...payload,
        convertOperations: [convertOperation],
      });
    } else {
      imageEditorAction(payload);
    }
  }

  return socialShareImageEditorAction;
}

function useSeoSettingActivityToggleHandler(
  seoSettingActivity: Ref<boolean>,
): () => void {
  function toggleSeoSettingActivity(): void {
    seoSettingActivity.value = !seoSettingActivity.value;
  }

  return toggleSeoSettingActivity;
}

function useSeoField(
  initialValue: Ref<string | undefined>,
): UnwrapRef<StringSeoOption> {
  const value = initialValue;
  const activity = ref(false);
  const changeActivity = useSeoSettingActivityToggleHandler(activity);
  return reactive({
    value,
    activity,
    changeActivity,
  });
}

const SeoSettingsEditor = defineComponent({
  components: {
    TextEditorControl,
    MultilineTextEditorControl,
    ImageEditorControl,
    Accordion,
    AccordionItem,
    AccordionItemHint,
    NavigationLink,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const siteSettings = computed(() => state.value.siteSettings);
    const seoSettings = computed(() => siteSettings.value?.content?.seo);

    const seoTitle = useSeoField(
      computed({
        get: () => seoSettings.value?.title,
        set: useSeoTitleChangeHandler(root.$store, seoSettings),
      }),
    );

    const seoDescription = useSeoField(
      computed({
        get: () => seoSettings.value?.description,
        set: useSeoDescriptionChangeHandler(root.$store, seoSettings),
      }),
    );

    const updateSocialShareImage = useSocialShareImageChangeHandler(root.$store, seoSettings);
    const socialShareImageEditorAction = useSocialShareImageContentEditorActionHandler(
      root.$store,
      siteSettings,
      'social-share-image',
      updateSocialShareImage,
    );
    const initialSocialShareImageActivity = ref(false);
    const socialShareImage = {
      image: computed(() => seoSettings.value?.socialShareImage),
      changeValue: socialShareImageEditorAction,
      activity: initialSocialShareImageActivity,
      changeActivity: useSeoSettingActivityToggleHandler(initialSocialShareImageActivity),
      maxFileSize: 8 * 1024 * 1024,
      minDimensions: {
        height: 200,
        width: 200,
      },
      uploadErrorLabels: {
        ...COMMON_UPLOAD_ERROR_LABELS,
        [UploadError.SIZE_EXCEED]: 'SEO.SiteImage.Size.error',
        [UploadError.DIMENSION_EXCEEDED]: 'SEO.SiteImage.Dimensions.error',
      },
    };

    const navigateToCpSeo = (): void => {
      window.open(`${root.$store.state.context.cpUrl}#website-seo`, '_blank');
    };

    return {
      i18n,
      seoTitle,
      seoDescription,
      socialShareImage,
      navigateToCpSeo,
    };
  },
});

export default SeoSettingsEditor;
