




































import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { defineComponent } from '@vue/composition-api';

const DomainEditor = defineComponent({
  components: {
    ExternalIcon: Icon.ExternalIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const navigateToCpDomain = (): void => {
      window.open(`${root.$store.state.context.cpUrl}#website-domain`, '_blank');
    };

    return {
      i18n,
      navigateToCpDomain,
    };
  },
});

export default DomainEditor;
