import { MutationPayload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { State } from '@/app/editor/client/store/state';
import { OverlaySource } from '@/app/editor/client/type/overlay';

interface SetOverlayMutationPayload {
  overlaySource?: OverlaySource;
}

export interface SetOverlayMutation extends MutationPayload {
  type: MutationType.SET_OVERLAY;
  payload: SetOverlayMutationPayload;
}

export default function setOverlayMutation(oldState: State, data: SetOverlayMutation): State {
  const newState = oldState;
  newState.overlaySource = data.payload.overlaySource;
  return newState;
}
