import { State } from '@/app/editor/client/store/state';

interface AddImageInUploadingStateMutationPayload {
  readonly tileId: string;
  readonly imageId: string;
  readonly rawImage: string;
}

export default function addImageInUploadingStateMutation(
  oldState: State,
  payload: AddImageInUploadingStateMutationPayload,
): State {
  const { tileId, imageId, rawImage } = payload;
  const newState = oldState;
  newState.imageListInUploadingState.push({ tileId, imageId, rawImage });
  return newState;
}
