import Vue from 'vue';
import VueRouter from 'vue-router';
import CreateContainer from '@/app/editor/client/view/section/header/navigation/create';
import BackContainer from '@/app/editor/client/view/section/header/navigation/back';
import TileList from '@/app/editor/client/view/section/sidebar/pages/TileList';
import TileEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor';
import TileContentEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor';
import TileDesignEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileDesignEditor';
import TileManagementContainer
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/footer/TileManagementContainer';
import TileStyleDiscardEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/footer/TileStyleDiscardEditor';
// eslint-disable-next-line import/extensions
import TileActionLinkEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileActionLinkEditor.vue';
// eslint-disable-next-line import/extensions
import TileContactInfoEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileContactInfoEditor.vue';
// eslint-disable-next-line import/extensions
import TileLogoEditor
  from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileLogoEditor.vue';
// eslint-disable-next-line import/extensions
import TileHoursEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileHoursEditor.vue';
// eslint-disable-next-line import/extensions
import TileSocialIconListEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileSocialIconListEditor.vue';
// eslint-disable-next-line import/extensions
import TileLocationEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileLocationEditor.vue';
// eslint-disable-next-line import/extensions
import TileMenuEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileMenuEditor.vue';
// eslint-disable-next-line import/extensions
import TileMenuItemEditor from '@/app/editor/client/view/section/sidebar/pages/TileEditor/body/TileContentEditor/TileMenuItemEditor.vue';
import GlobalSettingsEditor
  from '@/app/editor/client/view/section/sidebar/pages/GlobalSettingsEditor';
import StoreDesignEditor from '@/app/editor/client/view/section/sidebar/pages/StoreDesignEditor';
import SeoSettingsEditor from '@/app/editor/client/view/section/sidebar/pages/SeoSettingsEditor';
import DomainEditor from '@/app/editor/client/view/section/sidebar/pages/DomainEditor';
import OrderingEditor from '@/app/editor/client/view/section/sidebar/pages/OrderingEditor';
import VueMeta from 'vue-meta';
import TrackingEditor from '@/app/editor/client/view/section/sidebar/pages/TrackingEditor';
import StyleSettingsEditor from '@/app/editor/client/view/section/sidebar/pages/StyleSettingsEditor';
import RouteName from './RouteName';

Vue.use(VueRouter);
Vue.use(VueMeta);

const routes = [
  {
    path: '/',
    name: RouteName.TILE_LIST,
    components: {
      sidebar: TileList,
      navigation: CreateContainer,
    },
  },
  {
    path: '/edit/:id',
    components: {
      sidebar: TileEditor,
      navigation: BackContainer,
    },
    children: [
      {
        path: '',
        name: RouteName.TILE_CONTENT,
        components: {
          editorBody: TileContentEditor,
          editorFooter: TileManagementContainer,
        },
      },
      {
        path: 'design',
        name: RouteName.TILE_DESIGN,
        components: {
          editorBody: TileDesignEditor,
          editorFooter: TileStyleDiscardEditor,
        },
      },
    ],
  },
  {
    path: '/edit/:id/action-link/:actionLinkId',
    name: RouteName.TILE_ACTION_LINK,
    components: {
      sidebar: TileActionLinkEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/contact-info',
    name: RouteName.TILE_CONTACT_INFO,
    components: {
      sidebar: TileContactInfoEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/contact-info-with-subheading',
    name: RouteName.TILE_CONTACT_INFO_WITH_SUBHEADING,
    components: {
      sidebar: TileContactInfoEditor,
      navigation: BackContainer,
    },
    props: {
      sidebar: {
        withSubheading: true,
      },
    },
  },
  {
    path: '/edit/:id/logo',
    name: RouteName.TILE_LOGO,
    components: {
      sidebar: TileLogoEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/hours',
    name: RouteName.TILE_HOURS,
    components: {
      sidebar: TileHoursEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/social-icon-list/:socialIconListId',
    name: RouteName.TILE_SOCIAL_ICON_LIST,
    components: {
      sidebar: TileSocialIconListEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/location/:locationId',
    name: RouteName.TILE_LOCATION,
    components: {
      sidebar: TileLocationEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/menu/',
    name: RouteName.TILE_MENU,
    components: {
      sidebar: TileMenuEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/edit/:id/menu/item/:menuItemId',
    name: RouteName.TILE_MENU_ITEM,
    components: {
      sidebar: TileMenuItemEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/global-settings',
    name: RouteName.GLOBAL_SETTINGS,
    components: {
      sidebar: GlobalSettingsEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/store-design',
    name: RouteName.STORE_DESIGN,
    components: {
      sidebar: StoreDesignEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/seo-settings',
    name: RouteName.SEO_SETTINGS,
    components: {
      sidebar: SeoSettingsEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/domain-settings',
    name: RouteName.DOMAIN_SETTINGS,
    components: {
      sidebar: DomainEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/ordering-settings',
    name: RouteName.ORDERING_SETTINGS,
    components: {
      sidebar: OrderingEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/tracking-settings',
    name: RouteName.TRACKING_SETTINGS,
    components: {
      sidebar: TrackingEditor,
      navigation: BackContainer,
    },
  },
  {
    path: '/style-settings',
    name: RouteName.STYLE_SETTINGS,
    components: {
      sidebar: StyleSettingsEditor,
      navigation: BackContainer,
    },
  },
];

export default function createRouter(): VueRouter {
  return new VueRouter({
    routes,
    base: `${document.location.pathname}${document.location.search}`,
  });
}
