































































































































import Vue from 'vue';
import { computed, defineComponent } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import useI18N from '@/app/shared/client/logic/i18n';
import { MenuData, MenuItemData, MenuItemType } from '@/app/shared/client/type/menu';
import { State } from '@/app/editor/client/store/state';
import {
  CheckboxToggleEditorControl,
  ComboBoxEditorControl,
  TextEditorControl,
} from '@/app/editor/client/view/component/control';
import { canScrollToTile } from '@/app/editor/client/logic/tile';
import Icon from '@/app/editor/client/view/icon';
import {
  useMenuContentUpdate,
  useMenuItemPropertyUpdate,
  useMenuItemRemove,
} from '@/app/editor/client/logic/editor/menu';
import { useLinkCheckAndUpdate } from '@/app/editor/client/logic/link';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { TileData } from '@/app/shared/client/type/tile';

const TileMenuItemEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
    TextEditorControl,
    CheckboxToggleEditorControl,
    TrashIcon: Icon.TrashIcon,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => {
      const state = vm.$store.state as State;
      if (state.editedTileData !== undefined && state.editedTileData.id === id) {
        return Promise.resolve();
      }

      return vm.$store.dispatch<GetTileDataAction>({
        type: ActionType.GET_TILE_DATA,
        payload: { id },
      });
    });
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData as TileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const menuContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, MenuData | undefined>;
      return tileContentRecord?.menu;
    });
    const updateMenuContent = useMenuContentUpdate(root.$store);

    const menuItemContent = computed(() => {
      const menuItemId = root.$route.params.menuItemId;
      const content = menuContent.value?.items
        ?.find((item) => item.id === menuItemId) as MenuItemData;
      return content;
    });

    const updateMenuItemTitle = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'title',
    );

    const updateMenuItemType = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'type',
    );
    const menuItemTypeChoices = Object.keys(MenuItemType)
      .map((menuItemType) => ({
        key: menuItemType,
        label: i18n(`CommonLabels.MenuItem.MenuItemType.${menuItemType}`),
      }));
    const menuItemType = computed({
      get: () => menuItemContent.value?.type,
      set: updateMenuItemType,
    });

    const updateMenuItemLink = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'link',
    );
    const menuItemLink = computed({
      get: () => menuItemContent.value?.link,
      set: updateMenuItemLink,
    });
    const checkAndUpdateMenuItemLink = useLinkCheckAndUpdate(menuItemLink, updateMenuItemLink);

    const updateMenuItemEmail = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'email',
    );
    const menuItemEmail = computed({
      get: () => menuItemContent.value?.email,
      set: updateMenuItemEmail,
    });

    const updateMenuItemPhone = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'phone',
    );
    const menuItemPhone = computed({
      get: () => menuItemContent.value?.phone,
      set: updateMenuItemPhone,
    });

    const updateMenuItemTileIdForScroll = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'tileIdForScroll',
    );
    const tilesForScrollChoices = computed(() => root.$store.getters.blockTileList
      .filter((tile: TileData) => canScrollToTile(tile))
      .map((tile: TileData) => ({
        key: tile.id,
        label: i18n(`TileItem.${tile.sourceId}.name`),
      })));
    const menuItemTileIdForScroll = computed({
      get: () => menuItemContent.value?.tileIdForScroll,
      set: updateMenuItemTileIdForScroll,
    });

    const updateMenuItemShowStoreCategories = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'showStoreCategories',
    );
    const menuItemShowStoreCategories = computed({
      get: () => menuItemContent.value?.showStoreCategories ?? true,
      set: updateMenuItemShowStoreCategories,
    });

    const updateMenuItemShowStoreLinks = useMenuItemPropertyUpdate(
      menuContent,
      updateMenuContent,
      menuItemContent,
      'showStoreLinks',
    );
    const menuItemShowStoreLinks = computed({
      get: () => menuItemContent.value?.showStoreLinks ?? true,
      set: updateMenuItemShowStoreLinks,
    });

    const removeMenuItem = useMenuItemRemove(
      root.$router,
      editedTile,
      menuContent,
      updateMenuContent,
    );

    return {
      i18n,
      tileDataLoaded,
      menuItemContent,
      updateMenuItemTitle,
      MenuItemType,
      menuItemTypeChoices,
      menuItemType,
      menuItemLink,
      checkAndUpdateMenuItemLink,
      menuItemEmail,
      menuItemPhone,
      tilesForScrollChoices,
      menuItemTileIdForScroll,
      menuItemShowStoreCategories,
      menuItemShowStoreLinks,
      removeMenuItem,
    };
  },
});

export default TileMenuItemEditor;
