































































































import Vue from 'vue';
import { computed, defineComponent } from '@vue/composition-api';
import { Route } from 'vue-router/types/router';
import { ActionType } from '@/app/editor/client/store/action/type';
import useI18N from '@/app/shared/client/logic/i18n';
import Icon from '@/app/editor/client/view/icon';
import { MenuData, MenuItemData } from '@/app/shared/client/type/menu';
import { State } from '@/app/editor/client/store/state';
import draggable from 'vuedraggable';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import {
  MENU_MAX_ITEMS,
  useMenuContentUpdate,
  useMenuItemAdd, useMenuItemListUpdate,
  useMenuItemNavigate,
} from '@/app/editor/client/logic/editor/menu';
import { GetTileDataAction } from '@/app/editor/client/store/action/get-tile-data';
import { TileData } from '@/app/shared/client/type/tile';

function menuItemHasTitle(menuItem: MenuItemData): boolean {
  return isNotNullOrEmpty(menuItem.title);
}

const TileMenuEditor = defineComponent({
  components: {
    PlusIcon: Icon.PlusIcon,
    ArrowRightIcon: Icon.ArrowRightIcon,
    DragIcon: Icon.DragIcon,
    draggable,
  },
  beforeRouteEnter(to: Route, _: Route, next: (p: (vm: Vue) => Promise<void>) => void) {
    const { id } = to.params;
    next((vm) => {
      const state = vm.$store.state as State;
      if (state.editedTileData !== undefined && state.editedTileData.id === id) {
        return Promise.resolve();
      }

      return vm.$store.dispatch<GetTileDataAction>({
        type: ActionType.GET_TILE_DATA,
        payload: { id },
      });
    });
  },
  beforeRouteUpdate(toRoute: Route, fromRoute: Route, next: () => void) {
    const toId = toRoute.params.id;
    const fromId = fromRoute.params.id;

    if (toId !== fromId) {
      if ((this as Vue).$store) {
        (this as Vue).$store.dispatch<GetTileDataAction>({
          type: ActionType.GET_TILE_DATA,
          payload: {
            id: toId,
          },
        });
      }
    }
    next();
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData as TileData);
    const tileDataLoaded = computed(() => editedTile.value !== undefined);

    const menuContent = computed(() => {
      const tileContentRecord = editedTile.value
        ?.content as Record<string, MenuData | undefined>;
      return tileContentRecord?.menu;
    });
    const updateMenuContent = useMenuContentUpdate(root.$store);

    const menuItemList = computed({
      get: () => menuContent.value?.items ?? [],
      set: useMenuItemListUpdate(menuContent, updateMenuContent),
    });
    const menuMaxItemsReached = computed(
      () => menuItemList.value.length === MENU_MAX_ITEMS,
    );

    const navigateToMenuItem = useMenuItemNavigate(
      root.$router,
      editedTile,
    );
    const addMenuItem = useMenuItemAdd(
      root.$router,
      i18n,
      editedTile,
      menuContent,
      updateMenuContent,
    );

    const moveTileUp = (index: number): void => {
      if (index > 0) {
        const newList = menuItemList.value;
        [newList[index], newList[index - 1]] = [newList[index - 1], newList[index]];
        menuItemList.value = newList;

        root.$nextTick().then(() => {
          const htmlElement = document.querySelector(`#${newList[index - 1].id} .ine-control`);
          (htmlElement as HTMLHtmlElement)?.focus();
        });
      }
    };

    const moveTileDown = (index: number): void => {
      if (index < menuItemList.value.length - 1) {
        const newList = menuItemList.value;
        [newList[index], newList[index + 1]] = [newList[index + 1], newList[index]];
        menuItemList.value = newList;

        root.$nextTick().then(() => {
          const htmlElement = document.querySelector(`#${newList[index + 1].id} .ine-control`);
          (htmlElement as HTMLHtmlElement)?.focus();
        });
      }
    };

    return {
      i18n,
      tileDataLoaded,
      menuItemList,
      menuMaxItemsReached,
      navigateToMenuItem,
      addMenuItem,
      menuItemHasTitle,
      moveTileUp,
      moveTileDown,
    };
  },
});

export default TileMenuEditor;
