import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface GetTileListRequest {
  readonly published: boolean;
}

export interface GetTileListResponse {
  readonly tiles: TileData[];
}

export function createGetTileListFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetTileListRequest) => Promise<GetTileListResponse> {
  return (request): Promise<GetTileListResponse> => fetch(`${apiUrl}/${siteId.value}/tile?published=${request.published}`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetTileListResponse) => resp);
}
