





















import { computed, defineComponent, ref } from '@vue/composition-api';
import { AuthorizeAction } from '@/app/editor/client/store/action/authorize';
import { ActionType } from '@/app/editor/client/store/action/type';
import { GetTileListAction } from '@/app/editor/client/store/action/get-tile-list';
import { GetProfileAction } from '@/app/editor/client/store/action/get-profile';
import { GetTranslationsAction } from '@/app/editor/client/store/action/get-translations';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import SmoothScrollPolyfill from '@/app/shared/client/polyfill/smooth-scroll';
import { GetBucketUrlsAction } from '@/app/editor/client/store/action/get-bucket-urls';
import ThePreview from './section/ThePreview.vue';
import TheSideBar from './section/TheSideBar.vue';
import TheHeader from './section/TheHeader.vue';
import ThePopup from './section/ThePopup.vue';

SmoothScrollPolyfill.install();

function bucketUrlsLoadingPromise(store: Store<State>): Promise<void> {
  return store.dispatch<GetBucketUrlsAction>({
    type: ActionType.GET_BUCKET_URLS,
  });
}

function profileLoadingPromise(store: Store<State>): Promise<void> {
  return store.dispatch<GetProfileAction>({ type: ActionType.GET_PROFILE })
    .then(() => {
      const locale = store.state.profile?.locale;
      return store.dispatch<GetTranslationsAction>({
        type: ActionType.GET_TRANSLATIONS,
        payload: { locale },
      });
    });
}

function tileListLoadingPromise(store: Store<State>): Promise<void> {
  return store.dispatch<GetTileListAction>({ type: ActionType.GET_TILE_LIST });
}

const Main = defineComponent({
  components: {
    TheHeader,
    TheSideBar,
    ThePreview,
    ThePopup,
  },
  setup(_, { root }) {
    const dataLoaded = ref(false);
    const hasPopup = computed(() => root.$store.state.currentPopup !== undefined);
    const hasOverlay = computed(() => root.$store.state.overlaySource !== undefined);
    const showPreview = computed(() => root.$store.state.isShowMobilePreview);
    const isTouchDevice = 'ontouchstart' in window;

    root.$store.dispatch<AuthorizeAction>({ type: ActionType.AUTHORIZE })
      .then(() => {
        const bucketUrlsLoading = bucketUrlsLoadingPromise(root.$store);
        const profileLoading = profileLoadingPromise(root.$store);
        const tileListLoading = tileListLoadingPromise(root.$store);
        return Promise.all([bucketUrlsLoading, profileLoading, tileListLoading]);
      })
      .then(() => {
        dataLoaded.value = true;
      })
      .catch(() => {
        dataLoaded.value = false;
      });

    return {
      dataLoaded,
      hasPopup,
      hasOverlay,
      showPreview,
      isTouchDevice,
    };
  },
});

export default Main;
