import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from './type';

export interface UpdateSiteSettingsFieldMutationPayload {
  readonly section: 'content' | 'design';
  readonly field: string;
  readonly value: unknown;
}

export interface UpdateSiteSettingsFieldMutation extends MutationPayload {
  type: MutationType.UPDATE_SITE_SETTINGS_FIELD;
  payload: UpdateSiteSettingsFieldMutationPayload;
}

export default function updateSiteSettingsFieldMutation(
  oldState: State,
  data: UpdateSiteSettingsFieldMutation,
): State {
  const newState = oldState;
  const siteSettings = newState.siteSettings;
  if (siteSettings === undefined) {
    return oldState;
  }

  siteSettings.hasChanges = true;
  if (data.payload.section === 'content') {
    const newSiteSettingsContent = (siteSettings.content ?? {}) as Record<string, unknown>;
    newSiteSettingsContent[data.payload.field] = data.payload.value;
    siteSettings.content = newSiteSettingsContent;
  }
  if (data.payload.section === 'design') {
    const newSiteSettingsDesign = (siteSettings.design ?? {}) as Record<string, unknown>;
    newSiteSettingsDesign[data.payload.field] = data.payload.value;
    siteSettings.design = newSiteSettingsDesign;
  }

  newState.siteSettings = {
    ...siteSettings,
  };

  if (data.payload.field === 'customCss') {
    // обновили кастомные стили, при паблише нужно будет перезагрузить превью,
    // чтобы стили применились
    newState.hasCustomCssChanges = true;
  }

  return newState;
}
