import { MutationPayload } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { State } from '@/app/editor/client/store/state';

interface SetSyncTilesStatusMutationPayload {
  syncTilesInProgress: boolean;
}

export interface SetSyncTilesStatusMutation extends MutationPayload {
  type: MutationType.SET_SYNC_TILES_STATUS;
  payload: SetSyncTilesStatusMutationPayload;
}

export default function setSyncTilesStatusMutation(
  oldState: State,
  data: SetSyncTilesStatusMutation,
): State {
  const newState = oldState;
  newState.syncTilesInProgress = data.payload.syncTilesInProgress;
  return newState;
}
