import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetProfileMutation } from '@/app/editor/client/store/mutation/set-profile';
import { Profile } from '@/app/shared/client/type/profile';
import { api } from '@/app/editor/client/api';

interface UpdateProfileActionPayload {
  profile: Profile;
}

export interface UpdateProfileAction extends ActionPayload {
  type: ActionType.UPDATE_PROFILE;
  payload: UpdateProfileActionPayload;
}

export default function updateProfileAction(
  actionContext: ActionContext<State, State>,
  data: UpdateProfileAction,
): void {
  const { profile } = data.payload;
  actionContext.commit<SetProfileMutation>({
    type: MutationType.SET_PROFILE,
    payload: {
      profile,
    },
  });

  api.updateProfile({ profile });
}
