







































































import {
  computed, defineComponent,
} from '@vue/composition-api';
import useI18N from '@/app/shared/client/logic/i18n';
import DragIcon from '@/app/editor/client/view/icon/DragIcon.vue';
import draggable from 'vuedraggable';
import { State } from '@/app/editor/client/store/state';
import { Store } from 'vuex';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { SetTileListMutation } from '@/app/editor/client/store/mutation/set-tile-list';
import { ActionType } from '@/app/editor/client/store/action/type';
import { SaveTileListAction } from '@/app/editor/client/store/action/save-tile-list';
import { StartDraggingEvent } from '@/app/editor/client/type/draggable';
import { sendPreviewFocusedTileId }
  from '@/app/editor/client/logic/preview/send-preview-focused-tile-id';
import { TileData } from '@/app/shared/client/type/tile';
import { TileType } from '@/app/shared/client/type/tile-type';

function isMovableTile(tile: TileData): boolean {
  switch (tile.type) {
    case 'HEADER':
    case 'FOOTER':
    case 'GDPR_BANNER':
      return false;
    default:
      return true;
  }
}

function getMovableTiles(store: Store<State>): TileData[] {
  return store.state.tiles.filter(isMovableTile);
}

function findTileByType(state: State, tileType: TileType): TileData | undefined {
  return state.tiles.find((tile) => tile.type === tileType);
}

function saveTilesOrdering(store: Store<State>): (movableTilesOrdering: TileData[]) => void {
  return (movableTilesOrdering): void => {
    const headerTile = findTileByType(store.state, 'HEADER');
    const footerTile = findTileByType(store.state, 'FOOTER');
    if (headerTile === undefined || footerTile === undefined) {
      return;
    }

    const tileOrders = movableTilesOrdering.map((tile) => tile.order);
    let tileOrderIndex = Math.min(...tileOrders);
    movableTilesOrdering.forEach((tile) => {
      tile.hasChanges = tile.hasChanges || tile.order !== tileOrderIndex;
      tile.order = tileOrderIndex;
      tileOrderIndex += 1;
    });

    const newTileList = [
      headerTile,
      ...movableTilesOrdering,
      footerTile,
    ];

    const payload = { tileList: newTileList, publishedTileList: undefined };
    store.commit<SetTileListMutation>({ type: MutationType.SET_TILE_LIST, payload });
    store.dispatch<SaveTileListAction>({ type: ActionType.SAVE_TILE_LIST, payload });
  };
}

function useDragging(store: Store<State>): {
  startDragging: ({ oldIndex }: { oldIndex: number }) => void;
  endDragging: () => void;
  } {
  let draggingTileId: string | undefined;
  const startDragging = ({ oldIndex }: StartDraggingEvent): void => {
    const movableTiles = getMovableTiles(store);
    draggingTileId = movableTiles[oldIndex]?.id;
  };

  const endDragging = (): void => {
    if (draggingTileId !== undefined) {
      sendPreviewFocusedTileId(draggingTileId);
      draggingTileId = undefined;
    }
  };

  return {
    startDragging,
    endDragging,
  };
}

const OrderingEditor = defineComponent({
  components: {
    DragIcon,
    draggable,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const movableTiles = computed({
      get: () => getMovableTiles(root.$store),
      set: saveTilesOrdering(root.$store),
    });

    const headerTile = computed(() => findTileByType(root.$store.state, 'HEADER'));
    const footerTile = computed(() => findTileByType(root.$store.state, 'FOOTER'));
    const {
      startDragging,
      endDragging,
    } = useDragging(root.$store);

    const moveTileUp = (index: number): void => {
      if (index > 0) {
        const newList = movableTiles.value;
        [newList[index], newList[index - 1]] = [newList[index - 1], newList[index]];
        movableTiles.value = newList;

        root.$nextTick().then(() => {
          const htmlElement = document.querySelector(`#${newList[index - 1].id} .ine-control`);
          (htmlElement as HTMLHtmlElement)?.focus();
          sendPreviewFocusedTileId(newList[index - 1].id);
        });
      }
    };

    const moveTileDown = (index: number): void => {
      if (index < movableTiles.value.length - 1) {
        const newList = movableTiles.value;
        [newList[index], newList[index + 1]] = [newList[index + 1], newList[index]];
        movableTiles.value = newList;

        root.$nextTick().then(() => {
          const htmlElement = document.querySelector(`#${newList[index + 1].id} .ine-control`);
          (htmlElement as HTMLHtmlElement)?.focus();
          sendPreviewFocusedTileId(newList[index + 1].id);
        });
      }
    };

    return {
      i18n,
      movableTiles,
      headerTile,
      footerTile,
      moveTileUp,
      moveTileDown,
      startDragging,
      endDragging,
    };
  },
});

export default OrderingEditor;
