import { Ref } from '@vue/composition-api';
import { TileShowcaseCategory } from '@/app/editor/client/type/showcase';

export interface GetTileShowcaseResponse {
  readonly categories: TileShowcaseCategory[];
}

export function createGetTileShowcaseFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): () => Promise<GetTileShowcaseResponse> {
  return (): Promise<GetTileShowcaseResponse> => fetch(`${apiUrl}/${siteId.value}/tile/showcase`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetTileShowcaseResponse) => resp);
}
