import { ActionTree } from 'vuex';
import { ActionType } from '@/app/editor/client/store/action/type';
import authorizeAction from '@/app/editor/client/store/action/authorize';
import getTileConfigAction from '@/app/editor/client/store/action/get-tile-config';
import getTileListAction from '@/app/editor/client/store/action/get-tile-list';
import removeTileAction from '@/app/editor/client/store/action/remove-tile';
import getTileDataAction from '@/app/editor/client/store/action/get-tile-data';
import saveTileDataAction from '@/app/editor/client/store/action/save-tile-data';
import resetTileDataAction from '@/app/editor/client/store/action/reset-tile-data';
import discardTileDesignAction from '@/app/editor/client/store/action/discard-tile-design';
import saveSiteSettingsAction from '@/app/editor/client/store/action/save-site-settings';
import getImageUploadInfoAction from '@/app/editor/client/store/action/get-image-upload-info';
import getProfileAction from '@/app/editor/client/store/action/get-profile';
import updateProfileAction from '@/app/editor/client/store/action/update-profile';
import getTranslationsAction from '@/app/editor/client/store/action/get-translations';
import getTileShowcaseDataAction from '@/app/editor/client/store/action/get-tile-showcase-data';
import addTileFromShowcaseAction from '@/app/editor/client/store/action/add-tile-from-showcase';
import getImageUploadResultAction from '@/app/editor/client/store/action/get-image-upload-result';
import addImageInUploadingStateAction
  from '@/app/editor/client/store/action/add-image-in-uploading-state';
import removeImageInUploadingStateAction
  from '@/app/editor/client/store/action/remove-image-in-uploading-state';
import getBucketUrlsAction from '@/app/editor/client/store/action/get-bucket-urls';
import publishAllChangesAction from '@/app/editor/client/store/action/publish-all-changes';
import discardChangesAction from '@/app/editor/client/store/action/discard-changes';
import saveTileListAction from '@/app/editor/client/store/action/save-tile-list';
import createGDPRBannerTileAction from '@/app/editor/client/store/action/create-gdpr-banner-tile';
import { State } from './state';

function actionsFactory(): ActionTree<State, State> {
  return {
    [ActionType.AUTHORIZE]: authorizeAction,
    [ActionType.GET_PROFILE]: getProfileAction,
    [ActionType.UPDATE_PROFILE]: updateProfileAction,
    [ActionType.GET_TRANSLATIONS]: getTranslationsAction,
    [ActionType.GET_TILE_CONFIG]: getTileConfigAction,
    [ActionType.GET_TILE_LIST]: getTileListAction,
    [ActionType.REMOVE_TILE]: removeTileAction,
    [ActionType.DISCARD_TILE_DESIGN]: discardTileDesignAction,
    [ActionType.GET_TILE_DATA]: getTileDataAction,
    [ActionType.SAVE_TILE_DATA]: saveTileDataAction,
    [ActionType.RESET_TILE_DATA]: resetTileDataAction,
    [ActionType.SAVE_SITE_SETTINGS]: saveSiteSettingsAction,
    [ActionType.ADD_TILE_FROM_SHOWCASE]: addTileFromShowcaseAction,
    [ActionType.GET_TILE_SHOWCASE_DATA]: getTileShowcaseDataAction,
    [ActionType.GET_IMAGE_UPLOAD_INFO]: getImageUploadInfoAction,
    [ActionType.GET_IMAGE_UPLOAD_RESULT]: getImageUploadResultAction,
    [ActionType.ADD_IMAGE_IN_UPLOADING_STATE]: addImageInUploadingStateAction,
    [ActionType.REMOVE_IMAGE_IN_UPLOADING_STATE]: removeImageInUploadingStateAction,
    [ActionType.GET_BUCKET_URLS]: getBucketUrlsAction,
    [ActionType.PUBLISH_ALL_CHANGES]: publishAllChangesAction,
    [ActionType.DISCARD_CHANGES]: discardChangesAction,
    [ActionType.SAVE_TILE_LIST]: saveTileListAction,
    [ActionType.CREATE_GDPR_BANNER_TILE]: createGDPRBannerTileAction,
  };
}

export default function createActions(): ActionTree<State, State> {
  const actions = actionsFactory();
  return { ...actions };
}
