























































































































import {
  computed, defineComponent, PropType, Ref, ref, toRefs,
} from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ImageData, ImageInfoData } from '@/app/shared/client/type/image';
import getImageUrl from '@/app/shared/client/helper/image-url';
import {
  ImageContentEditorActionPayload,
  ImageContentEditorActionType,
} from '@/app/editor/client/type/editor-action/image-content-editor';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  COMMON_UPLOAD_ERROR_LABELS,
  UploadError,
  UploadErrorLabels,
} from '@/app/editor/client/type/image-uploader';
import { getUploadErrorLabel } from '@/app/editor/client/logic/image-uploader';
import { getImageInUploadingState } from '@/app/website/client/logic/image-uploading';

const SUPPORTED_ACCEPT_HEADERS = [
  'image/jpeg',
  'image/png',
  'image/gif',
  'image/bmp',
  'image/tiff',
  'image/heic',
  'image/webp',
  'image/jp2',
];
const DEFAULT_MAX_FILE_SIZE = 20 * 1024 * 1024;

function checkFileFormat(acceptHeader: string): boolean {
  return SUPPORTED_ACCEPT_HEADERS.includes(acceptHeader);
}

function checkFileSize(imageSize: number, maxFileSize: number): boolean {
  return imageSize < maxFileSize;
}

function checkMinDimensions(height: number, width: number, minDimensions?: ImageInfoData): boolean {
  if (minDimensions === undefined) {
    return true;
  }
  if (minDimensions.height !== undefined && height < minDimensions.height) {
    return false;
  }
  return !(minDimensions.width !== undefined && width < minDimensions.width);
}

function useUploadHandler(
  emit: (event: string, payload: ImageContentEditorActionPayload) => void,
  fileUploader: Ref<HTMLInputElement | undefined>,
  currentUploadError: Ref<UploadError | undefined>,
  maxFileSize: number,
  minDimensions: ImageInfoData,
): () => void {
  function onStartUpload(): void {
    const fileReader = new FileReader();
    if (fileUploader.value !== undefined) {
      const imageFile = fileUploader.value.files?.[0];
      if (imageFile !== undefined) {
        const fileAcceptHeader = imageFile.type;
        if (!checkFileFormat(fileAcceptHeader)) {
          fileUploader.value.value = '';
          currentUploadError.value = UploadError.FORMAT_NOT_SUPPORTED;
          return;
        }

        if (!checkFileSize(imageFile.size, maxFileSize)) {
          fileUploader.value.value = '';
          currentUploadError.value = UploadError.SIZE_EXCEED;
          return;
        }

        fileUploader.value.value = '';
        currentUploadError.value = undefined;
        fileReader.readAsDataURL(imageFile);
        fileReader.addEventListener('loadend', () => {
          const rawImage = fileReader.result as string | null;
          if (rawImage === null) {
            return;
          }

          const img = new Image();
          img.addEventListener('load', () => {
            if (!checkMinDimensions(img.height, img.width, minDimensions)) {
              currentUploadError.value = UploadError.DIMENSION_EXCEEDED;
              return;
            }
            emit('editor-action', {
              type: ImageContentEditorActionType.UPLOAD_IMAGE,
              imageUploadingData: {
                imageFile,
                rawImage,
                uploadError: currentUploadError,
              },
            });
          });
          img.src = rawImage;
        });
      }
    }
  }
  return onStartUpload;
}

function useUploadPopupHandler(
  fileUploader: Ref<HTMLInputElement | undefined>,
): () => void {
  function onOpenUploadPopup(): void {
    if (fileUploader.value !== undefined) {
      fileUploader.value.click();
    }
  }
  return onOpenUploadPopup;
}

function useRemoveHandler(
  emit: (event: string, payload: ImageContentEditorActionPayload) => void,
): () => void {
  function onRemove(): void {
    emit('editor-action', {
      type: ImageContentEditorActionType.REMOVE_IMAGE,
    });
  }
  return onRemove;
}

const ImageEditorControl = defineComponent({
  components: {
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ImageData>,
    },
    tileId: {
      required: true,
      type: String,
    },
    imageId: {
      required: true,
      type: String,
    },
    thumbnailFit: {
      required: false,
      default: undefined,
      type: String,
    },
    thumbnailBackground: {
      required: false,
      default: undefined,
      type: String,
    },
    maxFileSize: {
      required: false,
      default: DEFAULT_MAX_FILE_SIZE,
      type: Number,
    },
    minDimensions: {
      required: false,
      default: undefined,
      type: Object as PropType<ImageInfoData>,
    },
    uploadErrorLabels: {
      required: false,
      default: (): UploadErrorLabels => COMMON_UPLOAD_ERROR_LABELS,
      type: Object as PropType<UploadErrorLabels>,
    },
  },
  setup(props, { emit, root }) {
    const store = root.$store as Store<State>;
    const i18n = useI18N(store);
    const { tileId, imageId } = toRefs(props);

    const imageData = computed<ImageData | undefined>(() => props.value);
    const hasImageSetData = computed(() => props.value?.set !== undefined);

    const fileUploader = ref<HTMLInputElement>();

    const currentUploadingImage = computed(() => {
      const { imageListInUploadingState } = store.state;
      return getImageInUploadingState(imageListInUploadingState, tileId.value, imageId.value);
    });

    const preview = computed(() => {
      if (currentUploadingImage.value !== undefined) {
        return currentUploadingImage.value.rawImage;
      }

      const bucket = imageData.value?.bucket;
      const currentImage = imageData.value?.set?.['default-200x200']?.url;
      return getImageUrl(currentImage, bucket, root.$store.state.context.bucketUrls);
    });
    const hasPreview = computed(() => preview.value !== undefined);

    const isUploading = computed<boolean>(() => currentUploadingImage.value !== undefined);

    const uploadError = ref<UploadError | undefined>();
    const hasUploadError = computed(() => uploadError.value !== undefined);
    const uploadErrorLabel = computed(
      () => (uploadError.value !== undefined
        ? getUploadErrorLabel(uploadError.value, props.uploadErrorLabels)
        : uploadError),
    );

    const onStartUpload = useUploadHandler(
      emit,
      fileUploader,
      uploadError,
      props.maxFileSize,
      props.minDimensions,
    );
    const onOpenUploadPopup = useUploadPopupHandler(fileUploader);
    const onRemove = useRemoveHandler(emit);

    const supportedAcceptHeaders = SUPPORTED_ACCEPT_HEADERS.join(',');

    return {
      i18n,
      fileUploader,
      imageData,
      hasImageSetData,
      preview,
      hasPreview,
      hasUploadError,
      isUploading,
      onOpenUploadPopup,
      onStartUpload,
      onRemove,
      supportedAcceptHeaders,
      uploadErrorLabel,
    };
  },
});

export default ImageEditorControl;
