import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MenuData, MenuItemData, MenuItemType } from '@/app/shared/client/type/menu';
import { useSaveTileSettingsDebounced } from '@/app/shared/client/helper/save-tile-settings-debounce';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { VueRouter } from 'vue-router/types/router';
import { Ref } from '@vue/composition-api';
import { nanoid } from 'nanoid';
import RouteName from '@/app/editor/client/router/RouteName';
import { FieldName, FieldTypes } from '@/app/shared/client/helper/base';
import { TileData } from '@/app/shared/client/type/tile';

const MENU_ITEM_ID_SIZE = 5;
export const MENU_MAX_ITEMS = 15;

export function useMenuContentUpdate(
  store: Store<State>,
): (menuContent: MenuData | undefined) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);

  function updateMenuContent(menuContent: MenuData | undefined): void {
    store.commit(
      MutationType.UPDATE_EDITED_TILE_FIELD,
      { section: 'content', field: 'menu', value: menuContent },
    );
    debouncedDispatch();
  }

  return updateMenuContent;
}

export function useMenuItemPropertyUpdate(
  menuContent: Readonly<Ref<MenuData | undefined>>,
  updateMenuContent: (menuContent: MenuData | undefined) => void,
  menuItemContent: Ref<Readonly<MenuItemData>>,
  field: FieldName<MenuItemData>,
): (value?: FieldTypes<MenuItemData>) => void {
  function updateValue(value?: FieldTypes<MenuItemData>): void {
    const menuItemContentRecord: Record<string, FieldTypes<MenuItemData>> = menuItemContent.value;
    menuItemContentRecord[field] = value;

    const menuItemIndexForUpdate = menuContent.value?.items
      ?.findIndex((item) => item.id === menuItemContent.value?.id);
    if (menuItemIndexForUpdate !== undefined) {
      menuContent.value?.items?.splice(menuItemIndexForUpdate, 1,
        menuItemContentRecord as unknown as MenuItemData);
      updateMenuContent(menuContent.value);
    }
  }
  return updateValue;
}

export function useMenuItemAdd(
  router: VueRouter,
  i18n: (label: string) => string,
  tile: Readonly<Ref<TileData>>,
  menuContent: Readonly<Ref<MenuData | undefined>>,
  updateMenuContent: (menuContent: MenuData | undefined) => void,
): () => void {
  function addMenuItem(): void {
    const updatedMenuContent = { ...menuContent.value };
    const updatedMenuItemList = updatedMenuContent.items ?? [];
    const newMenuItem = {
      id: nanoid(MENU_ITEM_ID_SIZE),
      title: i18n('Block.Header.Content.Menu.MenuItem.Name.default'),
      type: MenuItemType.HYPER_LINK,
    };
    updatedMenuItemList.push(newMenuItem);
    updatedMenuContent.items = updatedMenuItemList;
    updateMenuContent(updatedMenuContent);
    router.push({
      name: RouteName.TILE_MENU_ITEM,
      params: {
        id: tile.value.id,
        menuItemId: newMenuItem.id,
      },
    });
  }

  return addMenuItem;
}

export function useMenuItemRemove(
  router: VueRouter,
  tile: Readonly<Ref<TileData>>,
  menuContent: Readonly<Ref<MenuData | undefined>>,
  updateMenuContent: (menuContent: MenuData | undefined) => void,
): (menuItemId: string) => void {
  function removeMenuItem(menuItemId: string): void {
    const menuItemIndexForRemove = menuContent.value?.items
      ?.findIndex((item) => item.id === menuItemId);
    if (menuItemIndexForRemove !== undefined) {
      menuContent.value?.items?.splice(menuItemIndexForRemove, 1);
      updateMenuContent(menuContent.value);
      router.push({
        name: RouteName.TILE_MENU,
        params: {
          id: tile.value.id,
        },
      });
    }
  }

  return removeMenuItem;
}

export function useMenuItemListUpdate(
  menuContent: Readonly<Ref<MenuData | undefined>>,
  updateMenuContent: (menuContent: MenuData | undefined) => void,
): (menuItemList: MenuItemData[]) => void {
  function updateMenuItemList(updatedMenuItemList: MenuItemData[]): void {
    const updatedMenuContent = { ...menuContent.value };
    updatedMenuContent.items = updatedMenuItemList;
    updateMenuContent(updatedMenuContent);
  }

  return updateMenuItemList;
}

export function useMenuNavigate(
  router: VueRouter,
  tile: Readonly<Ref<TileData>>,
): () => void {
  function navigateToMenu(): void {
    const tileId = tile.value?.id;
    router.push({ name: RouteName.TILE_MENU, params: { id: tileId } });
  }

  return navigateToMenu;
}

export function useMenuItemNavigate(
  router: VueRouter,
  tile: Readonly<Ref<TileData>>,
): (menuItemId: string) => void {
  function navigateToMenuItem(menuItemId: string): void {
    router.push({
      name: RouteName.TILE_MENU_ITEM,
      params: {
        id: tile.value.id,
        menuItemId,
      },
    });
  }

  return navigateToMenuItem;
}
