import { Ref } from '@vue/composition-api';
import { ConvertOperation } from '@/app/editor/client/type/image-convert-operation';
import { ImageUploadInfo } from '@/app/editor/client/type/image-uploader';

export interface GetImageUploadInfoRequest {
  readonly tileId: string;
  readonly acceptHeader: string;
  readonly convertOperations?: ConvertOperation[];
}

export type GetImageUploadInfoResponse = ImageUploadInfo

export function createGetImageUploadInfoFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetImageUploadInfoRequest) => Promise<GetImageUploadInfoResponse> {
  return (request): Promise<GetImageUploadInfoResponse> => fetch(`${apiUrl}/${siteId.value}/tile/${request.tileId}/image`, {
    method: 'POST',
    body: JSON.stringify(request),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetImageUploadInfoResponse) => resp);
}
