import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { TileData } from '@/app/shared/client/type/tile';

export interface SetTileDataMutation extends MutationPayload {
  type: MutationType.SET_TILE_DATA;
  tileData: TileData | undefined;
}

export default function setTileDataMutation(oldState: State, data: SetTileDataMutation): State {
  const newState = oldState;
  newState.editedTileData = data.tileData;
  return newState;
}
