














































import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { computed, defineComponent, Ref } from '@vue/composition-api';
import { State } from '@/app/editor/client/store/state';
import { Store } from 'vuex';
import { ActionType } from '@/app/editor/client/store/action/type';
import { SaveTileDataAction } from '@/app/editor/client/store/action/save-tile-data';
import { CheckboxToggleEditorControl } from '@/app/editor/client/view/component/control';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import {
  CreateGDPRBannerTileAction,
} from '@/app/editor/client/store/action/create-gdpr-banner-tile';
import { TileData } from '@/app/shared/client/type/tile';

function createGDPRBannerTile(store: Store<State>, visibility: boolean): void {
  store.dispatch<CreateGDPRBannerTileAction>({
    type: ActionType.CREATE_GDPR_BANNER_TILE,
    payload: { visibility },
  });
}

function changeGDPRBannerTileVisibility(
  store: Store<State>,
  gdprBannerTile: TileData,
  visibility: boolean,
): void {
  store.commit(
    MutationType.CHANGE_TILE_VISIBILITY,
    { tileId: gdprBannerTile.id, visibility },
  );
  store.dispatch<SaveTileDataAction>({
    type: ActionType.SAVE_TILE_DATA,
    payload: { tileData: gdprBannerTile },
  });
}

function createGDPRBannerTileVisibilityChangeHandler(
  store: Store<State>,
  gdprBannerTile: Ref<TileData | undefined>,
): (visibility: boolean) => void {
  function gdprBannerTileVisibilityChangeHandler(visibility: boolean): void {
    if (gdprBannerTile.value === undefined) {
      createGDPRBannerTile(store, visibility);
      return;
    }

    changeGDPRBannerTileVisibility(store, gdprBannerTile.value, visibility);
  }

  return gdprBannerTileVisibilityChangeHandler;
}

const TrackingEditor = defineComponent({
  components: {
    ExternalIcon: Icon.ExternalIcon,
    CheckboxToggleEditorControl,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const navigateToCpTracking = (): void => {
      window.open(`${root.$store.state.context.cpUrl}#analytics`, '_blank');
    };

    const gdprBannerTile = computed(() => {
      const state = root.$store.state as State;
      return state.tiles.find((tile) => tile.type === 'GDPR_BANNER');
    });
    const gdprBannerTileVisibility = computed({
      get: () => gdprBannerTile.value?.visibility ?? false,
      set: createGDPRBannerTileVisibilityChangeHandler(root.$store, gdprBannerTile),
    });

    return {
      i18n,
      navigateToCpTracking,
      gdprBannerTileVisibility,
    };
  },
});

export default TrackingEditor;
