import { Ref } from '@vue/composition-api';
import { TileConfig } from '@/app/editor/client/type/tile-config';
import { TileType } from '@/app/shared/client/type/tile-type';

export interface GetTileConfigRequest {
  readonly type: string;
}
export interface GetTileConfigResponse {
  readonly type: TileType;
  readonly config: TileConfig;
}

export function createGetTileConfigFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetTileConfigRequest) => Promise<GetTileConfigResponse> {
  return (request): Promise<GetTileConfigResponse> => fetch(`${apiUrl}/${siteId.value}/tile/config/${request.type}`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetTileConfigResponse) => resp);
}
