












































import { defineComponent } from '@vue/composition-api';
import { VueRouter } from 'vue-router/types/router';
import RouteName from '@/app/editor/client/router/RouteName';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { sendPreviewFocusedTileId }
  from '@/app/editor/client/logic/preview/send-preview-focused-tile-id';

function handleBackToTileList(): void {
  sendPreviewFocusedTileId(undefined);
}

function useBack($router: VueRouter): () => void {
  function back(): void {
    switch ($router.currentRoute.name) {
      case RouteName.TILE_CONTENT:
      case RouteName.TILE_DESIGN:
      case RouteName.GLOBAL_SETTINGS:
      case RouteName.STORE_DESIGN:
      case RouteName.SEO_SETTINGS:
      case RouteName.DOMAIN_SETTINGS:
      case RouteName.ORDERING_SETTINGS:
      case RouteName.TRACKING_SETTINGS:
      case RouteName.STYLE_SETTINGS:
        $router.push('/');
        handleBackToTileList();
        break;
      case RouteName.TILE_ACTION_LINK:
      case RouteName.TILE_CONTACT_INFO:
      case RouteName.TILE_CONTACT_INFO_WITH_SUBHEADING:
      case RouteName.TILE_LOGO:
      case RouteName.TILE_HOURS:
      case RouteName.TILE_SOCIAL_ICON_LIST:
      case RouteName.TILE_LOCATION:
      case RouteName.TILE_MENU: {
        const tileId = $router.currentRoute.params.id;
        $router.push(`/edit/${tileId}`);
        break;
      }
      case RouteName.TILE_MENU_ITEM: {
        const tileId = $router.currentRoute.params.id;
        $router.push({
          name: RouteName.TILE_MENU,
          params: {
            id: tileId,
          },
        });
        break;
      }
      default:
        break;
    }
  }
  return back;
}

const Container = defineComponent({
  components: {
    BackIcon: Icon.BackIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const back = useBack(root.$router);

    return {
      i18n,
      back,
    };
  },
});

export default Container;
