















































import Vue from 'vue';
import { computed, defineComponent } from '@vue/composition-api';
import { Store } from 'vuex';
import Icon from '@/app/editor/client/view/icon/';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { PopupType } from '@/app/editor/client/type/popup';
import { SetPopupMutation } from '@/app/editor/client/store/mutation/set-popup';
import useI18N from '@/app/shared/client/logic/i18n';
import SiteSettings from '@/app/editor/client/view/section/sidebar/pages/TileList/SiteSettings.vue';
import { ActionType } from '@/app/editor/client/store/action/type';
import { Route } from 'vue-router';
import { ResetTileDataAction } from '@/app/editor/client/store/action/reset-tile-data';
import { GetTileListAction } from '@/app/editor/client/store/action/get-tile-list';
import { TileData } from '@/app/shared/client/type/tile';
import { Profile } from '@/app/shared/client/type/profile';
import TileItem from './TileItem.vue';

function addTileHandler(store: Store<State>): () => void {
  function addTile(): void {
    store.commit<SetPopupMutation>({
      type: MutationType.SET_POPUP,
      payload: {
        popup: PopupType.TILE_SHOWCASE,
      },
    });
  }
  return addTile;
}

function getEditableTileList(tileList: TileData[]): TileData[] {
  return tileList.filter((tile) => tile.type !== 'GDPR_BANNER');
}

function getTileActivity(profile: Profile, tile: TileData): boolean {
  switch (tile.type) {
    case 'STORE':
      return !profile.storeClosed;
    default:
      return true;
  }
}

const TileList = defineComponent({
  components: {
    SiteSettings,
    PlusIcon: Icon.PlusIcon,
    TileItem,
  },
  beforeRouteEnter(
    _toRoute: Route,
    fromRoute: Route,
    next: (p: (vm: Vue) => Promise<void>) => void,
  ) {
    next(({ $store }) => {
      const isInitialLoading = fromRoute.name === null;
      // Не обновляем список тайлов если:
      // - это первая загрузка компонента — тайлы только что загрузили
      // - кто-то уже их обновляет
      if (!isInitialLoading && !$store.state.syncTilesInProgress) {
        return $store.dispatch<ResetTileDataAction>({
          type: ActionType.RESET_TILE_DATA,
        })
          .then(() => $store.dispatch<GetTileListAction>({
            type: ActionType.GET_TILE_LIST,
          }));
      }

      return Promise.resolve();
    });
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);

    const profile = computed(() => state.value.profile);
    const tileList = computed(() => getEditableTileList(state.value.tiles));
    const addTile = addTileHandler(root.$store);

    return {
      i18n,
      profile,
      tileList,
      addTile,
      getTileActivity,
    };
  },
});

export default TileList;
