import { Ref } from '@vue/composition-api';
import { Bucket } from '@/app/shared/client/type/bucket';

export interface GetImageBucketsResponse {
  readonly urls: Record<Bucket, string>;
}

export function createGetImageBucketsFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): () => Promise<GetImageBucketsResponse> {
  return (): Promise<GetImageBucketsResponse> => fetch(`${apiUrl}/${siteId.value}/image/bucket`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetImageBucketsResponse) => resp);
}
