









































































































































































import {
  computed, defineComponent, Ref, ref, watch,
} from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon/';
import useI18N from '@/app/shared/client/logic/i18n';
import TheCenterHeaderMenu from '@/app/editor/client/view/section/header/TheCenterHeaderMenu.vue';
import { toggleOverlay } from '@/app/editor/client/view/helpers/overlay';
import { OverlaySource } from '@/app/editor/client/type/overlay';
import { ActionType } from '@/app/editor/client/store/action/type';
import { PublishAllChangesAction } from '@/app/editor/client/store/action/publish-all-changes';
import { DiscardChangesAction } from '@/app/editor/client/store/action/discard-changes';
import { State } from '@/app/editor/client/store/state';
import { Store } from 'vuex';
import ClosePopup from './header/ClosePopup.vue';

function doPublishHandler(
  store: Store<State>,
  isPublishing: Ref<boolean>,
  successfullyPublished: Ref<boolean>,
): () => void {
  return (): void => {
    if (isPublishing.value) {
      return;
    }
    isPublishing.value = true;
    successfullyPublished.value = false;
    store.dispatch<PublishAllChangesAction>({ type: ActionType.PUBLISH_ALL_CHANGES })
      .then(() => {
        isPublishing.value = false;
        successfullyPublished.value = true;
      })
      .catch(() => {
        isPublishing.value = false;
        successfullyPublished.value = false;
      });
  };
}

const Header = defineComponent({
  components: {
    TheCenterHeaderMenu,
    ArrowDownIcon: Icon.ArrowDownIcon,
    CheckmarkIcon: Icon.CheckmarkIcon,
    ExternalIcon: Icon.ExternalIcon,
    HelpIcon: Icon.HelpIcon,
    MoreIcon: Icon.MoreIcon,
    RefreshIcon: Icon.RefreshIcon,
    ClosePopup,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    const hasPopup = computed(() => root.$store.state.currentPopup !== undefined);
    const isPublishing = ref(false);
    const successfullyPublished = ref(false);
    const allChangesPublished = computed(() => root.$store.getters.allChangesPublished);
    watch(allChangesPublished, (newValue) => {
      if (!newValue && successfullyPublished.value) {
        successfullyPublished.value = false;
      }
    });

    const doPublish = doPublishHandler(root.$store, isPublishing, successfullyPublished);

    const doDiscardChanges = (): void => {
      root.$store.dispatch<DiscardChangesAction>({
        type: ActionType.DISCARD_CHANGES,
      });
    };

    const mobileMenuOpened = ref(false);
    const toggleMobileMenu = (): void => {
      mobileMenuOpened.value = !mobileMenuOpened.value;
      toggleOverlay(mobileMenuOpened, OverlaySource.MOBILE_MENU, root.$store);
    };

    const navigateToWebsite = (): void => {
      window.open('/', '_blank');
    };

    return {
      i18n,
      hasPopup,
      isPublishing,
      allChangesPublished,
      successfullyPublished,
      doPublish,
      doDiscardChanges,
      mobileMenuOpened,
      toggleMobileMenu,
      navigateToWebsite,
    };
  },
});

export default Header;
