import { ActionContext, Payload } from 'vuex';
import { ActionType } from '@/app/editor/client/store/action/type';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { DiscardChangesMutation } from '@/app/editor/client/store/mutation/discard-changes';
import { commitSetSyncTilesStatus, commitTileList } from '@/app/editor/client/store/action/helpers';
import sendPreviewSiteSettings from '@/app/editor/client/logic/preview/send-preview-site-settings';
import { DiscardChangesResponse } from '@/app/editor/client/api/discard-changes';

export interface DiscardChangesAction extends Payload {
  type: ActionType.DISCARD_CHANGES;
}

export default function discardChangesAction(
  { commit, state }: ActionContext<State, State>,
): Promise<void> {
  commit<DiscardChangesMutation>({ type: MutationType.DISCARD_CHANGES });
  const hasSiteSettingsChanges = state.siteSettings?.hasChanges ?? false;
  if (hasSiteSettingsChanges) {
    sendPreviewSiteSettings(state.siteSettings);
  }
  commitSetSyncTilesStatus(commit, true);
  return new Promise((resolve, reject) => {
    api.discardChanges()
      .then((response: DiscardChangesResponse) => {
        commitSetSyncTilesStatus(commit, false);
        const newTileList = response.tiles;
        // после успешного discardChange draft == published
        commitTileList(newTileList, newTileList, commit);
        sendPreviewSiteSettings(state.siteSettings);
        resolve();
      }).catch((error: Error) => reject(error.message));
  });
}
