import Vue from 'vue';
import CompositionApi from '@vue/composition-api';
import { VNode } from 'vue/types/vnode';
import { Store } from 'vuex';
import { VueRouter } from 'vue-router/types/router';
import { State } from '@/app/editor/client/store/state';
import { InitContextMutation } from '@/app/editor/client/store/mutation/init-context';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import createRouter from '@/app/editor/client/router';
import { initApiContext } from '@/app/editor/client/api';
import createStore from './store';
import App from './view/Main.vue';

Vue.use(CompositionApi);

function initAppContext(
  store: Store<State>,
  storeId: number,
  apiToken: string,
  cpUrl: string,
): void {
  const payload = {
    context: {
      ...store.state.context,
      storeId,
      apiToken,
      cpUrl,
    },
  };
  store.commit<InitContextMutation>({ type: MutationType.INIT_CONTEXT, payload });
}

function initApp(router: VueRouter, store: Store<State>): Vue {
  const app = new Vue({
    router,
    store,
    render: (h): VNode => h(App),
  });

  return app;
}

export default function createApp(storeId: number, apiToken: string, cpUrl: string): Vue {
  const store = createStore();
  const router = createRouter();
  initAppContext(store, storeId, apiToken, cpUrl);
  initApiContext({ siteId: storeId });

  return initApp(router, store);
}
