import { ActionContext, Payload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import { MutationType } from '@/app/editor/client/store/mutation/type';

export interface ResetTileDataAction extends Payload {
  type: ActionType.RESET_TILE_DATA;
}

export default function resetTileDataAction(
  actionContext: ActionContext <State, State>,
): void {
  actionContext.commit(MutationType.SET_TILE_DATA, {
    type: MutationType.SET_TILE_DATA,
    tileData: undefined,
  });
  actionContext.commit(MutationType.SET_TILE_CONFIG, {
    type: MutationType.SET_TILE_CONFIG,
    payload: { tileConfig: undefined },
  });
}
