import { Color } from '@/app/shared/client/type/color';
import { TextTileLayoutDefaults } from '@/app/shared/client/type/defaults/text-tile-defaults';
import { CoverTileLayoutDefaults } from '@/app/shared/client/type/defaults/cover-tile-defaults';
import { CTATileLayoutDefaults } from '@/app/shared/client/type/defaults/cta-tile-defaults';
import { FooterTileLayoutDefaults } from '@/app/shared/client/type/defaults/footer-tile-defaults';
import { HeaderTileLayoutDefaults } from '@/app/shared/client/type/defaults/header-tile-defaults';
import { ImageTextTileLayoutDefaults } from '@/app/shared/client/type/defaults/image-text-tile-defaults';
import { LocationTileLayoutDefaults } from '@/app/shared/client/type/defaults/location-tile-defaults';
import { StoreTileLayoutDefaults } from '@/app/shared/client/type/defaults/store-tile-defaults';

export enum ContentEditorType {
  TEXT_EDITOR = 'TEXT_EDITOR',
  MULTILINE_TEXT_EDITOR = 'MULTILINE_TEXT_EDITOR',
  ACTION_LINK_EDITOR = 'ACTION_LINK_EDITOR',
  IMAGE_EDITOR = 'IMAGE_EDITOR',
  CONTACT_INFO_EDITOR = 'CONTACT_INFO_EDITOR',
  CONTACT_INFO_WITH_SUBHEADING_EDITOR = 'CONTACT_INFO_WITH_SUBHEADING_EDITOR',
  LOGO_EDITOR = 'LOGO_EDITOR',
  HOURS_EDITOR = 'HOURS_EDITOR',
  SOCIAL_ICON_LIST_EDITOR = 'SOCIAL_ICON_LIST_EDITOR',
  LOCATION_EDITOR = 'LOCATION_EDITOR',
  MENU_EDITOR = 'MENU_EDITOR',
}

export interface ContentEditor {
  readonly type?: ContentEditorType;
  readonly field?: string;
  readonly label?: string;
  readonly placeholderLabel?: string;
  readonly maxListItems?: number;
}

export enum DesignEditorType {
  GENERAL_EDITOR = 'GENERAL_EDITOR',
  TEXT_EDITOR = 'TEXT_EDITOR',
  ACTION_LINK_EDITOR = 'ACTION_LINK_EDITOR',
  IMAGE_EDITOR = 'IMAGE_EDITOR',
  CUSTOM_SIDEBAR_EDITOR = 'CUSTOM_SIDEBAR_EDITOR',
  SCROLLABLE_IMAGE_EDITOR = 'SCROLLABLE_IMAGE_EDITOR',
  ARROW_EDITOR = 'ARROW_EDITOR',
  ICON_EDITOR = 'ICON_EDITOR',
  LOGO_EDITOR = 'LOGO_EDITOR',
  VISIBILITY_EDITOR = 'VISIBILITY_EDITOR',
  SOCIAL_ICON_LIST_EDITOR = 'SOCIAL_ICON_LIST_EDITOR'
}

export interface DesignEditor {
  readonly type?: DesignEditorType;
  readonly field?: string;
  readonly contentSourceField?: string;
  readonly label?: string;
  readonly textSizes?: number[];
  readonly colors?: string[];
  readonly autoColorEnabled?: boolean;
  readonly alphaColorEnabled?: boolean;
  readonly defaultFont?: string;
  readonly defaultTextSize?: number;
  readonly defaultColor?: Color;
  readonly hideVisibleToggle?: boolean;
}

export enum EditorButtonAdviceType {
  STORE_CLOSED = 'STORE_CLOSED',
  NO_CATEGORIES = 'NO_CATEGORIES',
  NO_PRODUCTS = 'NO_PRODUCTS',
}

export interface EditorButtonAdvice {
  readonly type?: EditorButtonAdviceType;
  readonly label?: string;
}

export enum EditorButtonType {
  CONTROL_PANEL_LINK_BUTTON= 'CONTROL_PANEL_LINK_BUTTON',
}

export interface EditorButton {
  readonly type?: EditorButtonType;
  readonly label?: string;
  readonly descriptionLabel?: string;
  readonly actionLabel?: string;
  readonly url?: string;
  readonly advices?: EditorButtonAdvice[];
}

interface ContentEditorConfig {
  readonly mainEditors?: ContentEditor[];
  readonly buttons?: EditorButton[];
}

interface DesignEditorConfig {
  readonly mainEditors?: DesignEditor[];
  readonly customEditors?: DesignEditor[];
  readonly buttons?: EditorButton[];
}

export type LayoutDefaults = CoverTileLayoutDefaults
  | TextTileLayoutDefaults
  | CTATileLayoutDefaults
  | FooterTileLayoutDefaults
  | HeaderTileLayoutDefaults
  | ImageTextTileLayoutDefaults
  | LocationTileLayoutDefaults
  | StoreTileLayoutDefaults;

export interface LayoutConfig {
  readonly name?: string;
  readonly contentEditorConfig?: ContentEditorConfig;
  readonly designEditorConfig?: DesignEditorConfig;
  readonly defaults?: LayoutDefaults;
  readonly deprecated?: boolean;
}

export interface TileConfig {
  readonly layoutConfigList: LayoutConfig[];
}
