


































































import {
  computed, defineComponent, ref,
} from '@vue/composition-api';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import {
  DesignEditor, DesignEditorType, LayoutConfig,
} from '@/app/editor/client/type/tile-config';
import Icon from '@/app/editor/client/view/icon';
import {
  designEditorName,
  useEditorButtonCreator,
} from '@/app/editor/client/logic/tile-editor';
import { GlobalData, GlobalDesign } from '@/app/shared/client/type/tile/global';
import { extractGlobalColorHex } from '@/app/editor/client/logic/site-settings';
import useI18N from '@/app/shared/client/logic/i18n';
import { Accordion, AccordionItem } from '@/app/editor/client/view/component';
import { isMadeWithSectionVisible } from '@/app/shared/client/logic/footer-tile';
import { FooterTileExternalContent }
  from '@/app/shared/client/type/tile/footer-tile';
import { LogoData, LogoType } from '@/app/shared/client/type/logo';
import { useSaveTileSettingsDebounced }
  from '@/app/shared/client/helper/save-tile-settings-debounce';
import { TileType } from '@/app/shared/client/type/tile-type';
import { TileData } from '@/app/shared/client/type/tile';
import { StoreTileExternalContent } from '@/app/shared/client/type/tile/store-tile';
import { VisibilityDefaults } from '@/app/shared/client/type/settings/visibility-settings';
import { HeaderTileExternalContent } from '@/app/shared/client/type/tile/header-tile';
import LayoutDesignEditor from './editor/LayoutDesignEditor.vue';

interface DesignEditorData {
  type?: DesignEditorType;
  name?: string;
  field?: string;
  contentSourceField?: string,
  label?: string;
  textSizes?: number[];
  colors?: string[];
  autoColorEnabled?: boolean;
  alphaColorEnabled?: boolean;
  action?: (value: string) => void;
  hideVisibleToggle?: boolean;
}

function useChangeLayout(store: Store<State>): (value: string) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);
  function changeLayout(value: string): void {
    store.commit(MutationType.UPDATE_EDITED_TILE_FIELD, {
      section: 'design',
      field: 'layout',
      value,
    });
    debouncedDispatch();
  }
  return changeLayout;
}

interface DesignEditorManager {
  isActiveDesignEditor: (designEditor: string) => boolean;
  isElementVisibleOnWebsite: (
    designEditor: DesignEditorData,
    content: Record<string, unknown>,
    design: VisibilityDefaults,
    defaults: VisibilityDefaults,
  ) => boolean;
  onChangeActiveDesignEditor: (designEditor: string) => void;
}
function useDesignEditorManager(): DesignEditorManager {
  const activeDesignEditor = ref<string | undefined>(undefined);

  function isActiveDesignEditor(designEditor: string): boolean {
    return activeDesignEditor.value === designEditor;
  }

  function isElementVisibleOnWebsite(
    designEditor: DesignEditorData,
    content: Record<string, unknown>,
    design: VisibilityDefaults,
    defaults: VisibilityDefaults,
  ): boolean {
    let hasContent = true;
    if (designEditor.contentSourceField) {
      const fieldContent = designEditor.contentSourceField.split('.')
        .reduce((
          arr: Record<string, unknown>,
          val: string,
        ) => arr as Record<string, unknown> && arr[val] as Record<string, unknown>, content);

      hasContent = fieldContent !== undefined && fieldContent.toString() !== '';
    }

    const isVisible = design?.visible ?? defaults?.visible ?? true;
    switch (designEditor.type) {
      case DesignEditorType.GENERAL_EDITOR:
      case DesignEditorType.CUSTOM_SIDEBAR_EDITOR:
        return true;
      case DesignEditorType.TEXT_EDITOR:
      case DesignEditorType.ACTION_LINK_EDITOR:
      case DesignEditorType.IMAGE_EDITOR:
      case DesignEditorType.SCROLLABLE_IMAGE_EDITOR:
      case DesignEditorType.ARROW_EDITOR:
      case DesignEditorType.ICON_EDITOR:
      case DesignEditorType.VISIBILITY_EDITOR:
      case DesignEditorType.SOCIAL_ICON_LIST_EDITOR:
        return hasContent && isVisible;
      case DesignEditorType.LOGO_EDITOR: {
        const logoData = content?.logo as LogoData;
        if (logoData.type === LogoType.IMAGE) {
          return logoData.image !== undefined && isVisible;
        }
        return hasContent && isVisible;
      }
      default:
        throw new Error('Unknown design editor type');
    }
  }

  function onChangeActiveDesignEditor(designEditor: string): void {
    if (!isActiveDesignEditor(designEditor)) {
      activeDesignEditor.value = designEditor;
    } else {
      activeDesignEditor.value = undefined;
    }
  }

  return {
    isActiveDesignEditor,
    isElementVisibleOnWebsite,
    onChangeActiveDesignEditor,
  };
}

function useUpdateValue(store: Store<State>, field: string | undefined): (value: string) => void {
  const debouncedDispatch = useSaveTileSettingsDebounced(store);
  function updateValue(value: string): void {
    store.commit(MutationType.UPDATE_EDITED_TILE_FIELD, { section: 'design', field, value });
    debouncedDispatch();
  }
  return updateValue;
}

function prepareColors(
  colors?: string[],
  siteDesignSettings?: GlobalDesign,
): string[] | undefined {
  return colors?.map((color) => extractGlobalColorHex(color, siteDesignSettings));
}

function useDesignEditorCreator(
  $store: Store<State>,
  siteDesignSettings?: GlobalDesign,
): (editor: DesignEditor) => DesignEditorData {
  function createDesignEditor(editor: DesignEditor): DesignEditorData {
    const {
      type,
      field,
      contentSourceField,
      label,
      textSizes,
      colors,
      autoColorEnabled,
      alphaColorEnabled,
      hideVisibleToggle,
    } = editor;
    const action = useUpdateValue($store, field);
    return {
      type,
      name: designEditorName(type),
      field,
      contentSourceField,
      label,
      textSizes,
      colors: prepareColors(colors, siteDesignSettings),
      autoColorEnabled,
      alphaColorEnabled,
      action,
      hideVisibleToggle,
    };
  }

  return createDesignEditor;
}

function getTileEditorFilterPredicate(
  tileType: TileType | undefined,
): (
  field: string | undefined,
  externalContent:
    Record<string, unknown>
    | StoreTileExternalContent
    | HeaderTileExternalContent
    | FooterTileExternalContent
    | undefined,
) => boolean {
  switch (tileType) {
    case 'TEXT':
    case 'CALL_TO_ACTION':
    case 'COVER':
    case 'STORE':
    case 'IMAGE_TEXT':
    case 'HEADER':
    case 'LOCATION':
    case 'FEATURED_PRODUCTS':
    case 'ROOT_CATEGORIES':
      return (): boolean => true;
    case 'FOOTER':
      return (
        field: string | undefined,
        externalContent:
          Record<string, unknown>
          | StoreTileExternalContent
          | HeaderTileExternalContent
          | FooterTileExternalContent
          | undefined,
      ): boolean => {
        const footerExternalContent = externalContent as FooterTileExternalContent;
        const channelSettings = footerExternalContent.channelSettings;
        return field !== 'madeWithNotice' || isMadeWithSectionVisible(channelSettings);
      };
    default:
      throw new Error(`Unexpected tile type '${tileType}'`);
  }
}

const TileDesignEditor = defineComponent({
  components: {
    LayoutDesignEditor,
    ArrowRightIcon: Icon.ArrowRightIcon,
    Accordion,
    AccordionItem,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const state = computed(() => root.$store.state as State);
    const editedTile = computed(() => state.value.editedTileData as TileData);

    const design = computed(() => editedTile.value.design);
    const siteSettings = computed(() => state.value.siteSettings as GlobalData);
    const siteDesignSettings = computed(() => siteSettings.value.design);

    const content = computed(() => editedTile.value.content);
    const externalContent = computed(() => editedTile.value.externalContent);

    const layoutConfigList = computed(() => {
      const { editedTileConfig } = root.$store.state;
      return editedTileConfig.layoutConfigList as LayoutConfig[];
    });
    const layoutValue = computed(() => design.value?.layout);
    const canChangeLayout = computed(() => layoutConfigList.value.length > 1);
    const changeLayout = useChangeLayout(root.$store);

    const currentLayoutConfig = computed(() => {
      const configList = layoutConfigList.value;
      return configList
        .find((config: LayoutConfig) => {
          const currentLayout = design?.value?.layout;
          return config.name === currentLayout;
        });
    });

    const defaults = computed(() => currentLayoutConfig.value?.defaults);
    const designMainEditorList = computed(() => {
      const createDesignEditor = useDesignEditorCreator(
        root.$store,
        siteDesignSettings.value,
      );
      return currentLayoutConfig.value
        ?.designEditorConfig
        ?.mainEditors
        ?.filter((editor: DesignEditor): boolean => {
          const isEditorShouldBeShown = getTileEditorFilterPredicate(editedTile.value.type);
          return isEditorShouldBeShown(editor.field, externalContent.value);
        })
        ?.map(createDesignEditor);
    });

    const designCustomEditorList = computed(() => {
      const createDesignEditor = useDesignEditorCreator(root.$store, siteDesignSettings.value);
      return currentLayoutConfig.value
        ?.designEditorConfig
        ?.customEditors
        ?.map(createDesignEditor);
    });

    const editorButtons = computed(() => {
      const createEditorButton = useEditorButtonCreator();
      return currentLayoutConfig.value
        ?.designEditorConfig
        ?.buttons
        ?.map(createEditorButton);
    });

    const {
      isActiveDesignEditor,
      isElementVisibleOnWebsite,
      onChangeActiveDesignEditor,
    } = useDesignEditorManager();

    return {
      i18n,
      design,
      content,
      defaults,
      siteDesignSettings,
      layoutConfigList,
      layoutValue,
      canChangeLayout,
      changeLayout,
      designMainEditorList,
      designCustomEditorList,
      editorButtons,
      isActiveDesignEditor,
      isElementVisibleOnWebsite,
      onChangeActiveDesignEditor,
    };
  },
});

export default TileDesignEditor;
