import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { EditorRuntimeContext } from '@/app/editor/client/type/editor-runtime-context';
import { MutationType } from '@/app/editor/client/store/mutation/type';

interface InitContextMutationPayload {
  context: EditorRuntimeContext;
}

export interface InitContextMutation extends MutationPayload {
  type: MutationType.INIT_CONTEXT;
  payload: InitContextMutationPayload;
}

export default function initContextMutation(oldState: State, data: InitContextMutation): State {
  const newState = oldState;
  newState.context = data.payload.context;
  return newState;
}
