import { TileData } from '@/app/shared/client/type/tile';
import {
  PreviewApiEventType,
  UpdatePreviewTileListPayload,
} from '@/app/shared/client/type/preview-api';
import { sendToPreviewWindow } from '@/app/editor/client/logic/preview/preview';

export default function sendPreviewTileList(tileList: TileData[]): void {
  sendToPreviewWindow<UpdatePreviewTileListPayload>({
    type: PreviewApiEventType.UPDATE_PREVIEW_TILE_LIST,
    payload: { tileList },
  });
}
