import { Ref } from '@vue/composition-api';

export enum UploadError {
  INTERNAL,
  FORMAT_NOT_SUPPORTED,
  SIZE_EXCEED,
  DIMENSION_EXCEEDED,
}

export interface UploadErrorLabels {
  [UploadError.INTERNAL]: string,
  [UploadError.FORMAT_NOT_SUPPORTED]: string,
  [UploadError.SIZE_EXCEED]: string,
  [UploadError.DIMENSION_EXCEEDED]?: string,
}

export const COMMON_UPLOAD_ERROR_LABELS: UploadErrorLabels = {
  [UploadError.INTERNAL]: 'CommonLabels.StandardImage.UploadFailure.internal',
  [UploadError.FORMAT_NOT_SUPPORTED]: 'CommonLabels.StandardImage.UploadFailure.format_not_supported',
  [UploadError.SIZE_EXCEED]: 'CommonLabels.StandardImage.UploadFailure.format_not_supported',
};

export interface ImageUploadingData {
  readonly imageFile: File;
  readonly rawImage: string;
  readonly uploadError: Ref<UploadError | undefined>;
}

export interface ImageUploadInfo {
  id?: string;
  url?: string;
}
