import Vue from 'vue';
import Vuex, { Store, StoreOptions } from 'vuex';
import { Bucket } from '@/app/shared/client/type/bucket';
import { State } from './state';
import createActions from './actions';
import createMutations from './mutations';
import createGetters from './getters';

Vue.use(Vuex);

const initialState: State = {
  context: {
    storeId: 0,
    apiToken: '',
    cpUrl: '',
    bucketUrls: {
      [Bucket.US_VIR]: undefined,
      [Bucket.EU_FRA]: undefined,
      [Bucket.AU_SYD]: undefined,
    },
  },
  profile: undefined,
  translations: {},
  websiteTranslations: {},
  tiles: [],
  publishedTiles: [],
  siteSettings: undefined,
  publishedSiteSettings: undefined,
  configs: [],
  editedTileData: undefined,
  editedTileConfig: undefined,
  currentPopup: undefined,
  tileShowcaseCategories: [],
  imageListInUploadingState: [],
  overlaySource: undefined,
  isShowMobilePreview: false,
  syncTilesInProgress: false,
  hasCustomCssChanges: false,
};

function createStoreOptions(state: State): StoreOptions<State> {
  return {
    state,
    mutations: createMutations(),
    actions: createActions(),
    getters: createGetters(),
  };
}

export default function createStore(): Store<State> {
  const store = new Store(createStoreOptions(initialState));
  return store;
}
