import { Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';

export interface RemoveTileRequest {
  readonly id: string;
}
export type RemoveTileResponse = TileData;

export function createRemoveTileFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: RemoveTileRequest) => Promise<RemoveTileResponse> {
  return (request: RemoveTileRequest): Promise<RemoveTileResponse> => fetch(`${apiUrl}/${siteId.value}/tile/${request.id}`, {
    method: 'DELETE',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: RemoveTileResponse) => resp);
}
