import { ActionContext, ActionPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { api } from '@/app/editor/client/api';
import { ActionType } from '@/app/editor/client/store/action/type';
import {
  GetImageUploadInfoResponse,
} from '@/app/editor/client/api/get-image-upload-info';
import { ConvertOperation } from '@/app/editor/client/type/image-convert-operation';
import { ImageUploadInfo } from '@/app/editor/client/type/image-uploader';

interface GetImageUploadInfoActionPayload {
  tileId: string;
  acceptHeader: string;
  convertOperations?: ConvertOperation[];
}

export interface GetImageUploadInfoAction extends ActionPayload {
  type: ActionType.GET_IMAGE_UPLOAD_INFO;
  payload: GetImageUploadInfoActionPayload;
}

export default function getImageUploadInfoAction(
  _: ActionContext <State, State>,
  data: GetImageUploadInfoAction,
): Promise<ImageUploadInfo | undefined> {
  return new Promise((resolve, reject) => {
    const { tileId, acceptHeader, convertOperations } = data.payload;
    if (tileId === undefined || acceptHeader === undefined) {
      reject();
      return;
    }

    api.getImageUploadInfo({
      tileId,
      acceptHeader,
      convertOperations,
    }).then((response: GetImageUploadInfoResponse) => {
      resolve(response);
    }).catch((error: Error) => reject(error.message));
  });
}
