import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import Vue from 'vue';
import { TileDesign } from '@/app/shared/client/type/tile';

export interface DiscardTileDesignMutation extends MutationPayload {
  type: MutationType.DISCARD_TILE_DESIGN;
}

export const ignoredPropertyNameForDiscard = 'layout';

export default function discardTileDesignMutation(
  oldState: State,
): State {
  const newState = oldState;
  const { editedTileData, tiles } = oldState;

  if (editedTileData != null) {
    const oldDesign = editedTileData.design;
    if (oldDesign === undefined) {
      return oldState;
    }

    const newDesign = { [ignoredPropertyNameForDiscard]: oldDesign[ignoredPropertyNameForDiscard] };
    editedTileData.design = newDesign as TileDesign;
    editedTileData.hasChanges = true;
    newState.editedTileData = editedTileData;

    const tileIndexForUpdate = tiles.findIndex((tile) => tile.id === editedTileData.id);
    if (tileIndexForUpdate >= 0) {
      Vue.set(tiles, tileIndexForUpdate, editedTileData);
    }
  }
  return newState;
}
