import { Ref } from '@vue/composition-api';

export interface GetTranslationsRequest {
  readonly locale: string;
}

export interface GetTranslationsResponse {
  readonly editorTranslations: Record<string, string>;
  readonly websiteTranslations: Record<string, string>;
}

export function createGetTranslationsFunction(
  apiUrl: string,
  siteId: Ref<number>,
  token: Ref<string | undefined>,
): (request: GetTranslationsRequest) => Promise<GetTranslationsResponse> {
  return (request: GetTranslationsRequest): Promise<GetTranslationsResponse> => fetch(`${apiUrl}/${siteId.value}/translation?locale=${request.locale}`, {
    method: 'GET',
    headers: {
      Authorization: `Bearer ${token.value}`,
    },
  }).then((resp) => {
    if (!resp.ok) {
      throw new Error(resp.statusText);
    }
    return resp.json();
  }).then((resp: GetTranslationsResponse) => resp);
}
