import { MutationPayload } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { TileConfig } from '@/app/editor/client/type/tile-config';

interface SetTileConfigMutationPayload {
  tileConfig?: TileConfig;
}

export interface SetTileConfigMutation extends MutationPayload {
  type: MutationType.SET_TILE_CONFIG;
  payload: SetTileConfigMutationPayload;
}

export default function setTileConfigMutation(oldState: State, data: SetTileConfigMutation): State {
  const newState = oldState;
  newState.editedTileConfig = data.payload.tileConfig;
  return newState;
}
