
















































































































































































import { computed, defineComponent, ref } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { SetPopupMutation } from '@/app/editor/client/store/mutation/set-popup';
import { MutationType } from '@/app/editor/client/store/mutation/type';
import { PopupType } from '@/app/editor/client/type/popup';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { OverlaySource } from '@/app/editor/client/type/overlay';
import { toggleOverlay } from '@/app/editor/client/view/helpers/overlay';
import { ToggleMobilePreviewMutation }
  from '@/app/editor/client/store/mutation/toggle-mobile-preview';
import saveMobilePreviewPosition
  from '@/app/editor/client/logic/preview/save-mobile-preview-position';
import recoveryMobilePreviewPosition
  from '@/app/editor/client/logic/preview/recovery-mobile-preview-position';

function navigateToAddTileHandler(store: Store<State>): () => void {
  return (): void => {
    store.commit<SetPopupMutation>({
      type: MutationType.SET_POPUP,
      payload: {
        popup: PopupType.TILE_SHOWCASE,
      },
    });
  };
}

function toggleMobilePreviewHandler(store: Store<State>): () => void {
  return (): void => {
    if (store.state.isShowMobilePreview) {
      saveMobilePreviewPosition();
    }

    // ассинхронно скрываем превью, чтобы позиция скролла успела сохраниться
    // т.к. сохранение происходит через post message
    setTimeout(() => {
      store.commit<ToggleMobilePreviewMutation>({
        type: MutationType.TOGGLE_MOBILE_PREVIEW,
        payload: {
          isShowMobilePreview: !store.state.isShowMobilePreview,
        },
      });
      if (store.state.isShowMobilePreview) {
        recoveryMobilePreviewPosition();
      }
    });
  };
}

const Container = defineComponent({
  components: {
    DomainIcon: Icon.DomainIcon,
    PlusIcon: Icon.PlusIcon,
    PreviewOffIcon: Icon.PreviewOffIcon,
    PreviewOnIcon: Icon.PreviewOnIcon,
    ReorderIcon: Icon.ReorderIcon,
    SeoIcon: Icon.SeoIcon,
    SettingsIcon: Icon.SettingsIcon,
    StoreIcon: Icon.StoreIcon,
    ChartIcon: Icon.ChartIcon,
    StyleSettingsIcon: Icon.StyleSettingsIcon,
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);
    const opened = ref(false);

    const navigateToAddTile = navigateToAddTileHandler(root.$store);
    const toggleOpened = (): void => {
      opened.value = !opened.value;
      toggleOverlay(
        opened,
        OverlaySource.HEADER_SITE_SETTINGS_MENU,
        root.$store,
      );
    };

    const isShowMobilePreview = computed(() => root.$store.state.isShowMobilePreview);
    const toggleMobilePreview = toggleMobilePreviewHandler(root.$store);

    return {
      i18n,
      navigateToAddTile,
      opened,
      toggleOpened,
      isShowMobilePreview,
      toggleMobilePreview,
    };
  },
});

export default Container;
