import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';
import { ActionType } from '@/app/editor/client/store/action/type';
import {
  ImageUploadInfo,
  ImageUploadingData,
  UploadError, UploadErrorLabels,
} from '@/app/editor/client/type/image-uploader';
import { ImageData } from '@/app/shared/client/type/image';
import {
  GetImageUploadInfoAction,
} from '@/app/editor/client/store/action/get-image-upload-info';
import {
  RemoveImageInUploadingStateAction,
} from '@/app/editor/client/store/action/remove-image-in-uploading-state';
import {
  AddImageInUploadingStateAction,
} from '@/app/editor/client/store/action/add-image-in-uploading-state';
import { GetImageUploadResultAction } from '@/app/editor/client/store/action/get-image-upload-result';
import { ConvertOperation } from '@/app/editor/client/type/image-convert-operation';

function interruptImageUploading(
  store: Store<State>,
  tileId: string,
  imageId: string,
): void {
  store.dispatch<RemoveImageInUploadingStateAction>({
    type: ActionType.REMOVE_IMAGE_IN_UPLOADING_STATE,
    payload: {
      tileId,
      imageId,
    },
  });
}

function processUploadingResult(
  store: Store<State>,
  tileId: string,
  imageId: string,
  result: ImageData,
  updateImage: (newImageData: ImageData | undefined) => void,
): void {
  updateImage(result);
  interruptImageUploading(store, tileId, imageId);
}

function scheduleUploadingResultPolling(
  store: Store<State>,
  tileId: string,
  imageId: string,
  uploadInfo: ImageUploadInfo,
  updateImage: (newImageData: ImageData | undefined) => void,
): void {
  const uploadId = uploadInfo?.id;
  if (uploadId === undefined) {
    return;
  }

  const pollingTimer = setInterval(() => {
    store.dispatch<GetImageUploadResultAction>({
      type: ActionType.GET_IMAGE_UPLOAD_RESULT,
      payload: { imageId: uploadId },
    })
      .then((result: ImageData | undefined) => {
        if (result === undefined) {
          return;
        }

        processUploadingResult(store, tileId, imageId, result, updateImage);
        clearInterval(pollingTimer);
      })
      .catch(() => {
        // omits error
      });
  }, 3000);
}

function getUploadInfo(
  store: Store<State>,
  tileId: string,
  acceptHeader: string,
  convertOperations?: ConvertOperation[],
): Promise<ImageUploadInfo> {
  const payload = {
    tileId,
    acceptHeader,
    convertOperations,
  };
  return store.dispatch<GetImageUploadInfoAction>({
    type: ActionType.GET_IMAGE_UPLOAD_INFO,
    payload,
  });
}

function requestUpload(
  store: Store<State>,
  tileId: string,
  imageId: string,
  uploadInfo: ImageUploadInfo,
  formData: FormData,
  updateImage: (newImageData: ImageData | undefined) => void,
): Promise<void> {
  const uploadUrl = uploadInfo.url;
  if (uploadUrl === undefined) {
    throw new Error('uploadUrl is invalid');
  }

  return fetch(uploadUrl, {
    method: 'POST',
    mode: 'cors',
    cache: 'no-cache',
    body: formData,
  }).then((response) => {
    if (!response.ok) {
      throw new Error(`Error with status code: ${response.status}`);
    }

    scheduleUploadingResultPolling(store, tileId, imageId, uploadInfo, updateImage);
  });
}

export function uploadImage(
  store: Store<State>,
  tileId: string,
  imageId: string,
  imageUploadingData: ImageUploadingData,
  updateImage: (newImageData: ImageData | undefined) => void,
  convertOperations?: ConvertOperation[],
): void {
  const {
    imageFile, rawImage, uploadError,
  } = imageUploadingData;

  store.dispatch<AddImageInUploadingStateAction>({
    type: ActionType.ADD_IMAGE_IN_UPLOADING_STATE,
    payload: {
      tileId,
      imageId,
      rawImage,
    },
  });

  getUploadInfo(store, tileId, imageFile.type, convertOperations)
    .then((uploadInfo: ImageUploadInfo) => {
      const formData = new FormData();
      formData.append('image', imageFile);
      return requestUpload(store, tileId, imageId, uploadInfo, formData, updateImage);
    }).catch(() => {
      uploadError.value = UploadError.INTERNAL;
      interruptImageUploading(store, tileId, imageId);
    });
}

export function getUploadErrorLabel(
  error: UploadError,
  errorLabels: UploadErrorLabels,
): string | undefined {
  return errorLabels[error];
}

export default {};
